import React, { useState, useEffect } from 'react';
import quizQuestions from './quiz_questions.json';

function Quiz() {
  const [selectedTopics, setSelectedTopics] = useState([]);
  const [numQuestionsPerTopic, setNumQuestionsPerTopic] = useState(1);
  const [quizState, setQuizState] = useState('topicSelection');
  const [questions, setQuestions] = useState([]);
  const [finalScore, setFinalScore] = useState(null); // State for final score
  const [quizDuration, setQuizDuration] = useState('unlimited'); // State for quiz duration
  const [timeRemaining, setTimeRemaining] = useState(null); // State for time remaining
  const [savedQuizzes, setSavedQuizzes] = useState([]);

  useEffect(() => {
    // Retrieve saved quizzes from local storage on component mount
    const savedQuizzesData = localStorage.getItem('savedQuizzes');
    console.log("on component mount");
    if (savedQuizzesData) {
      console.log("loaded up saved quizzes");
      setSavedQuizzes(JSON.parse(savedQuizzesData));
    }
  }, []);

  useEffect(() => {
    // Save quizzes to local storage whenever the savedQuizzes state changes
    console.log("save quiz to local storage");
    localStorage.setItem('savedQuizzes', JSON.stringify(savedQuizzes));
  }, [savedQuizzes]);

  // Options for quiz duration dropdown
  const quizDurationOptions = [
    { label: '10 seconds', value: 10},
    { label: '30 seconds', value: 30},
    { label: '1 min', value: 60 },
    { label: '5 mins', value: 300 },
    { label: '10 mins', value: 600 },
    { label: '30 mins', value: 1800 },
    { label: '1 hr', value: 3600 },
    { label: 'Unlimited', value: 'unlimited' }
  ];

  // Timer function
  useEffect(() => {
    let timer;
    if (quizDuration !== 'unlimited' && timeRemaining !== null && timeRemaining > 0) {
      timer = setInterval(() => {
        setTimeRemaining(prevTime => prevTime - 1);
      }, 1000);
    } else if (timeRemaining === 0) {
      finishQuiz();
    }
    return () => clearInterval(timer);
  }, [quizDuration, timeRemaining]);

  useEffect(() => {
    // Save state to local storage
    localStorage.setItem('savedQuizzes', JSON.stringify(savedQuizzes));
  
    // Cleanup function
    return () => {
      // Any cleanup code here
    };
  }, [savedQuizzes]);

  const handleTopicChange = (topic) => {
    if (selectedTopics.includes(topic)) {
      setSelectedTopics(selectedTopics.filter(item => item !== topic));
    } else {
      setSelectedTopics([...selectedTopics, topic]);
    }
  };

  const handleNumQuestionsChange = (e) => {
    setNumQuestionsPerTopic(parseInt(e.target.value)); // Parse the value to integer and update state
  };

  const startQuiz = () => {
    if (quizDuration !== 'unlimited') {
      setTimeRemaining(quizDuration);
    }
    // Selecting questions for the quiz
    const selectedQuestions = [];
    selectedTopics.forEach(topic => {
      const topicQuestions = quizQuestions.find(q => q.topic === topic).questions;
      const randomQuestions = topicQuestions.sort(() => Math.random() - 0.5).slice(0, numQuestionsPerTopic);
      selectedQuestions.push(...randomQuestions);
    });
    
    const resetQuestions = selectedQuestions.map(question => ({
      ...question,
      userAnswer: -1
    }));
    setQuestions(resetQuestions);
    setQuizState('doingQuiz');
  };

  const finishQuiz = () => {
    // Calculate final score
    const totalQuestions = questions.length;
    //const correctAnswers = questions.filter(q => q.userAnswer === q.correctAnswerNumber - 1).length;
    let correctAnswers = 0;
    questions.forEach(question => {
      // Check if the user has answered the question
      console.log("user answer:" + question.userAnswer);
      if ((typeof question.userAnswer !== 'undefined') && question.userAnswer !== -1) {
        // Check if the user's answer is correct 
        //(userAnswer is the index while correctAnswerNumber is not, so they are off-by-one)
        if (question.userAnswer + 1 === question.correctAnswerNumber) { 
          correctAnswers++;
        }
      }
    });

    questions.forEach(question => {
      console.log("useranswer: " + question.userAnswer);
    });

    
    console.log("correct answers: " + correctAnswers);
    const score = "" + correctAnswers + "/" + totalQuestions;
    setFinalScore(score); // Set final score state

    // Display results
    setQuizState('quizResults');
    console.log("Final Score:", finalScore);

    
    // Save quiz data
    const quizData = {
      questions: questions.map(question => ({
        question: question.question,
        answers: question.answers,
        userAnswerIndex: (question.userAnswer !== undefined) ? question.userAnswer : -1, 
        correctAnswerIndex: question.correctAnswerNumber - 1 //convert one-based indexing to zero-based
      }))
    };
    console.log("update saved Quizzes array");
    setSavedQuizzes([...savedQuizzes, quizData]);
  };

  function renderTopicSelection() {
    return (
      <div style={{
        textAlign: 'justify',
        padding: '20px',
        margin: '10px' 
      }}>
      <h2>Select Topics for the Quiz</h2>
      {quizQuestions.map((topic, index) => (
        <div key={index}>
          <input
            type="checkbox"
            id={topic.topic}
            onChange={() => handleTopicChange(topic.topic)}
            checked={selectedTopics.includes(topic.topic)}
          />
          <label htmlFor={topic.topic}>{topic.topic}</label>
        </div>
      ))}
      <div>
        <label>Select number of questions per topic:</label>
        <select value={numQuestionsPerTopic} onChange={handleNumQuestionsChange}>
          <option value={1}>1</option>
          <option value={3}>3</option>
          <option value={5}>5</option>
        </select>
      </div>
      <div>
        <label>Select quiz duration:</label>
        <select value={quizDuration} onChange={(e) => setQuizDuration(e.target.value)}>
          {quizDurationOptions.map(option => (
            <option key={option.value} value={option.value}>{option.label}</option>
          ))}
        </select>
      </div>
      <button onClick={startQuiz} disabled={selectedTopics.length === 0}>Start Quiz</button>
      <button onClick={() => setQuizState('archive')}>View Archive</button>
    </div>
  );

}

  function renderArchivePage() {

    console.log("saved quizzes: " + JSON.stringify(savedQuizzes));
    /*
    savedQuizzes.forEach((quiz, quizIndex) => {
      console.log(`Quiz ${quizIndex + 1}:`);
      quiz.forEach((question, questionIndex) => {
        console.log(`Question ${questionIndex + 1}: ${question.question}`);
        console.log("answers: " +question.answers);
        console.log("correctAnswerIndex")
      });
    });
    */
    
    if (savedQuizzes.length === 0) {
      console.log("savedQuizzes is empty");
      return (
        <div style={{
          textAlign: 'justify',
          padding: '20px',
          margin: '10px' 
        }}>
          <h2>Archive Page</h2>
          <p>No quizzes done yet</p>
        </div>
      );
    }
    return (
      <div style={{
        textAlign: 'justify',
        padding: '20px',
        margin: '10px' 
      }}>
        <h2>Archive Page</h2>
        {savedQuizzes?.map((quiz, index) => (
          <div key={index}>
            <h3>Quiz {index + 1}</h3>
            {quiz.questions.map((question, qIndex) => (
              <div key={qIndex}>
                <h4>{question.question}</h4>
                <p><strong>Selected Answer:</strong> {(question.userAnswerIndex !== -1) ? question.answers[question.userAnswerIndex] : "[No Answer]"}</p>
                <p><strong>Correct Answer:</strong> {question.answers[question.correctAnswerIndex]}</p>
                <p>{(question.userAnswerIndex === question.correctAnswerIndex) ? "Correct" : "Incorrect"}</p>
              </div>
            ))}
          </div>
        ))}
      </div>
    ); 
   
  }

  // Function to format time
function formatTime(seconds) {
  const hours = Math.floor(seconds / 3600);
  const minutes = Math.floor((seconds % 3600) / 60);
  const remainingSeconds = seconds % 60;

  // Format time based on duration
  if (hours > 0) {
    return `${padZero(hours)}:${padZero(minutes)}:${padZero(remainingSeconds)}`;
  } else if (minutes > 0) {
    return `${padZero(minutes)}:${padZero(remainingSeconds)}`;
  } else {
    return `00:${padZero(remainingSeconds)}`;
  }
};

// Function to pad zero
function padZero(num) { 
  return (num < 10 ? '0' : '') + num;
};

  const renderQuiz = () => (
    <div style={{
      textAlign: 'justify',
      padding: '20px',
      margin: '10px' 
    }}>
      <h2>Quiz</h2>
      <div className="timer">{quizDuration !== 'unlimited' && timeRemaining !== null && formatTime(timeRemaining)}</div>
      {questions.map((question, index) => (
        <div key={index}>
          <p>{question.question}</p>
          <ul>
            {question.answers.map((answer, idx) => (
              <li key={idx}>
                <input
                  type="radio"
                  id={`answer_${idx}`}
                  name={`question_${index}`}
                  value={idx}
                  onChange={(e) => question.userAnswer = parseInt(e.target.value)}
                />
                <label htmlFor={`answer_${idx}`}>{answer}</label>
              </li>
            ))}
          </ul>
        </div>
      ))}
      <button onClick={finishQuiz}>Finish Quiz</button>
    </div>
  );

  const renderQuizResults = () => (
    <div style={{
      textAlign: 'center',
      padding: '20px',
      margin: '10px' 
    }}>
      <h2>Quiz Results</h2>
      <p>Final Score: {finalScore}</p>
    </div>
  );

  let content;
  switch (quizState) {
    case 'topicSelection':
      content = renderTopicSelection();
      break;
    case 'doingQuiz':
      content = renderQuiz();
      break;
    case 'quizResults':
      content = renderQuizResults();
      break;
    case 'archive':
      content = renderArchivePage();
      break;
    default:
      content = <p>Invalid state</p>;
  }

  return (
    <div>
      {content}
    </div>
  );
}

export default Quiz;