import React from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 2.3 Sequences and Summations 
var Latex = require("react-latex")
function SeqSum() {
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 2.3 Sequences and Summations </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5> An arithmetic sequence in a sequence in which the next term is always equal to the previous term plus some constant. </h5>
            <Latex displayMode={true}>{`$$
            a_n=a_{n-1}+d,\\quad a_0=c\\\\ 
            \\Longrightarrow a_n = a_0 + nd
            $$`}</Latex>
            <br/>
            <h5> A geometric sequence in a sequence in which the next term is always equal to the previous term multiplied by some constant. </h5>
            <Latex displayMode={true}>{`$$
            a_n=ra_{n-1},\\quad a_0=c\\\\ 
            \\Longrightarrow a_n = a_0r^n
            $$`}</Latex>
            <br/>
            <h5> A series is the sum of the terms of a sequence, either finite or infinite. For example, </h5>
            <Latex displayMode={true}>{`$$
            \\sum_{i=0}^{n}a_n=\\sum_{i=0}^{n}(a_0+id)=a_0(n+1)+\\frac{n(n+1)}{2}d\\\\ 
            \\text{ or }\\sum_{i=0}^{n}a_n=\\sum_{i=0}^{n}a_0r^i=a_0\\frac{r^{n+1}-1}{r-1}
            $$`}</Latex>
            <h5>
                <p>
                    Below is a list of useful formulas.
                </p>
                <BlockMath math="
                \begin{align*}
                    & \sum_{i=0}^{n}i=\frac{n(n+1)}{2} \\
                    & \sum_{i=0}^{n}i^2=\frac{n(n+1)(2n+2)}{6} \\
                    & \sum_{i=0}^{n}i^3=\frac{n^2(n+1)^2}{4} \\
                    & \sum_{i=0}^{n}ar^i = a\frac{r^{n+1}-1}{r-1} \\
                    & \sum_{i=0}^{\infty}r^i = \frac{1}{1-r},\quad |r|<1 \\
                    & \sum_{i=1}^{\infty}ir^{i-1} = \frac{1}{(1-r)^2},\quad |r|<1
                \end{align*}
                "/>
            </h5>
            </div>
            <div style={{ position: 'relative'}}>
                <Link to = "/topics/Relations/RelProp">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
                <Link to="/topics/SetsFunctions/series">
                <button style={{margin: "30px", fontSize: "16px"}} >
                    Series Interactive
                </button>
                </Link>
            </div>
        </LeafTopic>
    );
}

export default SeqSum;
