import React from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 7.2 Permutations and Combinations
function PermuComb() {
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 7.2 Permutations and Combinations </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>
                <p>
                Many counting problems can be solved by finding the number of ways to arrange a specified number of distinct elements of a 
                set of a particular size, where the order of these elements matters. Many other counting problems can be solved by finding 
                the number of ways to select a particular number of elements from a set of a particular size, where the order of the 
                elements selected does not matter.
                </p>
            </h5>
            <h3>
                Permutations
            </h3>
            <h5>
                <p>
                A <InlineMath>{'\\textbf{permutation}'}</InlineMath> of a set of distinct objects is an ordered arrangement of these objects. We 
                also are interested in ordered arrangements of some of the elements of a set. An ordered arrangement of <InlineMath>r</InlineMath> elements 
                of a set is called an <InlineMath>{'{\\bf r-}\\textbf{permutation}'}</InlineMath>.
                </p>
                <p>
                    Theorem 1. If <InlineMath>n</InlineMath> is a positive integer and <InlineMath>r</InlineMath> is an integer 
                    with <InlineMath>1\leq r \leq n</InlineMath>, then there are 
                </p>
                <BlockMath math="P(n,r)=n(n-1)(n-2)\cdots(n-r+1)=\frac{n!}{(n-r)!}"/>
                <p>
                    <InlineMath>r</InlineMath>-permutations of a set with <InlineMath>n</InlineMath> distinct elements.
                </p>
                <p>
                    Since there are <InlineMath>n</InlineMath> elements, there are <InlineMath>n</InlineMath> ways to determine the first element. 
                    Now, since there are <InlineMath>n-1</InlineMath> elements remaining, there are <InlineMath>n-1</InlineMath> ways to determine 
                    the second element. Similarly, with <InlineMath>n-r+1</InlineMath> elements remaining when we get to the <InlineMath>r</InlineMath>-th position, 
                    there are <InlineMath>n-r+1</InlineMath> ways to determine the <InlineMath>r</InlineMath>-th element. To compute the total number 
                    of permutations, we multiply the numbers and obtain <InlineMath>n(n-1)(n-2)\cdots (n-r+1)</InlineMath>. Note that we multiply the terms 
                    because <InlineMath>{'\\textbf{for each}'}</InlineMath> fixed element in the first position, there are <InlineMath>n-1</InlineMath> ways to determine the second element.
                </p>
            </h5>
            <h3>
                Combinations
            </h3>
            <h5>
                <p>
                    An <InlineMath>{'{\\bf r-}\\textbf{combination}'}</InlineMath> of elements of a set is an unordered selection of <InlineMath>r</InlineMath> elements 
                    from the set. Thus, an <InlineMath>r</InlineMath>-combination is simply a subset of the set with <InlineMath>r</InlineMath> elements.
                </p>
                <p>
                    Theorem 2. The number of <InlineMath>r</InlineMath>-combinations of a set with <InlineMath>n</InlineMath> elements, 
                    where <InlineMath>n</InlineMath> is a nonnegative integer and <InlineMath>r</InlineMath> is an integer with <InlineMath>0\leq r \leq n</InlineMath>, equals
                </p>
                <BlockMath math="C(n,r)=\frac{n!}{r!(n-r)!}"/>
                <p>
                    Since order does not matter, we simply divide <InlineMath>P(n,r)</InlineMath> by <InlineMath>r!</InlineMath>. That is, 
                    for each way we fix the <InlineMath>r</InlineMath> elements, there are <InlineMath>P(r,r)=r!</InlineMath> ways to order them. This is 
                    why we need to divide <InlineMath>P(n,r)</InlineMath> by <InlineMath>r!</InlineMath>.
                </p>
            </h5>
            <h3>
                Examples
            </h3>
            <h5>
                <p>
                Example 1. How many ways are there to select a first-prize winner, a second-prize winner, and a third-prize winner from <InlineMath>100</InlineMath> different 
                people who have entered a contest?
                </p>
                <p>
                    <InlineMath>\textit Solution:</InlineMath> Since each of the three people wins a different prize, order matters. Therefore, the answer is 
                </p>
                <BlockMath math="P(100,3)=100\cdot 99 \cdot 98=970,200"/>
                <p>
                    Example 2. How many ways are there to select five players from a <InlineMath>10</InlineMath>-member tennis team 
                    to make a trip to a match at another school?
                </p>
                <p>
                    <InlineMath>\textit Solution: </InlineMath> The order we select the players do not matter. Hence, the answer is
                </p>
                <BlockMath math="C(10,5)=\frac{10!}{5!5!}=252"/>
            </h5>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/GraphTheory/graphs">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
        </LeafTopic>
    );
}

export default PermuComb;
