class Algorithm {
  constructor() {
    this.freq = 200;
    this.handle = this.run();
    this.interval = null;
  }

  reset(setPlaying, setFinished) {
    this.handle = this.run();
    clearInterval(this.interval);
    setFinished(false);
    setPlaying(false);
  }

  toggle(playing, setPlaying, setFinished) {
    if (playing) {
      setPlaying(false);
      clearInterval(this.interval);
    } else {
      setPlaying(true);
      this.interval = setInterval(() => this.step(setPlaying, setFinished), this.freq);
    }
  }

  step(setPlaying, setFinished) {
    const { done } = this.handle.next();
    if (done) {
      clearInterval(this.interval);
      setPlaying(false);
      setFinished(true);
    }
  }

  *run() {}
}

export default Algorithm;
