import React from 'react';

const Edge = ({ x1, y1, x2, y2, value, directed, bg_color, onClick }) => {
  const VERTEX_RADIUS = 25;

  const length = Math.sqrt(Math.pow (x2 - x1, 2) + Math.pow(y2 - y1, 2));
  const angle = Math.atan2(y2 - y1, x2 - x1) * (180 / Math.PI);

  return (
    <div style={{
      zIndex: 9,
    }}>
    <div
      className="edge" 
      style={{ 
        position: 'absolute', 
        left: x1 + VERTEX_RADIUS, 
        top: y1 + VERTEX_RADIUS, 
        width: length, 
        height: 2, 
        
        transformOrigin: 'left top',
        transform: `rotate(${angle}deg)`,
        
        backgroundColor: 'black',
        zIndex: 9,
      }}  
    >
      {directed && (
    <div
        className="arrow"
        style={{
          position: 'absolute',
          top: '-5px', 
          left: length - 5 + 'px',
          width: 0,
          height: 0,
          borderTop: '5px solid transparent',
          borderBottom: '5px solid transparent',
          borderLeft: '5px solid black',
          zIndex: 9,
        }}
      />
      )}

      <div 
        className="edge-value"
        style={{
          position: 'relative',
          left: '50%',
          
          transform: `translate(-50%, -50%) rotate(${-angle}deg)`, // Apply reverse rotation and centering
          // textAlign: 'center', // Center align the text
          width: 'fit-content',
          cursor: 'pointer',
          zIndex: 9,
        }}
        onClick={onClick}
      >
        <h1>{value}</h1>
      </div>
      
      
      </div>
      </div>

      
      
  );
};

export default Edge;
