import React from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 4.1 Modular Arithmetic 
function Mod() {
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 4.1 Modular Arithmetic </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>
                <p>
                    Before introducing the concept of modular arithmetic, we need to review division.
                </p>
                <p>
                    If <InlineMath>a,b\in\Z</InlineMath> with <InlineMath>a\neq 0</InlineMath>, we say that <InlineMath>a</InlineMath> divides <InlineMath>b</InlineMath> if 
                    there is an integer <InlineMath>c</InlineMath> such that <InlineMath>b=ac</InlineMath>. When <InlineMath>a</InlineMath> divides <InlineMath>b</InlineMath> we say 
                    that <InlineMath>a</InlineMath> is a factor or divisor of <InlineMath>b</InlineMath>, and that <InlineMath>b</InlineMath> is a multiple of <InlineMath>a</InlineMath>. The 
                    notation <InlineMath>a\mid b</InlineMath> denotes that <InlineMath>a</InlineMath> divides <InlineMath>b</InlineMath>. We write <InlineMath>a\nmid b</InlineMath> when <InlineMath>a</InlineMath> does 
                    not divide <InlineMath>b</InlineMath>.
                </p>
                <p>
                    Theorem 1. Let <InlineMath>a,b,c\in Z</InlineMath>, where <InlineMath>a\neq 0</InlineMath>. Then
                </p>
                <p>
                    1. If <InlineMath>a\mid b</InlineMath> and <InlineMath>a\mid c</InlineMath>, then <InlineMath>a\mid (b+c)</InlineMath>.
                </p>
                <p>
                    2. If <InlineMath>a\mid b</InlineMath>, then <InlineMath>a\mid bc</InlineMath> for all integers <InlineMath>c</InlineMath>.
                </p>
                <p>
                    3. If <InlineMath>a\mid b</InlineMath> and <InlineMath>b\mid c</InlineMath>, then <InlineMath>a\mid c</InlineMath>.
                </p>
                <p>
                    Theorem 2. The Division Algorithm. Let <InlineMath>a\in\Z</InlineMath> and <InlineMath>d\in\Z^+</InlineMath>. Then there are 
                    unique integers <InlineMath>q</InlineMath> and <InlineMath>r</InlineMath>, with <InlineMath>0\leq r \lt d</InlineMath>, such 
                    that <InlineMath>a=dq+r</InlineMath>.
                </p>
                <p>
                    Now we will introduce the modulo operation. The modulo operation returns the remainder after a division. For example,
                </p>
                <BlockMath math="7\bmod{4}=3"/>
                <p>
                    because <InlineMath>7=1\cdot 4+3</InlineMath>. Let <InlineMath>a,b\in\Z</InlineMath>. We say that <InlineMath>a</InlineMath> is 
                    congruent to <InlineMath>b</InlineMath> modulo <InlineMath>m</InlineMath> if <InlineMath>\exists k\in\Z</InlineMath> such that <InlineMath>a-b=km</InlineMath> and we use the notation
                </p>
                <BlockMath math="a\equiv b\pmod{m}"/>
                <p>
                    Theorem 3. Let <InlineMath>a,b\in\Z</InlineMath>, and let <InlineMath>m\in\Z^+</InlineMath> be greater than <InlineMath>1</InlineMath>. Then
                </p>
                <BlockMath math="a\equiv b\pmod{m} \Longleftrightarrow a\bmod{m}=b\bmod{m}"/>
                <p>
                    Theorem 4. Let <InlineMath>m\in\Z^+</InlineMath>. The integers <InlineMath>a</InlineMath> and <InlineMath>b</InlineMath> are 
                    congruent modulo <InlineMath>m</InlineMath> if and only if there is an integer <InlineMath>k</InlineMath> such that <InlineMath>a=b+km</InlineMath>.
                </p>
                <p>
                    Theorem 5. Let <InlineMath>m\in\Z^+</InlineMath>. If <InlineMath>a\equiv b \pmod m</InlineMath> and <InlineMath>c\equiv d \pmod m</InlineMath>, then
                </p>
                <BlockMath math="a+c\equiv b+d \pmod{m}\quad \text{ and }\quad ac\equiv bd \pmod{m}"/>
                <p>
                    In addition, <InlineMath>(a+b)\bmod m = ((a \bmod m)+(b\bmod m))\bmod m </InlineMath> and <InlineMath>(ab)\bmod m = ((a \bmod m)(b\bmod m))\bmod m </InlineMath>.
                </p>
            </h5>
            <h3>
                Fast Modular Exponentiation
            </h3>
            <h5>
                <p>
                    It might be quite tedious to compute something such as 
                </p>
                <BlockMath math="436^{67}\bmod{7}"/>
                <p>
                    but the Fast Modular Exponentiation method can simplify the computations of such expressions.
                </p>
                <p>
                    Solution: 
                </p>
                <BlockMath math="
                \begin{equation*}
                    \begin{split}
                        & 67=1+2+64=1+1\cdot 2^1+1\cdot 2^6 \\
                        & \Longrightarrow 436^{67}\bmod{7} = 436^{1+1\cdot 2^1+1\cdot 2^6}\bmod{7} \\
                        & \Longrightarrow \textcircled{1}\  436\bmod{7} = 2 \\
                        & \hspace*{0.77cm} \textcircled{2} \ 436^{2^1}\bmod{7} = (436\bmod{7})^2\bmod{7} = 2^2\bmod 7 = 4 \\
                        & \hspace*{0.77cm} \textcircled{3} \ 436^{2^2}\bmod{7} = (436^2\bmod{7})^2\bmod{7} = 4^2\bmod{7} = 2 \\
                        & \hspace*{0.77cm} \textcircled{4} \ 436^{2^3}\bmod{7} = (436^{2^2}\bmod{7})^2\bmod{7} = 2^2\bmod{7} = 4 \\
                        & \hspace*{0.77cm} \textcircled{5} \ 436^{2^4}\bmod{7} = (436^{2^3}\bmod{7})^2\bmod{7} = 4^2\bmod{7} = 2 \\
                        & \hspace*{0.77cm} \textcircled{6} \ 436^{2^5}\bmod{7} = (436^{2^4}\bmod{7})^2\bmod{7} = 2^2\bmod{7} = 4 \\
                        & \hspace*{0.77cm} \textcircled{7} \ 436^{2^6}\bmod{7} = (436^{2^5}\bmod{7})^2\bmod{7} = 4^2\bmod{7} = 2 \\
                        & \Longrightarrow 436^{67}\bmod{7} = ((436\bmod{7})(436^{2^1}\bmod{7})(436^{2^6}\bmod{7}))\bmod{7} = 2
                    \end{split}
                \end{equation*}"/>
            </h5>
            <h5>
                <p>
                Notice that in this example, the remainder alternates between <InlineMath>2</InlineMath> and <InlineMath>4</InlineMath>. However, this 
                is not always the case. 
                </p>
            </h5>
            <h3>
                Fermat's Little Theorem
            </h3>
            <h5>
                <p>
                Let <InlineMath>a\in\Z</InlineMath> and <InlineMath>p</InlineMath> be prime. Then
                </p>
                <BlockMath math="a^{p}\equiv a \pmod{p}"/>
                <p>
                Corollary. Let <InlineMath>a\in\Z</InlineMath> and <InlineMath>p</InlineMath> be prime. If <InlineMath>p\nmid a</InlineMath>, or 
                equivalently, <InlineMath>\gcd(a,p)=1</InlineMath>, then 
                </p>
                <BlockMath math="a^{p-1}\equiv 1 \pmod{p}"/>
                <p>
                    For example, <InlineMath>13^6\equiv 1 \pmod 7</InlineMath>.
                </p>
            </h5>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/NumberTheory/primesgcd">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
        </LeafTopic>
    );
}

export default Mod;
