import { React, useEffect, useState} from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import InductMCQ1 from "./inductmcq/inductmcq1";
import InductMCQ2 from "./inductmcq/inductmcq2";
import InductMCQ3 from "./inductmcq/inductmcq3";
import InductMCQ4 from "./inductmcq/inductmcq4";
import InductMCQ5 from "./inductmcq/inductmcq5";
import axios from "axios";

const SelectInductMCQ = () => {
    const [selectedProblems, setSelectedProblems] = useState([]);
    const [selectedDifficulty, setSelectedDifficulty] = useState(false);
    const [problems, setProblems] = useState([]);

    const onReset = () => {
        window.location.reload();
    }

    const handleSubmit = () => {
        setSelectedDifficulty(true);
        let temp = [];
        if (selectedProblems.includes("Easy")) {
            temp.push(InductMCQ1);
            temp.push(InductMCQ3)
        }
        if (selectedProblems.includes("Medium")) {
            temp.push(InductMCQ2);
            temp.push(InductMCQ4)
        }
        if (selectedProblems.includes("Hard")) {
            temp.push(InductMCQ5);
        }
        setProblems(temp);
    }

    const handleCheckboxChange = (event) => {
        const { value, checked } = event.target;
        if (checked) {
            // If checked, add value to selectedProblems array
            setSelectedProblems([...selectedProblems, value]);
        } else {
            // If unchecked, remove value from selectedProblems array
            setSelectedProblems(selectedProblems.filter(problem => problem !== value));
        }
        console.log(selectedProblems);
    };

    return (
        <div style={{
            textAlign: 'justify',
            padding: '20px',
            margin: '10px' 
          }}>
            <h1>Induction MCQ Problems</h1>
            {!selectedDifficulty && <><h2>Select difficulty</h2>
            <input 
                type="checkbox" 
                id="vehicle1" 
                name="vehicle1" 
                value="Easy"
                onChange={handleCheckboxChange}
                checked={selectedProblems.includes('Easy')} 
            /> 
            <label htmlFor="vehicle1"> Easy</label><br/>
            <input 
                type="checkbox" 
                id="vehicle2" 
                name="vehicle2" 
                value="Medium"
                onChange={handleCheckboxChange}
                checked={selectedProblems.includes('Medium')} 
            />
            <label htmlFor="vehicle2"> Medium </label><br/>
            <input 
                type="checkbox" 
                id="vehicle3" 
                name="vehicle3" 
                value="Hard"
                onChange={handleCheckboxChange}
                checked={selectedProblems.includes('Hard')} 
            />
            <label htmlFor="vehicle3"> Hard</label><br/><br/>
            <input type="submit" onClick={handleSubmit} value="Submit"/></>}
            {selectedDifficulty && problems.map((Component, index) => (
                <div key={index}>
                    <Component />
                </div>
            ))}
            <br />
            <button style={{marginTop: "20px"}}onClick={onReset}>Reset Quiz</button>
        </div>
    )
};

export default SelectInductMCQ;