import { React, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { logout } from '../authentication/auth';
import axios from "axios";

import styles from "./styles/Navbar.module.css"

const Navbar = ({ logout, isAuthenticated } ) => {
    useEffect(() => {
        (async () => {
            try {
                const res = await axios.get("http://localhost:8000/api/profile-pic/", {
                    headers: { "Authorization": `JWT ${localStorage.getItem("access")}` },
                });
                const elem = document.getElementById("profile-pic");
                if (elem) { 
                  if (res.data.url) {
                    elem.src = `http://localhost:8000${res.data.url}`;
                  } else {
                    elem.src = "/user.png";
                  }
                }
            } catch (e) {
                console.error(e);
            }
        })();
    }, [isAuthenticated]);

    const notLoggedIn = () => (
        <>
            <li><LinkButton to="/login">Login</LinkButton></li>
            <li><LinkButton to="/signup">Register</LinkButton></li>
        </>
    );

    const loggedIn = () => (
        <>
            <li>
                <LinkButton to="/bio">
                    <img id="profile-pic" alt="User Profile" style={{height: "100%"}}></img>
                </LinkButton> 
            </li>
            <li><LinkButton to='/' onClick={logout}>Logout</LinkButton></li>
        </>
    );

    return (
        <div>
            <nav className={styles.navbar}>
                <Link to='/' style={{textDecoration: 'none', color: 'white', fontSize: '20px'}}>
                    BoilerAlgos
                </Link>
                <ul>
                    <li><LinkButton to="/">Home</LinkButton></li>
                    <li><LinkButton to="/topics">All Topics</LinkButton></li>
                    <li><LinkButton to="/feedback">Feedback</LinkButton></li>
                    <li><LinkButton to="/whiteboard">Whiteboard</LinkButton></li>
                    <li><LinkButton to="/quiz">Quiz</LinkButton></li>
                    {isAuthenticated ? loggedIn() : notLoggedIn()}
                </ul>
            </nav>   
        </div>
    );
}

function LinkButton({ to, children, onClick }) { //wrap link in a button
    return (
        <Link className={styles.linkButton} to={to} onClick={onClick}>
            <button className={styles.linkBtn}>{children}</button>
        </Link>
    );
}

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, { logout })(Navbar);
