import { useState, useRef, createElement } from "react";
import { BSTree, RBTree, BTree } from "./TreeInteractive.js";
import { BSTreeRender, RBTreeRender, BTreeRender } from "./TreeRenderer.js";

function TreeVisualizer() {
  const [mode, setMode] = useState("bst");
  const tree = useRef(new BSTree());
  const [disabled, setDisabled] = useState(false);
  const renderFlag = useState(false)[1];
  const rerender = () => renderFlag(v => !v);
  const insRef = useRef(null);
  const remRef = useRef(null);
  const fndRef = useRef(null);

  const modeMap = {
    "bst": {
      make: () => new BSTree(),
      render: BSTreeRender,
    },
    "rbt": {
      make: () => new RBTree(),
      render: RBTreeRender,
    },
    "bt3": {
      make: () => new BTree(3),
      render: BTreeRender,
    },
    "bt5": {
      make: () => new BTree(5),
      render: BTreeRender,
    },
  };

  return <div style={{
    textAlign: 'justify',
    padding: '20px',
    margin: '10px' 
    }}>
    <label htmlFor="tree-mode">Tree type:</label>
    <select name="tree-mode" id="tree-mode" onChange={(e) => {
      setMode(e.target.value);
      tree.current = modeMap[e.target.value].make();
    }}>
      <option value="bst">Binary search tree</option>
      <option value="rbt">Red Black Tree</option>
      <option value="bt3">B-Tree (order 3)</option>
      <option value="bt5">B-Tree (order 5)</option>
    </select>
    <div>
      <button disabled={disabled} onClick={async () => {
        setDisabled(true);
        const v = Number(insRef.current.value);
        for (const x of tree.current.insertSteps(v)) {
          // https://stackoverflow.com/questions/951021/what-is-the-javascript-version-of-sleep
          await new Promise(r => setTimeout(r, 500));
          rerender();
        }
        insRef.current.value = v + 1;
        rerender();
        setDisabled(false);
      }}>Visualize Insert</button>
      <button disabled={disabled} onClick={() => {
        const v = Number(insRef.current.value);
        tree.current.insert(v);
        insRef.current.value = v + 1;
        rerender();
      }}>Insert</button>
      <input type="number" ref={insRef} />
      <button disabled={disabled} onClick={() => {
        const v = Number(remRef.current.value);
        tree.current.remove(v);
        remRef.current.value = v + 1;
        rerender();
      }}>Remove</button> <input type="number" ref={remRef} />
      <button disabled={disabled} onClick={async () => {
        setDisabled(true);
        const v = Number(fndRef.current.value);
        for (const x of tree.current.findSteps(v)) {
          // https://stackoverflow.com/questions/951021/what-is-the-javascript-version-of-sleep
          await new Promise(r => setTimeout(r, 500));
          rerender();
        }
        rerender();
        setDisabled(false);
      }}>Find</button> <input type="number" ref={fndRef} />
    </div>
    {(modeMap[mode].render != null) && createElement(modeMap[mode].render, {tree: tree.current})}
  </div>;
};

export default TreeVisualizer;
