import { React, useState } from 'react';
import axios from "axios";

function Feedback() {
  const [message, setMessage] = useState("");
  const [clicked, setClicked] = useState(false);
  const [success, setSuccess] = useState(true);
  const maxLength = 400;

  const handleChange = e => {
    e.target.value = e.target.value.substring(0, maxLength);
    setMessage(e.target.value);
  };

  const sendFeedback = async () => {
    try {
      const formData = new FormData();
      formData.append('feedback', message);
      await axios.post("http://localhost:8000/api/send-feedback/", formData, {
        headers: { "Authorization": `JWT ${localStorage.getItem("access")}` },
      });
      setSuccess(true);
    } catch (e) {
      setSuccess(false);
      console.error(e);
    }
    setClicked(true);
  };

  return (
    <div className="container mt-5">
      <h1>Got any suggestions?</h1>
      <div>
        <div className="form-group">
          <textarea maxLength={maxLength} onChange={handleChange} className="form-control" rows="4" />
          <span style={{
            color: message.length === maxLength ? 'red' : null,
          }}>{message.length}/{maxLength} characters</span>
        </div>
        {(clicked && success) ? 
          <div className="alert alert-success mt-3" role='alert'>
              Feedback submitted successfully
          </div> : <></>}
        <button className="btn btn-primary mt-4" onClick={sendFeedback}>Submit feedback</button>
      </div>
    </div>
  );
}

export default Feedback;
