import Algorithm from '../Algorithm.js';

class VectorAlg extends Algorithm {
  constructor(data, setData) {
    super();
    this.vec = data;
    this.orig = [...data];
    this.setData = setData;
    this.cmps = 0;
    this.swps = 0;
  }

  reset(setPlaying, setFinished) {
    this.cmps = 0;
    this.swps = 0;
    this.setData([...this.orig]);
    this.vec = [...this.orig];
    super.reset(setPlaying, setFinished);
  }

  cmp(i, j) {
    this.cmps++;
    this.setData(o => {
      const copy = o.map(e => ({val: e.val}));
      copy[i].read = true;
      copy[j].read = true;
      this.vec = copy;
      return copy;
    });
    return (this.vec[i].val > this.vec[j].val) - (this.vec[i].val < this.vec[j].val);
  }

  swp(i, j) {
    this.swps++;
    this.setData(o => {
      const copy = o.map(e => ({val: e.val}));
      let tmp = copy[i].val;
      copy[i].val = copy[j].val;
      copy[j].val = tmp;
      copy[i].swap = true;
      copy[j].swap = true;
      this.vec = copy;
      return copy;
    });
  }
}

export default VectorAlg;
