import { useState, useRef, useEffect } from "react";

const modeMap = {
  "bubble": {
    name: "Bubble Sort",
    func: bubbleSort,
  },
  "insertion": {
    name: "Insertion Sort",
    func: insertionSort,
  },
  "selection": {
    name: "Selection Sort",
    func: selectionSort,
  },
  "quick": {
    name: "Quick Sort",
    func: quickSort,
  },
};
const nums = [...Array(100).keys()];

function SortingPuzzle() {
  // generate random puzzle
  const [ array, setArray ] = useState([]);
  // states [ "shuffle" -> "wait" -> "feedback" ]
  const [ state, setState ] = useState("shuffle");
  const [ mode, setMode ] = useState("bubble");
  // type: danger|success
  const [ message, setMessage ] = useState(null);
  const responseRef = useRef(null);
  const getNext = useRef(null);
  const next = useRef(null);

  useEffect(() => {
    setMessage(null);
    setState("shuffle");
  }, [mode]);

  // state transitions:
  // 1. get shuffle
  // 2. wait for user response (what is the next swap that will occur?)
  // 3. provide feedback
  // 3a. If user was correct advance state of array
  // 3a1. If array is sorted, go to (4:done)
  // 3a2. Else go to (2:wait)
  // 3b. If user was incorrect, show error message, go to (2:wait)
  // 4. wait for user to click "Next puzzle"
  useEffect(() => {
    //console.log(state, mode);
    if (state === "shuffle") {
      let a = shuffle(nums);
      while (isSorted(a)) {
        a = shuffle(nums);
      }
      setArray(a);
      responseRef.current.value = a.join(" ");
      getNext.current = modeMap[mode]?.func(a);
      console.log(getNext.current);
      const v = getNext.current.next();
      next.current = v.value;
      console.log(v);
      setState("wait");
    } else if (state === "feedback") {
      console.log(responseRef.current.value);
      const guess = responseRef.current.value.split(" ").map(Number);
      if (guess.some(isNaN)) {
        setMessage({ type: "danger", text: "invalid input"});
        setState("wait");
        return;
      }
      if (guess.length !== next.current.length || guess.some((e, i) => guess[i] !== next.current[i])) {
        console.log(guess, next.current);
        setMessage({ type: "danger", text: "wrong answer" });
        setState("wait");
        return;
      }
      setArray([...next.current]);
      const v = getNext.current.next()
      console.log(v);
      if (v.value === undefined) {
        setMessage({ type: "success", text: "nice! you successfully sorted the array!" });
        setState("done");
      } else {
        next.current = v.value;
        setMessage({ type: "success", text: "keep going :)" });
        setState("wait");
      }
    }
  }, [state, mode]);

  const submit = () => {
    setMessage(null);
    setState("feedback");
  };

  return <div style={{
    textAlign: 'justify',
    padding: '20px',
    margin: '10px' 
    }}>
    Sorting Puzzle
    <div>
      <label htmlFor="puzzle-mode">Mode:</label> <select name="puzzle-mode" id="puzzle-mode" onChange={(e) => {
        setMode(e.target.value);
      }}>
        <option value="bubble">Bubble Sort</option>
        <option value="insertion">Insertion Sort</option>
        <option value="selection">Selection Sort</option>
        <option value="quick" style={{paddingTop: '10px', fontSize: '14px'}}>Quick Sort</option>
      </select>
      <div>
        {array.join(",")}
      </div>
      <div>
        What will the array look like after the next swap?
      </div>
      <input className="w-100" disabled={state === "feedback"} ref={responseRef} onKeyPress={(e) => {
        if (e.key === "Enter") submit();
      }} />
      {(message != null) && <div className={`alert alert-${message.type} w-25`}>{message.text}</div>}
      {(state === "done") &&
        <div>
          <button className="btn btn-primary" onClick={() => {
            setState("shuffle");
            setMessage(null);
          }}>Next</button>
        </div>}
    </div>
  </div>;
}

function isSorted(a) {
  for (let i = 1; i < a.length; i++) {
    if (a[i] < a[i-1]) return false;
  }
  return true;
}

function shuffle(a) {
  let n = [...a];
  for (let i = 0; i < 10; i++) {
    const ind = Math.floor(Math.random() * (a.length - i)) + i;
    const tmp = n[ind];
    n[ind] = n[i];
    n[i] = tmp;
  }
  n = n.slice(0, 10);
  return n;
}

function *bubbleSort(a) {
  let n = [...a];
  while (true) {
    let swapped = false;
    for (let i = 0; i < n.length-1; i++) {
      if (n[i] > n[i+1]) {
        const tmp = n[i+1];
        n[i+1] = n[i];
        n[i] = tmp;
        swapped = true;
        yield n;
      }
    }
    if (!swapped) break;
  }
}

function *insertionSort(a) {
  let n = [...a];
  for (let i = 1; i < n.length; i++) {
    for (let j = i-1; j >= 0; j--) {
      if (n[j] > n[j+1]) {
        const tmp = n[j+1];
        n[j+1] = n[j];
        n[j] = tmp;
        yield n;
      }
    }
  }
}

function *selectionSort(a) {
  let n = [...a];
  for (let i = 0; i < n.length; i++) {
    let min = n[i];
    let mini = i;
    for (let j = i+1; j < n.length; j++) {
      if (n[j] < min) {
        min = n[j];
        mini = j;
      }
    }
    if (i === mini) continue;
    const tmp = n[mini];
    n[mini] = n[i];
    n[i] = tmp;
    yield n;
  }
}

function *partition(a, s, e) {
  if (s >= e) return;
  let i = s-1;
  for (let j = s; j < e; j++) {
    if (a[j] < a[e]) {
      i++;
      if (i !== j) {
        const tmp = a[i];
        a[i] = a[j];
        a[j] = tmp;
        yield a;
      }
    }
  }
  i++;
  if (i !== e) {
    const tmp = a[i];
    a[i] = a[e];
    a[e] = tmp;
    yield a;
  }
  yield* partition(a, s, i-1);
  yield* partition(a, i+1, e);
}

function *quickSort(a) {
  let n = [...a];
  yield* partition(n, 0, n.length-1);
}

export default SortingPuzzle;
