import React from "react";
import { Link } from 'react-router-dom';
import axios from "axios";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

import BFSPuzzle from "./bfsPuzzle";
import LeafTopic from "../LeafTopic";

//Lesson 6.2  BFS
function BFS() {
    const handleCreateLesson = async () => {
    try {
      const currentDate = new Date().toISOString().split('T')[0];
      //const csrfToken = getCookie('csrftoken');
      const body = {
        lessonName: 'bfs',
        topicID: 1,
        dateComplete: currentDate
      }
      const config = {
        headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                "Authorization": `JWT ${localStorage.getItem("access")}` 
        }}
      const response = await axios.post('http://localhost:8000/api/lessons/', body, config);
      console.log('New lesson created:', response.data);
    } catch (error) {
      console.error('Error creating new lesson:', error);
    }
    };
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 6.1 Breadth-first search </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>
              <p>
                Definition 1. Let <InlineMath>G</InlineMath> be a simple graph. A <InlineMath>{'\\textit{spanning tree }'}</InlineMath> of <InlineMath>G</InlineMath> is 
                a subgraph of <InlineMath>G</InlineMath> that is a tree containing every vertex of <InlineMath>G</InlineMath>.
              </p>
              <p>
                Theorem 1. A simple graph is connected if and only if it has a spanning tree.
              </p>
              <p>
                We can build a spanning tree for a connected simple graph using <InlineMath>{'\\textbf{breadth-first search}'}</InlineMath>. A 
                rooted tree will be constructed, and the underlying undirected graph of this rooted tree forms the spanning tree. Arbitrarily 
                choose a root from the vertices of the graph. Then add all edges incident to this vertex. The new vertices added at this stage 
                become the vertices at level <InlineMath>1</InlineMath> in the spanning tree. Arbitrarily order them. Next, for each vertex at 
                level <InlineMath>1</InlineMath>, visited in order, add each edge incident to this vertex to the tree as long as it does not produce a simple 
                circuit. Arbitrarily order the children of each vertex at level <InlineMath>1</InlineMath>. This produces the vertices at 
                level <InlineMath>2</InlineMath> in the tree. Follow the same procedure until all the vertices in the tree have been added. The 
                procedure ends because there are only a finite number of edges in the graph. A spanning tree is produced because we have produced 
                a tree containing every vertex of the graph. 
              </p>
              <p>
                Below is the implementation of breadth-first search.
              </p>
              <p>
                Algorithm 1. Breadth-First Search.
              </p>
              <BlockMath math="
              \begin{align*}
                & \textbf{procedure }\textit{BFS}(G:\text{ connected graph with vertices }v_1,v_2,\dots,v_n) \\
                & T:=\text{tree consisting only of vertex }v_1\\
                & L:=\text{empty list}\\
                & \text{put }v_1 \text{ in the list }L\text{ of unprocessed vertices} \\
                & \textbf{while }L\text{ is not empty} \\
                & \quad \text{remove the first vertex, }v, \text{ from }L\\
                & \quad  \textbf{for }\text{each neighbor }w\text{ of }v\\
                & \quad \quad \textbf{if }w\text{ is not in }L\text{ and not in }T\textbf{ then}\\
                & \quad \quad \quad \text{add }w\text{ to the end of the list }L\\
                & \quad \quad \quad \text{add }w\text{ and edge }\{v,w\}\text{ to }T
              \end{align*}
              "/>
            </h5>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <BFSPuzzle/>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
            <Link to="/topics/BFSandDFS/dfs">
            <button 
              style={{
                position: 'fixed',
                bottom: '0',
                right: '0',
                margin: '20px',
                }} 
              onClick={handleCreateLesson}>Finish Lesson</button>
            </Link>
            </div>
        </LeafTopic>
    );
}

export default BFS;
