import { useEffect } from 'react';

function Cell({ id, elem, setData }) {
  // TODO allow user to edit this
  return (
    <div key={id} style={{
      display: 'inline',
      padding: '1em',
      border: 'solid 1px black',
      backgroundColor: (elem.swap) ? 'red' : (elem.read) ? 'green' : 'white',
    }}>
      {elem.val}
    </div>
  );
}

function Vector({ data, setData }) {
  useEffect(() => {
    setData([]);
  }, [setData]);

  return (
    <div style={{
      position: 'absolute',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      WebkitTransform: 'translate(-50%, -50%)',
    }}>
      {data?.map((e, i) => (
        <Cell key={i} id={i} elem={e} setData={setData} />
      ))}
    </div>
  );
}

export default Vector;
