import React from "react";
import LeafTopic from "./LeafTopic";
import { Link } from "react-router-dom";
import axios from "axios";
import { useState, useEffect } from "react";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';
//this button will take user to modules associated with Sets, Functions, sequences, sums 
function QuickSort() {
    const [rating, setRating] = useState(0);
    //const [communityRating, setCommunityRating] = useState(0);
    const [hoverRating, setHoverRating] = useState(0);

    const lessonID = 12;
    const [scoreSum, setScoreSum] = useState(null);
    const [scoreCount, setScoreCount] = useState(null);

    useEffect(() => {
      const config = {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `JWT ${localStorage.getItem('access')}`,
                'Accept': 'application/json'
            }
        };   

    const fetchScoreData = async () => {
      
      try {
        const response = await axios.get(`http://localhost:8000/ratelesson/${lessonID}/`, config);
        setScoreSum(response.data.scoreSum);
        setScoreCount(response.data.scoreCount);
      } catch (error) {
        console.error('Error fetching score data:', error);
      }
    };

    fetchScoreData();
  }, [lessonID]);
    
    const handleStarClick = async (starValue) => {
       const config = {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `JWT ${localStorage.getItem('access')}`,
                'Accept': 'application/json'
            }
        };

        try {
            const res = await axios.put("http://localhost:8000/ratelesson/12/", 
            {
              scoreSum: scoreSum + starValue,
              scoreCount: scoreCount + 1,

            }, config);
            console.log(res);
        } catch (e) {
            console.error(e);
        }
        
            
    setScoreSum(scoreSum + starValue);
    setScoreCount(scoreCount + 1);
    setRating(starValue);
    
    const data = {
      rating: starValue
    };
  };

  const handleStarHover = (starValue) => {
    setHoverRating(starValue);

  };

  const renderStar = (starValue) => {
    const starStyle = {
      fontSize: '24px',
      cursor: 'pointer', //to change cursor
      color: starValue <= (hoverRating || rating) ? 'gold' : 'gray',
      transition: 'color 0.3s ease'
    };
    const filled = starValue <= (hoverRating || rating) ? 'filled' : '';
    return (
      <span
        style = {starStyle}
        onClick={() => handleStarClick(starValue)}
        onMouseEnter={() => handleStarHover(starValue)}
        onMouseLeave={() => handleStarHover(0)}
      >
        &#9733; 
      </span>
    );
  };
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' 
            }}>
            <h1>Quicksort</h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' 
            }}>
            <h5>
              <p>
              Quicksort is popular because it is not difficult to implement, works well for a variety of different kinds of input 
              data, and is substantially faster than any other sorting method in typical applications. The quicksort algorithm's 
              desirable features are that it is in-place (uses only a small auxiliary stack) and that it requires time proportional 
              to <InlineMath>n\log n</InlineMath> on the average to sort an array of length <InlineMath>n</InlineMath>.
              </p>
              <p>
                Quicksort is a divide-and-conquer method for sorting. It works by partitioning an array into two subarrays, then sorting 
                the subarrays independently. Quicksort is complementary to mergesort: for mergesort, we break the array into two subarrays 
                to be sorted and then combine the ordered subarrays to make the whole ordered array; for quicksort, we rearrange the array 
                such that, when the two subarrays are sorted, the whole array is ordered. In the first instance, we do the two recursive 
                calls before working on the whole array; in the second instance, we do the two recursive calls after working on the whole 
                array. For mergesort, the array is divided in half; for quicksort, the position of the partition depends on the choice of 
                the pivot.
              </p>
              <p>
                Below is the implementation of Quicksort.
              </p>
              <p>
                Algorithm 1. Quicksort.
              </p>
              <BlockMath math="
              \begin{align*}
                & \textbf{procedure }\textit{quicksort}(A, l, r) \\
                & \textbf{if }r-l+1\leq 1\textbf{ then}\\
                & \quad \textbf{return} \\
                & p:=\textit{choosepivot}(A, l,r) \\
                & \textit{partition}(A, p, l, r)\\
                & \textit{quicksort}(A, l, p-1) \\
                & \textit{quicksort}(A, p+1, r)
              \end{align*}
              "/>
              <p>
                Algorithm 2. Partition.
              </p>
              <BlockMath math="
              \begin{align*}
                & \textbf{procedure }\textit{partition}(A, p, l, r) \\
                & i:=l+1\\
                & \textbf{for }j:=l+1\textbf{ to }r\\
                & \quad \textbf{if }A[j]\leq p \textbf{ then}\\
                & \quad \quad \text{interchange }A[i]\text{ and }A[j]\\
                & \quad \quad i:=i+1\\
                & \text{interchange }A[i-1]\text{ with }p\\
                & \textbf{return }i-1
              \end{align*}
              "/>
              <p>
                Algorithm 3. Choosepivot.
              </p>
              <BlockMath math="
              \begin{align*}
                & \textbf{procedure }\textit{choosepivot}(A, l, r) \\
                & x := \text{random element from }A[l]\text{ to }A[r] \\
                & \textbf{return }x
              \end{align*}
              "/>
              <p>
                The time complexity of Quicksort is <InlineMath>O(n\log n)</InlineMath> on average, but the worst 
                case time complexity is <InlineMath>O(n^2)</InlineMath> when the pivot choice consistently results in unbalanced partitions.
              </p>
            </h5>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/BinarySearchTree">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' 
            }}>
                {[1, 2, 3, 4, 5].map((value) => (
                    <React.Fragment key={value}>
                        {renderStar(value)}
                    </React.Fragment>
                ))}
                <fetchScoreData/>
                <p>Your rating: {rating}/5</p>
                <p>Community rating: {scoreSum / scoreCount} / 5</p>
            </div>
        </LeafTopic>
    );
}

export default QuickSort;
