export default function RecursionTree({ tree, depth=0 }) {
  return (
    <div style={{
      borderLeft: 'solid 1px black',
      marginLeft: '2em',
      paddingLeft: '.25em',
    }}>
      ({tree.params.join()}) = {JSON.stringify(tree.return)}
      {tree.children.map((t) => <RecursionTree key={t.id} tree={t} depth={depth+1} />)}
    </div>
  );
}
