import React, { useState } from 'react';
import { Navigate, useParams } from 'react-router-dom';
import { connect } from 'react-redux';
import { reset_password_confirm } from '../authentication/auth';


const ResetPasswordConfirm = ({ reset_password_confirm }) => {
    const [requestSent, setRequestSent] = useState(false);
    const { uid, token } = useParams();
    const [formData, setFormData] = useState({
        new_password: '',
        re_new_password: '' 
    });

    const { new_password, re_new_password } = formData;

    const onChange = e => setFormData({ 
      ...formData, 
      [e.target.name]: e.target.value });

    const onSubmit = e => {
        e.preventDefault();
        reset_password_confirm(uid, token, new_password, re_new_password);
        setRequestSent(true);
    };


    if (requestSent) {
        return <Navigate to='/' />;
    }

    return (
        <div className='container mt-5'>
            <h1 style={{fontFamily: "Acumin Pro"}}>Reset Password</h1>
            <p>Enter a new password: </p>
            <form onSubmit={e => onSubmit(e)}>
            <div className='form-group'>
                    <label></label>
                    <input
                        className='form-control'
                        type='password'
                        placeholder='New Password'
                        name='new_password'
                        value={new_password}
                        onChange={e => onChange(e)}
                        minLength='6'
                        required
                    />
                </div>
                <div className='form-group'>
                    <label></label>
                    <input
                        className='form-control'
                        type='password'
                        placeholder='Confirm New Password'
                        name='re_new_password'
                        value={re_new_password}
                        onChange={e => onChange(e)}
                        minLength='6'
                        required
                    />
                </div>
                <button className='btn btn-primary mt-4' type='submit'>Reset Password</button>
            </form>
            
        </div>
    );
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, { reset_password_confirm })(ResetPasswordConfirm);