import { useEffect } from "react";
import { useLocation } from "react-router-dom";

function LeafTopic({ children }) {
  const location = useLocation();
  useEffect(() => {
    localStorage.setItem("resume", location.pathname);
  }, [location]);
  return (
    <div>
      {children}
    </div>
  );
}

export default LeafTopic;
