import { useEffect } from "react";

import Vertex from "./Vertex.js";
import Edge from "./Edge.js";

import styles from "../../styles/Whiteboard.module.css";

function idToLetter(id) {
  return  String.fromCharCode("A".charCodeAt(0)+id);
}

function Graph({ data, setData, onInteract }) {
  useEffect(() => {
    setData({
      AL: [
        { id: 0, x: 100, y: 100, edges: [{value: 1, vertex: 1}] },
        { id: 1, x: 400, y: 400, edges: [{value: 1, vertex: 0}] },
      ],
      visited: [],
      marked: [],
      visiting: -1,
      selected: false,
      vertex_id_selected: -1,
      marked_edges: [],
      directed: true,
    });
  }, [setData]);

  useEffect(() => {
    console.log("Data updated:", data);
  }, [data]); // Run this effect whenever data changes

  /*
  useEffect(() => {
    console.log("The vertex id selected value has changed:", data.vertex_id_selected);
  }, [data.vertex_id_selected]);
  */

  return (
    <>
      {/* Render vertices */}
      {data.AL.map((item) => (item &&
        <Vertex
          key={item.id}
          x={item.x}
          y={item.y}
          letter={idToLetter(item.id)}
          onClick={() => {
            console.log("item id: ", item.id);
            //data.starting_vertex = item.id;
            
            setData(prevData => ({
              ...prevData,
              vertex_id_selected: item.id
            }));
            
            
            onInteract?.(item);
          }}
          bg_color={
            data.visited?.includes(item.id) ? 'red'
            : data.marked?.includes(item.id) ? 'yellow'
            : data.visiting === item.id ? 'green'
            : data.selected ? '#E79B8C' : 'white'
          }
        />
      ))}
      {/* Render edges */}
      {data.AL.map((item) => (
        item?.edges.map((edge, edgeIndex) => {
          const other = data.AL[edge.vertex];
          
          //if the graph is undirected, only render one direction
          const renderEdge = data.directed || item.id < edge.vertex;  

          return renderEdge && (
            <Edge
              key={edgeIndex}
              x1={item.x} 
              y1={item.y}
              x2={other.x}
              y2={other.y}
              value={edge.value}
              directed={data.directed}  
              bg_color= {(data.marked_edges != null && data.marked_edges.includes({x1: item.x, x2: item.y, y1: other.x, y2: other.y})) ? "red" : "black"}
              onClick={() => onInteract?.({ other: edge.vertex, ...item } )}
            /> 
          );
        })
      ))}
      {data.selected && <Infobar adjList={data.AL} />}
    </>
  );
}

function Infobar({ adjList }) {

  const vertices = adjList.filter(v => v != null).map((item) => item.id).sort();

  function createAdjMatrix(adjList) {
    console.log("called create adj matrix");
    const matrix = [];
    for (let i = 0; i < vertices.length; i++) {
      matrix[i] = new Array(vertices.length).fill(0);
    }
    adjList.forEach((item) => {
      const row = vertices.indexOf(item?.id);
      item.edges.forEach((edge) => {
        const col = vertices.indexOf(edge.vertex);
        matrix[row][col] = edge.value;
      });
    });

    return matrix;
  };

  const adjMatrix = createAdjMatrix(adjList);
  //adjacency list and adjacency matrix
  return (
    <div className={styles.infobar}>
      <div className={styles.tableWrapper}>
      <h2>Adjacency List</h2>
        <table>
          <thead>
            <tr>
              <th>Vertex</th>
              <th>Adjacent Vertices</th>
            </tr>
          </thead>
          <tbody>
            {adjList.map((item) => (
              <tr key={item?.id}>
                <td>{idToLetter(item?.id)}</td>
                <td>{item.edges.map((edge) => idToLetter(edge.vertex)).join(', ')}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      <div className={styles.tableWrapper}>
        <h2>Adjacency Matrix</h2>
        <table>
          <thead>
            <tr>
              <th></th>
              {vertices.map((vertex) => (
                <th key={vertex}>{idToLetter(vertex)}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {vertices.map((vertex, rowIndex) => (
              <tr key={vertex}>
                <td>{idToLetter(vertex)}</td>
                {vertices.map((vertex, colIndex) => (
                  <td key={vertex}>
                    {adjMatrix[rowIndex][colIndex]}
                    {colIndex < vertices.length - 1 && <span className={styles.verticalBar}></span>}
                  </td>
                ))}
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default Graph;
