const MultipleChoiceProblem = ({ question, options, correctAnswer }) => {
    const [selectedOption, setSelectedOption ] = useState(null);
    const [showSolution, setShowSolution] = useState(false);

    const add_quiz = async () => {
        const config = {
            headers: {
                "Authorization": `JWT ${localStorage.getItem("access")}`,
                "quiz-name": "Induction MCQ4",
                "difficulty": "Easy"
            }
        };
        console.log("adding quiz");
        try {
            const data = {};
            await axios.post(`${process.env.REACT_APP_API_URL}/api/add-quiz/`, data, config);
        } catch (err) {
            console.log(err);
        }
    };

    const mark_incorrect = async () => {
        const config = {
            headers: {
                "Authorization": `JWT ${localStorage.getItem("access")}`,
                "quiz-name": "Induction MCQ4"
            }
        };
        console.log(config);
        try {
            const data = {};
            await axios.post(`${process.env.REACT_APP_API_URL}/api/set-quiz-wrong/`, data, config);
        } catch (err) {
            console.log(err);
        }
        setMarkedIncorrect(true);
        setShowSolution(true);
    };

    const mark_correct = async () => {
        const config = {
            headers: {
                "Authorization": `JWT ${localStorage.getItem("access")}`,
                "quiz-name": "Induction MCQ4"
            }
        };
        try {
            const data = {};
            await axios.post(`${process.env.REACT_APP_API_URL}/api/set-quiz-correct/`, data, config);
        } catch (err) {
            console.log(err);
        }
        setMarkedIncorrect(false);
        setIncorrect(false);
    };

    const fetch_quiz_status = async () => {
        const config = {
            headers: {
                "Authorization": `JWT ${localStorage.getItem("access")}`,
                "quiz-name": "Induction MCQ4"
            }
        };
        console.log(config);
        try {
            const quiz_status = await axios.get(`${process.env.REACT_APP_API_URL}/api/get-quiz-status/`, config);
            console.log(quiz_status.data);
            setIncorrect(!quiz_status.data['quiz_status']);
        } catch (err) {
            console.log(err);
        }
    };

    useEffect(() => {
        add_quiz();
        fetch_quiz_status();
    }, []);

    //implement solution box to show only if "Show Solution" is clicked

    const [incorrect, setIncorrect] = useState(false);
    const [markedIncorrect, setMarkedIncorrect] = useState(false);
    
    const handleOptionSelect = (option) => {
        setSelectedOption(option);
    };

    const checkAnswer = () => {
        setShowSolution(true);
        if (selectedOption === correctAnswer) {
            mark_correct();
        }
        else {
            mark_incorrect();
        }
    }

    return (
        <div>
            <p>{question}</p>
            {options.map((option, index) => (
                <div key={index}>
                    <input
                        type="radio"
                        id={`option-${index}`}
                        name="options"
                        value={option}
                        checked={selectedOption === option}
                        onChange={() => handleOptionSelect(option)}
                        disabled={showSolution}
                    />
                    <label htmlFor={`option-${index}`}>{option}</label>
                </div>
            ))}
            <button onClick={checkAnswer} disabled={selectedOption === null || showSolution}>
                Submit Answer
            </button>
            <button className="btn btn-danger" onClick={mark_incorrect}>Mark question incorrect</button>
            {incorrect && <div className="alert alert-danger">Question previously answered incorrectly 
            or marked as incorrect.</div>}
            {showSolution && (
                <p>
                    Correct Answer: {correctAnswer}
                    {selectedOption === correctAnswer ? (
                        <span style={{ color: 'green' }}> (Correct)</span>
                    ) : (
                        <span style={{ color: 'red' }}> (Incorrect)</span>
                    )}
                </p>
            )}
        </div>
    );
};

