import React from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 1.1 Selection Sort
function InsertSort() {
    return (
        <LeafTopic>
           <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 1.2 Insertion Sort </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>
                <p>
                    The insertion sort is a simple sorting algorithm, but it is usually not the most efficient. To sort a list 
                    with <InlineMath>n</InlineMath> elements, the insertion sort begins with the second element. The insertion sort 
                    compares this second element with the first element and inserts it before the first element if it does not 
                    exceed the first element and after the first element if it exceeds the first element. At this point, the first 
                    two elements are in the correct order. The third element is then compared with the first element, and if it is 
                    larger than the first element, it is compared with the second element; it is inserted into the correct position 
                    among the first three elements.
                </p>
                <p>
                    In general, in the <InlineMath>j</InlineMath>th step of the insertion sort, the <InlineMath>j</InlineMath>th element 
                    of the list is inserted into the correct position in the list of the previously sorted <InlineMath>j-1</InlineMath> elements. 
                    To insert the <InlineMath>j</InlineMath>th element in the list, a linear search technique is used; the <InlineMath>j</InlineMath>th element 
                    is successfully compared with the already sorted <InlineMath>j-1</InlineMath> elements at the start of the list until the 
                    first element that is not less than this element is found or until it has been compared with all <InlineMath>j-1</InlineMath> elements; 
                    the <InlineMath>j</InlineMath>th element is inserted in the correct position so that the first <InlineMath>j</InlineMath> elements 
                    are sorted. The algorithm continues until the last element is placed in the correct position relative to the already sorted list of the 
                    first <InlineMath>n-1</InlineMath> elements. Below is the pseudocode for the insertion sort.
                </p>
                <br/>
                <p>
                    Algorithm 1. The Insertion Sort.
                </p>
                <BlockMath math="
                \begin{align*}
                    & \textbf{procedure }\textit{insertion sort}(a_1,a_2,\dots,a_n:\text{ real numbers with }n\geq 2) \\
                    & \textbf{for }j:=2\textbf{ to }n\\
                    & \quad i:= 1 \\
                    & \quad \textbf{while }a_j>a_i \\
                    & \quad \quad i:=i+1 \\
                    & \quad m:=a_j \\
                    & \quad \textbf{for }k:=0\textbf{ to }j-i-1 \\
                    & \quad \quad a_{j-k}:=a_{j-k-1} \\
                    & \quad a_i:=m \\
                    & \{a_1,\dots,a_n\text{ is in increasing order}\}
                \end{align*}
                "/>
                <p>
                    The average time complexity of the insertion sort is <InlineMath>O(n^2)</InlineMath>.
                </p>
            </h5>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/MergeSort">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
        </LeafTopic>
    );
}

export default InsertSort;
