import React from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import { useState } from "react";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 8.1  Dijkstra's Algorithm
function DijkstraAlgo() {
    const graph = {
        'A': { 'B': 2, 'D': 3 },
        'B': { 'A': 2, 'C': 5, 'D': 1 },
        'C': { 'B': 5, 'D': 4, 'E': 6 },
        'D': { 'A': 3, 'B': 1, 'C': 4, 'E': 8 },
        'E': { 'C': 6, 'D': 8 },
        };
    
  const [selectedEdges, setSelectedEdges] = useState([]);
  const [errorMessage, setErrorMessage] = useState('');
  const [currentEdgeIndex, setCurrentEdgeIndex] = useState(0);

  const correctOrder = ['AB', 'BD', 'DC',  'CE'];

  const edgesList = Object.keys(graph).flatMap(node => 
    Object.keys(graph[node]).map(neighbor => `${node}${neighbor}`)
  );

  const handleClick = (from, to) => {
    const edge = `${from}${to}`;
    const edge2 = `${to}${from}`
    if ((edge === correctOrder[currentEdgeIndex]) || (edge2 === correctOrder[currentEdgeIndex])) {
      setSelectedEdges([...selectedEdges, edge]);
      setCurrentEdgeIndex(currentEdgeIndex + 1);
      if (currentEdgeIndex === 3) {
        setErrorMessage('You completed the puzzle!');
      }
      else {
        setErrorMessage('Thats correct!');
      }
    } else {
      setErrorMessage('Error! Wrong edge selected.');
    }
  };

  const nodesPosition = {
    'A': { x: 50, y: 50 },
    'B': { x: 150, y: 100 },
    'C': { x: 250, y: 50 },
    'D': { x: 150, y: 200 },
    'E': { x: 350, y: 150 },
  };

  const renderEdges = () => {
    return edgesList.map(edge => {
      const node = edge[0];
      const neighbor = edge[1];
      const weight = graph[node][neighbor];
      const startX = nodesPosition[node].x;
      const startY = nodesPosition[node].y;
      const endX = nodesPosition[neighbor].x;
      const endY = nodesPosition[neighbor].y;
      const color = selectedEdges.includes(edge) ? 'blue' : 'black';

      return (
        <g key={`${node}-${neighbor}`} onClick={() => handleClick(node, neighbor)} style={{ cursor: 'pointer' }}>
          <line x1={startX} y1={startY} x2={endX} y2={endY} style={{ stroke: color, strokeWidth: '2' }} />
          <text x={(startX + endX) / 2} y={(startY + endY) / 2} textAnchor="middle" dy="5" fill="black">
            {weight}
          </text>
        </g>
      );
    });

    };

    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 8.1 Dijkstra's Algorithm </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>
              <p>
                There are several different algorithms that find a shortest path between two vertices in a weighted graph.
                 The version of Dijkstra's Algorithm we will present solves the shortest path problem in undirected weighted graphs 
                 where all the weights are positive. It is easy to adapt it to solve shortest-path problems in directed graphs.
              </p>
              <p>
                Given an undirected connected simple weighted graph, suppose we want to find the shortest path from vertex <InlineMath>a</InlineMath> to vertex <InlineMath>z</InlineMath>. Dijkstra's 
                algorithm begins by labeling the vertex <InlineMath>a</InlineMath> with <InlineMath>0</InlineMath> and the other 
                vertices with <InlineMath>\infty</InlineMath>. We use the notation <InlineMath>L_0(a)=0</InlineMath> and <InlineMath>L_0(v)=\infty</InlineMath> for 
                these labels before any iterations have taken place (the subscript <InlineMath>0</InlineMath> stands for the "<InlineMath>0</InlineMath>th 
                iteration"). These labels are the lengths of the shortest paths from <InlineMath>a</InlineMath> to the  vertices, where the paths 
                contain only the vertex <InlineMath>a</InlineMath>. (Because no path from <InlineMath>a</InlineMath> to a vertex different 
                from <InlineMath>a</InlineMath> exists, <InlineMath>\infty</InlineMath> is the length of a shortest path between <InlineMath>a</InlineMath> and 
                this vertex.)
              </p>
              <p>
                Dijkstra's algorithm proceeds by forming a distinguished set of vertices. Let <InlineMath>S_k</InlineMath> denote this 
                set after <InlineMath>k</InlineMath> iterations of the labeling procedure. We begin with <InlineMath>S_0=\emptyset</InlineMath>. The 
                set <InlineMath>S_k</InlineMath> is formed from <InlineMath>{'S_{k-1}'}</InlineMath> by adding a vertex <InlineMath>u</InlineMath> not 
                in <InlineMath>{'S_{k-1}'}</InlineMath> with the smallest label.
              </p>
              <p>
                Once <InlineMath>u</InlineMath> is added to <InlineMath>{'S_{k}'}</InlineMath>, we update the labels of all vertices not 
                in <InlineMath>S_k</InlineMath>, so that <InlineMath>L_k(v)</InlineMath>, the label of the vertex <InlineMath>v</InlineMath> at 
                the <InlineMath>k</InlineMath>th stage, is the length of a shortest path from <InlineMath>a</InlineMath> to <InlineMath>v</InlineMath> that 
                contains vertices only in <InlineMath>S_k</InlineMath> (that is, vertices that were already in the distinguished set together 
                with <InlineMath>u</InlineMath>). Note that the way we choose the vertex <InlineMath>u</InlineMath> to add to <InlineMath>S_k</InlineMath> at 
                each step is an optimal choice at each step, making this a greedy algorithm. 
              </p>
              <p>
                Let <InlineMath>v</InlineMath> be a vertex not in <InlineMath>S_k</InlineMath>. To update the label of <InlineMath>v</InlineMath>, note 
                that <InlineMath>L_k(v)</InlineMath> is the length of a shortest path from <InlineMath>a</InlineMath> to <InlineMath>v</InlineMath> containing 
                only vertices in <InlineMath>S_k</InlineMath>. The updating can be carried out efficiently when this observation is used: A shortest 
                path from <InlineMath>a</InlineMath> to <InlineMath>v</InlineMath> that contains only elements of <InlineMath>{'S_k'}</InlineMath> is either a 
                shortest path from <InlineMath>a</InlineMath> to <InlineMath>v</InlineMath> that contains only elements of <InlineMath>{'S_{k-1}'}</InlineMath> (that is, 
                the distinguished vertices not including <InlineMath>u</InlineMath>), or it is a shortest path from <InlineMath>a</InlineMath> to <InlineMath>u</InlineMath> at 
                the <InlineMath>(k-1)</InlineMath>st stage with the edge <InlineMath>{'\\{u,v\\}'}</InlineMath> added. In other words
              </p>
              <BlockMath math="
              L_k(a,v)=\min\{L_{k-1}(a,v),L_{k-1}(a,u)+w(u,v)\}
              "/>
              <p>
                where <InlineMath>w(u,v)</InlineMath> is the length of the edge with <InlineMath>u</InlineMath> and <InlineMath>v</InlineMath> as 
                endpoints. This procedure is iterated by successively adding vertices to the distinguished set until <InlineMath>z</InlineMath> is added. 
                When <InlineMath>z</InlineMath> is added to the distinguished set, its label is the length of a shortest path from <InlineMath>a</InlineMath> to <InlineMath>z</InlineMath>.
              </p>
              <p>
                Algorithm 1. Dijkstra's Algorithm.
              </p>
              <BlockMath math="
              \begin{align*}
                  & \textbf{procedure }\textit{Dijkstra}(G:\text{ weighted connected simple graph, with}\\
                  & \quad \text{all weights positive}) \\
                  & \{G\text{ has vertices }a=v_0,v_1,\dots,v_n=z\text{ and lengths }w(v_i,v_j) \\
                  & \quad \text{where }w(v_i,v_j)=\infty \text{ if }\{v_i,v_j\}\text{ is not an edge in }G\}\\
                  & \textbf{for }i:=1\textbf{ to }n\\
                  & \quad L(v_i):=\infty \\
                  & L(a):= 0 \\
                  & S:=\emptyset \\
                  & \{\text{the labels are now initialized so that the label of }a\text{ is }0\text{ and all}\}\\
                  & \quad \text{other labels are }\infty\text{, and }S\text{ is the empty set}\\
                  & \textbf{while }z\not\in S \\
                  & \quad u:=a \text{ vertex not in }S\text{ with }L(u)\text{ minimal}\\
                  & \quad S:=S\cup\{u\} \\
                  & \quad \textbf{for }\text{all vertices }v\text{ not in }S \\
                  & \quad \quad \textbf{if }L(u)+w(u,v)<L(v)\textbf{ then }L(v):= L(u)+w(u,v)\\
                  & \quad \quad \{\text{this adds a vertex to }S\text{ with minimal label and updates the}\\
                  & \quad \quad \text{labels of vertices not in }S\}\\
                  & \textbf{return }L(z) \ \{L(z)=\text{length of a shortest path from }a\text{ to }z\}
              \end{align*}
              "/>
              <p>
                Note that in general, Dijkstra's algorithm can be used to find the shortest paths from a source 
                vertex <InlineMath>a</InlineMath> to all other vertices.
              </p>
            </h5>
            </div>
            <div style={{ textAlign: 'center', marginTop: "5px", marginBottom: "5px"}}>
                <h3>Select edges in the order that they are added to the shortest path tree when Djikstra's is run on node A</h3>
                <svg width="400" height="250" style={{ border: '1px solid black' }}>
                    {renderEdges()}
                    {Object.keys(nodesPosition).map((node) => (
                    <circle key={node} cx={nodesPosition[node].x} cy={nodesPosition[node].y} r="15" fill="lightblue" />
                    ))}
                    {Object.keys(nodesPosition).map((node) => (
                    <text key={`text-${node}`} x={nodesPosition[node].x} y={nodesPosition[node].y} textAnchor="middle" dy="5" fill="black">
                        {node}
                    </text>
                    ))}
                </svg>
                {errorMessage && <p style={{ color: 'red' }}>{errorMessage}</p>}
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/ShortestPath/bellmanford">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
            
            
        </LeafTopic>
    );
}

export default DijkstraAlgo;
