import React from "react";
import { useState } from "react";
function DFSPuzzle() {
  const [selectedCircles, setSelectedCircles] = useState([]);
  const [message, setMessage] = useState('');

  const handleCircleClick = (circle) => {
    const correctOrder = ['A', 'B', 'C', 'D', 'E'];
    const nextCircle = correctOrder[selectedCircles.length];

    if (circle.label === nextCircle) {
      setSelectedCircles((prevCircles) => [...prevCircles, circle.label]);
      setMessage('Thats correct!');
    } else {
      setMessage(`Try again ${circle.label} is not the right answer!`);
    }
  };

  const circles = [
    { id: 1, x: 50, y: 50, label: 'A' },
    { id: 2, x: 150, y: 50, label: 'B' },
    { id: 3, x: 250, y: 50, label: 'C' },
    { id: 4, x: 100, y: 150, label: 'D' },
    { id: 5, x: 200, y: 150, label: 'E' },
  ];

  const edges = [
    { start: 1, end: 2 },
    { start: 2, end: 3 },
    { start: 3, end: 4 },
    { start: 4, end: 5 },
    { start: 3, end: 5 },
  ];

  return (
    <div>
        <h2>Select the nodes in DFS order starting at A!</h2>
      <svg width="300" height="200">
        {edges.map((edge, index) => (
          <line
            key={index}
            x1={circles[edge.start - 1].x}
            y1={circles[edge.start - 1].y}
            x2={circles[edge.end - 1].x}
            y2={circles[edge.end - 1].y}
            stroke="black"
          />
        ))}
        {circles.map((circle) => (
          <g key={circle.id}>
            <circle
              cx={circle.x}
              cy={circle.y}
              r={20}
              fill={selectedCircles.includes(circle.label) ? 'red' : 'lightblue'}
              onClick={() => handleCircleClick(circle)}
            />
            <text
              x={circle.x}
              y={circle.y}
              textAnchor="middle"
              alignmentBaseline="central"
            >
              {circle.label}
            </text>
          </g>
        ))}
      </svg>
      <p>Selected circles: {selectedCircles.join(', ')}</p>
      <p>{message}</p>
    </div>
  );
}

export default DFSPuzzle;