import { React, useEffect } from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import axios from "axios";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';
var Latex = require("react-latex")

//Lesson 2.1 Sets 
function Sets() {
    const url = window.location.href;
    
    const add_page = async () => {
        const config = {
            headers: {
                "Authorization": `JWT ${localStorage.getItem("access")}`,
                "lesson-url": `${url}`
            }
        };
        console.log(config);
        try {
            const data = {};
            await axios.post(`${process.env.REACT_APP_API_URL}/api/modify-page/`, data, config);
        } catch (err) {
            console.log(err);
        }
    };

    useEffect(() => {
        add_page()
    }, []);

    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 2.1 Sets </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5> 
            <p>
            Recall the following important sets:
            </p>
            <BlockMath math="
            \begin{align*}
                & \mathbb{N}=\{0,1,2,3,\dots\}\\ 
                & \mathbb{Z}=\{0,1,-1,2,-2,\dots\}\\
                & \mathbb{Z}^+=\{1,2,3,\dots\}\\
                & \mathbb{Q}=\left\{\frac{p}{q}\mid p\in\mathbb{Z}\text{ and }q\in\mathbb{Z}^+\right\}\\
                & \mathbb{R}=\text{the set of real numbers}\\
                & \mathbb{R}^+=\text{the set of positive real numbers}\\
                & \mathbb{C}=\{a+bi\mid a,b\in\mathbb{R}\}
            \end{align*}
            "/>
            We will review the interval notation:
            <Latex displayMode={true}>{`$$
            [a,b]=\\{x\\mid a\\leq x \\leq b\\}\\\\
            [a,b)=\\{x\\mid a\\leq x < b\\}\\\\
            (a,b]=\\{x\\mid a< x \\leq b\\}\\\\
            (a,b)=\\{x\\mid a < x < b\\}\\\\
            $$`}</Latex>
            <BlockMath math="
            \begin{align*}
                & \textbf{Definition 1:}\text{ Two sets } A \text{ and }B\text{ are equal if and only if they have the same elements, 
                and we write }A=B. \\
                & \textbf{Definition 2: }\text{The set }A\text{ is a subset of }B\text{, denoted }A\subseteq B\text{, if and only if every 
                element of }A\text{ is also an element of }B.\\
                & \text{Equivalently, }A\subseteq B \equiv \forall x(x\in A\Longrightarrow x\in B)\\
                & \textbf{Definition 3: }A\cap B = \{x\mid x\in A \land x\in B\}\quad A \cup B = \{x\mid x\in A \lor x \in B\}\quad A^C = \{x\mid x\not\in A\}\quad A-B=\{x\in A : x\not\in B\}
            \end{align*}
            "/>
            </h5>
            </div>
            <div style={{ position: 'relative'}}>
                <Link to = "/topics/SetsFunctions/functions">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' 
            }}>
                <Link to="/topics/SetsFunctions/SetPuzzle">
                <button style={{margin: "20px", fontSize: "16px", zIndex: 9999}} >
                    Set Puzzle
                </button>
                </Link>
            </div>
        </LeafTopic>
    );
}

export default Sets;
