import { useEffect, useState } from 'react';

import SetProblem from './SetProblem';
import { infixPrecedence } from './SetProblem';
import SetEnv from './SetEnv';
import SetVisualizer from './SetVisualizer';
import Latex from "react-latex";

function randomSP() {
  const sp = new SetProblem();
  for (let i = 0; i < 10; i++) {
    if (sp.sets.size >= 2 && Math.random() >= 1/sp.sets.size) break;
    const c = String.fromCharCode("A".charCodeAt(0)+i);
    sp.set(c, new Set([...Array(10).keys()].filter(x => Math.random() < 1/2)));
  }
  return sp;
}

function randomSE(sp, depth=0) {
  if (Math.random() >= 1/(depth+1)) {
    const vars = [...sp.sets.keys()];
    return vars[Math.floor(Math.random()*vars.length)];
  }
  const types = ['+', '*', '-'];
  const randT = types[Math.floor(Math.random()*types.length)];
  return { "type": randT, "sets": [ randomSE(sp, depth+1), randomSE(sp, depth+1) ] };
}

function SEtoStr(se, lp=0, rp=0) {
  if (typeof se === "string") return se;
  const pr = infixPrecedence.get(se.type);
  if (pr[0] < lp || pr[1] < rp) return `(${SEtoStr(se.sets[0], 0, pr[0])} ${se.type} ${SEtoStr(se.sets[1], pr[1], 0)})`;
  let str = `${SEtoStr(se.sets[0], lp, pr[0])} ${se.type} ${SEtoStr(se.sets[1], pr[1], rp)}`;
  return str;
}

function texify(se) {
  const latexSym = {
    '+': '\\cup',
    '*': '\\cap',
    '-': '-',
  };
  let str = se;
  for (const [k, v] of Object.entries(latexSym)) {
    str = str.replaceAll(k, v);
  }
  return str;
}

export default function SetPuzzle() {
  // 0 - generating problem, 1 - waiting for answer, 2 - check answer, 3/-3 - visualize solution (+ correct, -incorrect)
  const [state, setState] = useState(0);
  const [type, setType] = useState(null);
  const [rite, setRite] = useState(0);
  const [sp, setSP] = useState(null);
  const [se, setSE] = useState(null);
  const [ans, setAns] = useState(new Set());
  const [visualize, setVisualize] = useState(false);

  useEffect(() => {
    if (state === 0) {
      if (Math.floor(Math.random() * 2)) {
        setType(new Set([...Array(10).keys()].filter(x => Math.random() < 1/2)));
        setRite(Math.floor(Math.random() * 2));
      } else {
        setType(null);
      }
      setVisualize(false);
      setSP(o => {
        const n = randomSP()
        setSE(SEtoStr(randomSE(n)));
        return n;
      });
      setState(1);
    } else if (state === 2) {
    }
  }, [state, sp]);

  const handleKeyPress = (e) => {
    if (e.key !== "Enter") return;
    const nAns = new Set(e.target.value.split(' ').filter(x => x !== "").map(Number));
    setAns(nAns);
    const exp = sp.eval(SetProblem.parse(se));
    setState((nAns.size === exp.size && [...nAns].every(x => exp.has(x))) ? 3 : -3);
  };

  return (
    <div style={{
      margin: '1em',
    }}>
      <div> Given:
        <div style={{
          marginLeft: '20px',
          backgroundColor: 'lightgrey',
          padding: '.5em',
          width: 'fit-content',
        }}>
          {state !== 0 && <SetEnv env={sp} />}
        </div>
      </div>
      <div> {type == null ? 'What does the following evaluate to?' : 'Is the following correct?'}
        <div style={{
          marginLeft: '20px',
          backgroundColor: 'lightgrey',
          padding: '.5em',
          width: 'fit-content',
        }}>
          {state !== 0 && <Latex displayMode={true}>{`$$${texify(se)}${type == null ? "" : "=\\{" + [...(rite ? sp.eval(SetProblem.parse(se)) : type)].join(',')+"\\}"}$$`}</Latex>}
        </div>
      </div>
      {
        Math.abs(state) < 3 ? (
          type == null ? <input onKeyPress={handleKeyPress} style={{
            margin: '1em',
          }}/> :
            <div style={{
              width: 'fit-content',
              margin: '1em',
              padding: '.5em',
            }}>
            <button onClick={() => setState(rite ? 3 : -3)}>True</button>
            <button onClick={() => setState(rite ? -3 : 3)}>False</button>
          </div>
        ) : (
          <>
            <div style={{
              backgroundColor: (state > 0) ? '#00ff0080' : '#ff000080',
              width: 'fit-content',
              margin: '1em',
              padding: '.5em',
            }}>
              {(state > 0) ? 'Correct' : 'Incorrect'}
              {(state < 0) && <div>Solution: {[...sp.eval(SetProblem.parse(se))].join(' ')}</div>}
              {(state < 0) && <div>Got: {[...ans].join(' ')}</div>}
            </div>
            <div style={{
              padding: '.5em',
            }}>
              <button onClick={() => setVisualize(true)}>Visualize</button>
              <button onClick={() => setState(0)}>Next</button>
            </div>
            {visualize && <SetVisualizer expr={se} env={sp} />}
          </>
        )
      }
    </div>
  );
}
