import React from "react";
import LeafTopic from "./LeafTopic";
import { Link } from "react-router-dom";
import axios from "axios";
import { useState, useEffect } from "react";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//this button will take user to modules associated with Sets, Functions, sequences, sums 
function BinarySearchTree() {
    const [rating, setRating] = useState(0);
    //const [communityRating, setCommunityRating] = useState(0);
    const [hoverRating, setHoverRating] = useState(0);

    const lessonID = 13;
    const [scoreSum, setScoreSum] = useState(null);
    const [scoreCount, setScoreCount] = useState(null);

    useEffect(() => {
      const config = {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `JWT ${localStorage.getItem('access')}`,
                'Accept': 'application/json'
            }
        };   

    const fetchScoreData = async () => {
      
      try {
        const response = await axios.get(`http://localhost:8000/ratelesson/${lessonID}/`, config);
        setScoreSum(response.data.scoreSum);
        setScoreCount(response.data.scoreCount);
      } catch (error) {
        console.error('Error fetching score data:', error);
      }
    };

    fetchScoreData();
  }, [lessonID]);
    
    const handleStarClick = async (starValue) => {
       const config = {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `JWT ${localStorage.getItem('access')}`,
                'Accept': 'application/json'
            }
        };

        try {
            const res = await axios.put("http://localhost:8000/ratelesson/13/", 
            {
              scoreSum: scoreSum + starValue,
              scoreCount: scoreCount + 1,

            }, config);
            console.log(res);
        } catch (e) {
            console.error(e);
        }
        
            
    setScoreSum(scoreSum + starValue);
    setScoreCount(scoreCount + 1);
    setRating(starValue);
    
    const data = {
      rating: starValue
    };
  };

  const handleStarHover = (starValue) => {
    setHoverRating(starValue);

  };

  const renderStar = (starValue) => {
    const starStyle = {
      fontSize: '24px',
      cursor: 'pointer', //to change cursor
      color: starValue <= (hoverRating || rating) ? 'gold' : 'gray',
      transition: 'color 0.3s ease'
    };
    const filled = starValue <= (hoverRating || rating) ? 'filled' : '';
    return (
      <span
        style = {starStyle}
        onClick={() => handleStarClick(starValue)}
        onMouseEnter={() => handleStarHover(starValue)}
        onMouseLeave={() => handleStarHover(0)}
      >
        &#9733; 
      </span>
    );
  };
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' 
            }}>
	    <h1>Binary Search Trees</h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' 
            }}>
	    <h5>
            <p>
              A binary search tree is a type of binary tree. The inorder traversal of a binary search tree produces a list of 
              sorted numbers in increasing order. 
            </p>
            <p>
              Specifically, a binary search tree is a binary tree in which each child of a vertex is designated as a right or left child, 
              no vertex has more than one right child or left child, and each vertex is labeled with a key, which is one of the items. 
              Furthermore, vertices are assigned keys so that the key of a vertex is both larger than the keys of all vertices in its 
              left subtree and smaller than the keys of all vertices in its right subtree.
            </p>
            <p>
              This recursive procedure is used to form the binary search tree for a list of items. Start with a tree containing just one 
              vertex, namely, the root. The first item in the list is assigned as the key of the root. To add a new item, first 
              compare it with the keys of vertices already in the tree, starting at the root and moving to the left if the item is less 
              than the key of the respective vertex if this vertex has a left child, or moving to the right if the item is greater than the key of the 
              respective vertex if this vertex has a right child. When the item is less than the respective vertex and this vertex has no left child, 
              then a new vertex with this item as its key is inserted as a new left child. Similarly, when the item is greater than the respective vertex 
              and this vertex has no right child, then a new vertex with this item as its key is inserted as a new right child. 
            </p>
            <p>
              Below is the implementation for locating an item in or adding an item to a binary search tree.
            </p>
            <p>
              Algorithm 1. Locating an Item in or Adding an Item to a Binary Search Tree.
            </p>
            <BlockMath math="
            \begin{align*}
              & \textbf{procedure }\textit{insertion}(T:\text{ binary search tree,}\\
              & x:\text{ item}) \\
              & v:=\text{root of }T\\
              & \{\text{a vertex not present in }T\text{ has the value}\textit{ null}\} \\
              & \textbf{while }v\neq \textit{null}\text{ and }\textit{label}(v)\neq x \\
              & \quad \textbf{if }x<\textit{label}(v)\textbf{ then}\\
              & \quad \quad \textbf{if }\text{left child of }v\neq \textit{null}\textbf{ then }v:=\text{left child of }v\\
              & \quad \quad \textbf{else }\text{add }\textit{new vertex }\text{as a left child of v}\text{ and set }v:=\textit{null}\\
              & \quad \textbf{else}\\
              & \quad \quad \textbf{if }\text{right child of }v\neq \textit{null}\textbf{ then }v:=\text{ right child of }v\\
              & \quad \quad \textbf{else }\text{add }\textit{new vertex }\text{as a right child of }v\text{ and set }v:=\textit{null}\\
              & \textbf{if }\text{root of }T=\textit{null }\textbf{then }\text{add a vertex }v\text{ to the tree and label it with }x\\
              & \textbf{else if }v\text{ is null or }\textit{label}(v)\neq x \textbf{ then }\text{label }\textit{new vertex }\text{with }x\text{ and let }v\text{ be this new vertex}\\
              & \textbf{return }v \ \{v=\text{location of }x\}
            \end{align*}
            "/>
      </h5>
            </div>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' 
            }}>
                <Link to = "/topics/BinarySearchTree/bstinsertion">
                <button
                    style={{margin: "20px", fontSize: "16px"}} >
                    Binary Search Tree Insertion Visualization
                </button>
                </Link>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/BalancedSearchTree/23tree">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' 
            }}>
                {[1, 2, 3, 4, 5].map((value) => (
                    <React.Fragment key={value}>
                        {renderStar(value)}
                    </React.Fragment>
                ))}
                <fetchScoreData/>
                <p>Your rating: {rating}/5</p>
                <p>Community rating: {scoreSum / scoreCount} / 5</p>
            </div>
        </LeafTopic>
    );
}

export default BinarySearchTree;
