import { useState, useEffect } from 'react';

import R from './RecursionExpand.js';
import RecursionTree from './RecursionTree.js';

const fib = R((n) => (n <= 1) ? 1 : fib(n-1) + fib(n-2));
const fibTailR = R((n, a, b) => (n === 0) ? b : fibTailR(n-1, b, a+b));
const fibTailRR = (n, a=0, b=1) => fibTailR(n, a, b);
const fibMemoC = new Map();
const fibMemo = R((n) => (n <= 1) ? 1 : fibMemoC.has(n) ? fibMemoC.get(n) : fibMemoC.set(n, fibMemo(n-1)+fibMemo(n-2)).get(n));
const fac = R((n) => (n <= 1) ? 1 : n*fac(n-1));
const merge = R((...list) => {
  if (list.length < 2) return list;
  const left = merge(...list.slice(0, list.length/2));
  const right = merge(...list.slice(list.length/2));
  let i = 0;
  let j = 0;
  let r = [];
  while (i < left.length || j < right.length) {
    if (j === right.length || (i < left.length && left[i] <= right[j])) { r.push(left[i++]); }
    else { r.push(right[j++]); }
  }
  return r;
});

const functions = [
  { name: "Factorial", func: fac, funcN: "fac" },
  { name: "Fibonacci", func: fib, funcN: "fib" },
  { name: "Fibonacci (tail recursive)", func: fibTailR, funcN: "fibTailR", funcR: fibTailRR },
  { name: "Fibonacci (memoized)", func: fibMemo, funcN: "fibMemo" },
  { name: "Mergesort", func: merge, funcN: "merge" },
];

export default function RecursionVisualizer() {
  const [func, setFunc] = useState(0);
  const [params, setParams] = useState([0]);
  const [tree, setTree] = useState(null);

  useEffect(() => {
    setTree(
      (functions[func].funcR ?? functions[func].func)(...params)
    );
  }, [func, params]);

  return (
    <div>
      Function: <select onChange={(e) => setFunc(Number(e.target.value))}>
        {functions.map((f, i) => <option key={i} value={i}>{f.name}</option>)}
      </select>
      <div>
        <blockquote style={{
          fontFamily: 'monospace',
          backgroundColor: 'lightgrey',
          width: 'fit-content',
          padding: '.5em',
          margin: '.5em',
        }}>
          <strong>{functions[func].funcN}</strong> := {functions[func].func.toString}
        </blockquote>
        Parameters: <input defaultValue="0" type="text" onChange={(e) => {
          const str = e.target.value;
          const p = str.split(' ').filter(x => x !== "").map(Number);
          setParams(p.length > 0 ? p : [0]);
        }}/>
        <div>
          {(tree != null) ? <RecursionTree tree={tree} /> : <div className="alert alert-danger w-25">Recursion limit reached</div>}
        </div>
      </div>
    </div>
  );
};
