import React from "react";
import LeafTopic from "../LeafTopic";
import { useState} from "react";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 8.2  Bellman-Ford Algorithm
function BellmanFord() {
    const [distanceArray, setDistanceArray] = useState(new Array(5).fill(''));
    const [message, setMessage] = useState('');
    const checkSolution = () => {
    const correctDistanceArray = [0, 4, 7, 3, 11];
    
    const isCorrect = distanceArray.every((value, index) => parseInt(value, 10) === correctDistanceArray[index]);
    
    if (isCorrect) {
      setMessage('Correct solution!');
    } else {
      setMessage('Incorrect solution. Try again!');
    }
   };
    const graph = {
        '0': {  '3': 3 },
        '1': { '2': 5, '3': 1 },
        '2': { '1': 5, '3': 4 },
        '3': { '0': 3, '1': 1, '2': 4, '4': 8 },
        '4': {  '3': 8 },
    };
    const nodesPosition = {
    '0': { x: 50, y: 50 },
    '1': { x: 150, y: 100 },
    '2': { x: 250, y: 50 },
    '3': { x: 150, y: 200 },
    '4': { x: 350, y: 150 },
    };
    const edgesList = Object.keys(graph).flatMap(node => 
        Object.keys(graph[node]).map(neighbor => `${node}${neighbor}`)
    );
    const renderEdges = () => {
        return edgesList.map(edge => {
        const node = edge[0];
        const neighbor = edge[1];
        const weight = graph[node][neighbor];
        const startX = nodesPosition[node].x;
        const startY = nodesPosition[node].y;
        const endX = nodesPosition[neighbor].x;
        const endY = nodesPosition[neighbor].y;
        const color = 'black';
        

        return (
            <g key={`${node}-${neighbor}`} style={{ cursor: 'pointer' }}>
            <line x1={startX} y1={startY} x2={endX} y2={endY} style={{ stroke: color, strokeWidth: '2' }} />
            <text x={(startX + endX) / 2} y={(startY + endY) / 2} textAnchor="middle" dy="5" fill="black">
                {weight}
            </text>
            </g>
        );
        });

    };
    
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 8.2 Bellman-Ford Algorithm </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>
                <p>
                    One of the requirements of the graph for Dijkstra's algorithm is that it must have positive weights. The Bellman-Ford 
                    algorithm improves Dijkstra's algorithm by handling negative weight cycles. The implementation of the Bellman-Ford algorithm is shown below.
                </p>
                <p>
                    Algorithm 1. Bellman-Ford algorithm.
                </p>
                <BlockMath math="
              \begin{align*}
                  & \textbf{procedure }\textit{BellmanFord}(G:\text{ weighted connected simple graph})\\
                  & \{G\text{ has vertices }a=v_0,v_1,\dots,v_n=z\text{ and lengths }w(v_i,v_j) \\
                  & \quad \text{where }w(v_i,v_j)=\infty \text{ if }\{v_i,v_j\}\text{ is not an edge in }G\}\\
                  & \textbf{for }i:=1\textbf{ to }n\\
                  & \quad L(v_i):=\infty \\
                  & L(a):= 0 \\
                  & S:=\emptyset \\
                  & \{\text{the labels are now initialized so that the label of }a\text{ is }0\text{ and all}\}\\
                  & \quad \text{other labels are }\infty\text{, and }S\text{ is the empty set}\\
                  & \textbf{while }z\not\in S \\
                  & \quad u:=a \text{ vertex not in }S\text{ with }L(u)\text{ minimal}\\
                  & \quad S:=S\cup\{u\} \\
                  & \quad \textbf{for }\text{all vertices }v\text{ not in }S \\
                  & \quad \quad \textbf{if }L(u)+w(u,v)<L(v)\textbf{ then }L(v):= L(u)+w(u,v)\\
                  & \quad \quad \{\text{this adds a vertex to }S\text{ with minimal label and updates the}\\
                  & \quad \quad \text{labels of vertices not in }S\}\\
                  & \textbf{for }u\in V \\
                  & \quad \textbf{for }v\in N(u) \\
                  & \quad \quad \textbf{if }L(u)+w(u,v)<L(v) \textbf{ then} \\
                  & \quad \quad \quad \textit{print}(\text{``Negative weight cycle detected.''}) \\
                  & \quad \quad \quad \textbf{return }-1 \\
                  & \textbf{return }L(z) \ \{L(z)=\text{length of a shortest path from }a\text{ to }z\}
              \end{align*}
              "/>
            <p> Note that the implementation is almost the same as Dijkstra's algorithm except for the nested for loop we 
                added to detect negative weight cycles. By adding this, Bellman-Ford's algorithm is slower than Dijkstra's algorithm.
            </p>
            </h5>
            
            <div style={{ textAlign: 'center', marginTop: "5px", marginBottom: "5px"}}>
                <h4>Fill up the distance array (L) from left to right for computing shortest path from 0 using Bellman-Ford</h4>
                <svg width="400" height="250" style={{ border: '1px solid black' }}>
                    {renderEdges()}
                    {Object.keys(nodesPosition).map((node) => (
                    <circle key={node} cx={nodesPosition[node].x} cy={nodesPosition[node].y} r="15" fill="lightblue" />
                    ))}
                    {Object.keys(nodesPosition).map((node) => (
                    <text key={`text-${node}`} x={nodesPosition[node].x} y={nodesPosition[node].y} textAnchor="middle" dy="5" fill="black">
                        {node}
                    </text>
                    ))}
                </svg>
                
            </div>
                <div className="distance-array">
                    {distanceArray.map((value, index) => (
                    <input 
                        key={index} 
                        type="number" 
                        value={value || ''} 
                        onChange={(e) => {
                        const newArr = [...distanceArray];
                        newArr[index] = e.target.value;
                        setDistanceArray(newArr);
                        }} 
                    />
                    ))}
                </div>
                
                <button onClick={checkSolution}>Check Solution</button>

                <div className="message">
                    <h3>Message</h3>
                    <div>{message}</div>
                </div>
            </div>
        </LeafTopic>
    );
}

export default BellmanFord;
