import React from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 3.2 Equivalence Relations 
function EquivRel() {
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 3.2 Equivalence Relations </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>
                <p>
                    In this lesson, we will study relations with a particular combination of properties that allows them to be used to 
                    relate objects that are similar in some way.
                </p>
                <p>
                    Definition 1. A relation on a set <InlineMath>A</InlineMath> is called an equivalence relation if it is reflexive, symmetric, and transitive.
                </p>
                <p>
                    Definition 2. Two elements <InlineMath>a</InlineMath> and <InlineMath>b</InlineMath> that are related by an equivalence relations are 
                    called equivalent. The notation <InlineMath>a\sim b</InlineMath> is often used to denote that <InlineMath>a</InlineMath> and <InlineMath>b</InlineMath> are 
                    equivalent elements with respect to a particular equivalence relation.
                </p>
                <p>
                    Example: The relation 
                </p>
                <BlockMath math="R=\{(a,b)|a\equiv b\pmod{m}\}"/>
                <p>
                    is an equivalence relation on the set of integers.
                </p>
                <p>
                    Proof. Refer to Lesson 4.1 Modular Arithmetic if you are unfamiliar with the congruence relation. 
                    Let <InlineMath>a\in\Z</InlineMath>. Then <InlineMath>m\mid (a-a)</InlineMath>. Thus, 
                </p>
                <BlockMath math="a\equiv a \pmod{m}"/>
                <p>
                    Let <InlineMath>a,b\in\Z</InlineMath>. Then we have
                </p>
                <BlockMath math="a\equiv b \pmod{m} \Longrightarrow a-b=mk,\ k\in\Z \Longrightarrow b-a=-mk \Longrightarrow b
                \equiv a \pmod{m}"/>
                <p>
                    Let <InlineMath>a,b,c\in\Z</InlineMath>. Then we have
                </p>
                <BlockMath math="a\equiv b \pmod{m}\text{ and } b\equiv c \pmod{m}\Longrightarrow a-b=km,\ b-c=k'm,\ k,k'\in \Z
                \Longrightarrow a-c=(k+k')m\Longrightarrow a\equiv c \pmod{m}"/>
                <p>We have shown that the congruence relation is reflexive, symmetric, and transitive, respectively. Thus, the congruence 
                    relation is an equivalence relation on the set of integers.
                </p>
                <p>
                    Now, we will introduce the concept of equivalence classes. 
                </p>
                <p>
                    Definition 3. Let <InlineMath>R</InlineMath> be an equivalence relation on <InlineMath>A</InlineMath>. The set of 
                    all elements that are related to an element <InlineMath>a</InlineMath> of <InlineMath>A</InlineMath> is called the 
                    equivalence class of <InlineMath>a</InlineMath>. The equivalence class of <InlineMath>a</InlineMath> with respect to 
                    <InlineMath>R</InlineMath> is denoted by <InlineMath>[a]_R</InlineMath>. When only one relation is under consideration, 
                    we can delete the subscript <InlineMath>R</InlineMath> and write <InlineMath>[a]</InlineMath> for this equivalence class.
                </p>
                <p>
                    If <InlineMath>R</InlineMath> is an equivalence relation on <InlineMath>A</InlineMath>, equivalence classes can be 
                    used to partition the set <InlineMath>A</InlineMath>.
                </p>
                <p>
                    Theorem 1. Let <InlineMath>R</InlineMath> be an equivalence relation on <InlineMath>A</InlineMath>. Let <InlineMath>a,b\in A</InlineMath>. The 
                    following are equivalent:
                </p>
                <BlockMath math="(i)\ \ a\ R\ b\quad (ii)\ \ [a]=[b]\quad (iii)\ \ [a]\cap[b]\neq \emptyset"/>
                <p>
                    Proof. To prove statements <InlineMath>(i),\ (ii),\ (iii)</InlineMath> are equivalent, it is sufficient to show <InlineMath>(i)\Longrightarrow(ii)</InlineMath>, <InlineMath>(ii)\Longrightarrow(iii)</InlineMath>, and <InlineMath>(iii)\Longrightarrow(i)</InlineMath>. Suppose 
                    that <InlineMath>a\ R\ b</InlineMath>. If <InlineMath>c \ R \ a</InlineMath>, then <InlineMath>a \ R \ b</InlineMath> implies <InlineMath>c \ R \ b</InlineMath>. Thus, <InlineMath>[a]\subset[b]</InlineMath>. Similarly, we can 
                    show <InlineMath>[b]\subset[a]</InlineMath>. Therefore, <InlineMath>(i)</InlineMath> implies <InlineMath>(ii)</InlineMath>. 
                </p>
                <p>
                    Now suppose <InlineMath>[a]=[b]</InlineMath>. Then obviously <InlineMath>[a]\cap[b]\neq\emptyset</InlineMath>. Thus, <InlineMath>(ii)</InlineMath> implies 
                    <InlineMath>(iii)</InlineMath>.
                </p>
                <p>
                    Suppose <InlineMath>[a]\cap[b]\neq\emptyset</InlineMath>. Let <InlineMath>c \in[a]\cap[b]</InlineMath>. Then <InlineMath>c \ R \ a \wedge c \ R \ b \Longrightarrow a \ R \ c \wedge c \ R \ b \Longrightarrow a \ R \ b</InlineMath>. Hence, 
                    <InlineMath>(iii)</InlineMath> implies <InlineMath>(i)</InlineMath>.
                </p>
                <p>
                    Theorem 2. Let <InlineMath>R</InlineMath> be an equivalence relation on a set <InlineMath>S</InlineMath>. Then the 
                    equivalence classes of <InlineMath>R</InlineMath> form a partition of <InlineMath>S</InlineMath>. Conversely, given a 
                    partition 
                </p>
                <BlockMath math="\{A_i \mid i\in I\}"/>
                <p>   
                    of the set <InlineMath>S</InlineMath>, there is an equivalence relation <InlineMath>R</InlineMath> that has 
                    the set <InlineMath>A_i,\ i\in I</InlineMath>, as its equivalence classes.
                </p>
            </h5>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/NumberTheory/mod">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
        </LeafTopic>
    );
}

export default EquivRel;
