import s from "./TreeRenderer.module.css";
import { BSTree, RBTree, BTree } from "./TreeInteractive.js";

function BSTreeRenderNode({tree, par, ind, node, rerender}) {
  if (node == null) {
    return <li><button className={s.black}
    onClick={(rerender != null) ? ((e) => {
      const n = new BSTree.Node(0);
      if (par == null) {
        tree.root = n;
      } else {
        par.child[ind] = new BSTree.Node(0);
      }
      rerender();
    }) : null}>NULL</button></li>;
  }
  return <li><button className={node.visited && s.green}
  onContextMenu={(rerender != null) ? ((e) => {
    e.preventDefault();
    if (node.child.some(c => c != null)) return;
    if (par == null) {
      tree.root = null;
    } else {
      par.child[ind] = null;
    }
    rerender();
  }) : null}
  onClick={(rerender != null) ? ((e) => {
    const val = Number(prompt("Enter new value:"));
    node.val = val;
    rerender();
  }) : null}>{node.val}</button><ul>{
    node.child.map((n, i) => <BSTreeRenderNode tree={tree} par={node} ind={i} node={n} rerender={rerender} />)
  }</ul></li>;
};
export function BSTreeRender({tree, rerender}) {
  return <div className={s.tree}>
    <ul>
      <BSTreeRenderNode tree={tree} par={null} node={tree.root} rerender={rerender} />
    </ul>
  </div>;
};

function RBTreeRenderNode({tree, par, ind, node, rerender}) {
  if (node == null) return <li><button className={s.black}
  onClick={(rerender != null) ? (() => {
      const n = new RBTree.Node(0);
      if (par == null) {
        tree.root = n;
      } else {
        par.child[ind] = new RBTree.Node(0);
      }
      rerender();
  }) : null}>NULL</button></li>;
  return <li><button className={(node.visited) ? s.green : (node.color === 1) ? s.red : null}
  onContextMenu={(rerender != null) ? ((e) => {
    e.preventDefault();
    if (node.child.some(c => c != null)) return;
    if (par == null) {
      tree.root = null;
    } else {
      par.child[ind] = null;
    }
    rerender();
  }) : null}
  onClick={(rerender != null) ? ((e) => {
    const response = prompt("Enter new value and color, e.g. 9 B:");
    if (response == null) return;
    const valcolor = response.split(" ");
    const val = Number(valcolor[0]);
    node.val = val;
    if (valcolor.length >= 2) {
      const color = (valcolor[1] === 'R') ? 1 : 0;
      node.color = color;
    }
    rerender();
  }) : null}>{node.val}</button><ul>{
    node.child.map((n, i) => <RBTreeRenderNode tree={tree} par={node} ind={i} node={n} rerender={rerender} />)
  }</ul></li>;
};
export function RBTreeRender({tree, rerender}) {
  return <div className={s.tree}>
    <ul>
      <RBTreeRenderNode tree={tree} par={null} node={tree.root} rerender={rerender} />
    </ul>
  </div>;
};

function addLayerR(n) {
  if (n.child.length !== 0) {
    for (const c of n.child) addLayerR(c);
  } else {
    for (let i = 0; i < n.nodes.length+1; i++) {
      const nn = new BTree.Node();
      nn.nodes = [0];
      n.child.push(nn);
    }
  }
}
function addLayer(tree) {
  addLayerR(tree.root);
}
function BTreeRenderNode({tree, par, ind, node, rerender}) {
  if (node == null || node.nodes == null) return <></>;
  if (node.nodes.length === 0) return <li><button className={s.black}>EMPTY</button></li>;
  return <li><button className={node.visited && s.green}
  onContextMenu={(rerender != null) ? ((e) => {
    e.preventDefault();
    if (node.child.length === 0) {
      // right click on child node == increase depth of tree
      addLayer(tree);
      rerender();
    }
  }) : null}
  onClick={(rerender != null) ? ((e) => {
    // prompt for space delimited list of numbers
    const nums = prompt("Enter a space-delimited list of numbers:");
    if (nums == null) return;
    node.nodes = nums.split(" ").filter(s => s !== "").map(Number);
    // number of children will be automatically adjusted based on number of nodes
    if (node.child.length !== 0) {
      while (node.child.length > node.nodes.length+1) {
        node.child.pop();
      }
      while (node.child.length <= node.nodes.length) {
        const n = new BTree.Node();
        n.nodes = [0];
        node.child.push(n);
      }
    }
    rerender();
  }) : null}>{node.nodes.join(',')}</button>{(node.child.length > 0) && <ul>{
    node.child.map((n, i) => <BTreeRenderNode tree={tree} par={node} ind={i} node={n} rerender={rerender} />)
  }</ul>}</li>;
};
export function BTreeRender({tree, rerender}) {
  return <div className={s.tree}>
    <ul>
      <BTreeRenderNode tree={tree} par={null} node={tree.root} rerender={rerender} />
    </ul>
  </div>;
};
