import React, { useRef, useState, useEffect } from 'react';
//import styles from "./styles/whiteboard.module.css"
import styles from "./styles/Whiteboard.module.css";

import AlgorithmButtons from './AlgorithmButtons.js';

function Whiteboard() {
  const canvasRef = useRef(null);  //use ref to ensure that changes in canvasRef.current do not re-render the component
  const [data, setData] = useState(null);
  const algoState = useRef({
    alg: null,
    reset: null,
  });

  const [toolMode, setToolMode] = useState('pencil'); //pencil or eraser tool
  const [context, setContext] = useState(null); // the canvas context
  const [pencilColor, setPencilColor] = useState('000000'); // Default color is black
  const [isMouseDown, setIsMouseDown] = useState(false);
  const [stack, setStack] = useState([]);
  const [stringToPush, setStringToPush] = useState('');

  const MAX_STACK_SIZE = 19;

  // const MAX_EDGE_VALUE = 999999;

  // Function to initialize context once canvas is mounted
  const initContext = () => {
    if (canvasRef.current) {
      const canvas = canvasRef.current;
      const ctx = canvas.getContext('2d');
      setContext(ctx);
    }
  };

  //helper function to set the color of the canvas' context
  const setContextStrokeStyle = (context, hexColor) => {
    const r = parseInt(hexColor.substring(0, 2), 16);
    const g = parseInt(hexColor.substring(2, 4), 16);
    const b = parseInt(hexColor.substring(4, 6), 16);
    context.strokeStyle = `rgb(${r}, ${g}, ${b})`;
  };

  useEffect(() => {
    drawStack();
  }, [stack, context]);

  //Handle when a hex value is entered into the hex changer box
  const handleColorChange = (event) => {
    if (event.key === 'Enter') { //TODO: still finicky, only works when enter is pressed
      const hexColor = event.target.value.trim(); // Remove whitespace
      if (/^[A-Fa-f0-9]{6}$/.test(hexColor)) { //regex to test if hexColor is valid
        if (context) {
          setContextStrokeStyle(context, hexColor)
        }
        setPencilColor(hexColor);
      } else { //Invalid
        event.target.value = '';
        event.target.placeholder = 'Invalid Hex';
      }
    }
  };

  const drawStack = () => {
    if (context) {
      // Clear the canvas
      context.clearRect(0, 0, canvasRef.current.width, canvasRef.current.height);
      context.fillStyle = '#AAA'; 
      context.font = '14px Arial'; 
  
      let x = canvasRef.current.width / 3;
      let y = canvasRef.current.height - 50; 
      
      stack.forEach((value, index) => {
        context.fillRect(x, y - index * 30, 100, 20); 
  
        // Draw the value inside the rectangle
        context.fillStyle = '#000';
        context.fillText(value, x + 10, y - index * 30 + 15); // Adjust position for text
        context.fillStyle = '#AAA';
      });
    }
  };

  //Handle when the string to push into the stack changes 
  const handlePushValueChange = (event) => {
    if (event.key === 'Enter') { 
      //const hexColor = event.target.value.trim();
      let value = event.target.value;
      //if (/^[A-Fa-f0-9]{6}$/.test(hexColor)) { //regex to test if hexColor is valid
      if (value.length < 10) {
        setStringToPush(value);
        console.log("set string to push to " + value);
      } else { //Invalid
        event.target.value = '';
        event.target.placeholder = 'Input is too long';
      }
    }
  };

  const handleMouseDown = (event) => {
    if (toolMode === 'handling') return;
    console.log("handleMouseDown triggered");
    setIsMouseDown(true);
    if (context) {
      const canvasCoordinates = getCanvasCoordinates(canvasRef.current, event);
      const canvasX = canvasCoordinates.x;
      const canvasY = canvasCoordinates.y;
      context.strokeStyle = pencilColor;
      context.beginPath();
      context.moveTo(canvasX, canvasY);
    }
  };

  const handleMouseMove = (event) => {
    if (toolMode === 'handling') return;
    if (!isMouseDown) return;

    const canvasCoordinates = getCanvasCoordinates(canvasRef.current, event);
    const canvasX = canvasCoordinates.x;
    const canvasY = canvasCoordinates.y;

    if (toolMode === 'pencil') {
      if (context) {
        context.lineTo(canvasX, canvasY);
        context.stroke();
      }
    } else if (toolMode === 'eraser') {
      if (context) {
        const eraserSize = 10;
        context.clearRect(
          canvasX - eraserSize / 2,
          canvasY - eraserSize / 2,
          eraserSize,
          eraserSize
        );
      }
    }
  };

  const handleMouseUp = () => {
    if (toolMode === 'handling') return;
    console.log("Handle mouse up triggered");
    setIsMouseDown(false);
  };

  const handleClearCanvas = () => {
    if (context) {
      context.clearRect(0, 0, canvasRef.current.width, canvasRef.current.height);
      algoState.current.reset(setData);
      setStack([]);
    }
  };

  const handleToolChange = (mode) => {
    canvasRef.current.onmousedown = null;
    setToolMode(mode); // Update tool mode state
  };

  const handlePush = () => {
    if (stack.length < MAX_STACK_SIZE) { //19 
      console.log("push " + stringToPush);
      if (stringToPush !== '') {
        setStack([...stack, stringToPush]);
      }
    }
    else {
      alert("Max stack size (19) reached");
    }
  };
  
  const handlePop = () => {
    console.log("pop");
    if (stack.length > 0) {
      const newStack = [...stack];
      newStack.pop();
      setStack(newStack);
    }
  };

  function getCanvasCoordinates(canvas, event) {
    if (canvas) {
      const rect = canvas.getBoundingClientRect();
      const scaleX = canvas.width / rect.width;
      const scaleY = canvas.height / rect.height;
      const offsetX = rect.left;
      const offsetY = rect.top;

      const clientX = event.clientX;
      const clientY = event.clientY;

      const canvasX = (clientX - offsetX) * scaleX;
      const canvasY = (clientY - offsetY) * scaleY;

      return { x: canvasX, y: canvasY };
    }
  }


  // function handleVertexClick(event, id) {
  //   event.stopPropagation();
  //   console.log("handle vertex click triggered");
  //   const curVertex = getVertexFromId(id);

  //   if (toolMode === 'select') {
  //     setSelectAll(true);
  //   }
  //   else if (toolMode === 'eraser') {
  //     console.log('remove vertex');
  //     removeVertex(id);
  //   }
  //   else if (toolMode === 'edge1') {
  //     setFirstEndpoint(curVertex); //selected first endpoint for the edge
  //     setToolMode('edge2');
  //   }
  //   else if (toolMode === 'edge2') {
  //     if (firstEndpoint.id !== curVertex.id) {
  //       createEdge(firstEndpoint, curVertex);
  //     }
  //     else {
  //       console.log("This is a loop; could not be added");
  //     }
  //     setFirstEndpoint(null);
  //     setToolMode('edge1'); //reset to edge1
  //   }
  // }

  // function handleEdgeClick(item, edge) {
  //   console.log("edge clicked");
  //   if (toolMode === 'select') {
  //     const newValue = prompt("Enter a new value for this edge: ");
  //     if (newValue <= MAX_EDGE_VALUE) {
  //       updateEdgeValue(newValue, item, edge);
  //     }
  //   }
  //   if (toolMode === 'eraser') {
  //     console.log('remove edge');
  //     removeEdge(item, edge);
  //   }
  // }

  // function removeVertex(id) {
  //   setAdjList(prevAdjList => {
  //     const newAdjList = prevAdjList.filter(item => item.vertex.id !== id);
  //     // Remove edges ending at the vertex with the specified ID
  //     newAdjList.forEach(item => {
  //       item.edges = item.edges.filter(edge => edge.vertex.id !== id);
  //     });
  //     return newAdjList;
  //   });

  //   setUsedLetters((prevUsedLetters) => {
  //     const letterToRemove = adjList.find(item => item.vertex.id === id)?.vertex.letter;
  //     if (letterToRemove) {
  //       return { ...prevUsedLetters, [letterToRemove]: false };
  //     }
  //     return prevUsedLetters;
  //   });

  //   setVertexCount(vertexCount - 1);
  // }

  // function updateEdgeValue(newValue, item, edge) {
  //   const newAdjList = adjList.map(adjItem => {
  //     if (adjItem.vertex.id === item.vertex.id) {
  //       // Find the edge in the edges array and update its value
  //       const newEdges = adjItem.edges.map(e => {
  //         if (e.vertex.id === edge.vertex.id) {
  //           return { ...e, value: newValue }; // Update the value
  //         }
  //         return e;
  //       });
  //       return { ...adjItem, edges: newEdges }; // Update the edges array
  //     }
  //     return adjItem;
  //   });
  //   setAdjList(newAdjList);
  // }
  // function removeEdge(item, edge) {
  //   const newAdjList = [...adjList];
  //   const endpoint1Id = item.vertex.id;
  //   const endpoint2Id = edge.vertex.id;
  //   newAdjList.forEach((item) => {
  //     item.edges = item.edges.filter((edge) => ( item.vertex.id !== endpoint1Id || edge.vertex.id !== endpoint2Id));
  //   });
  //   setAdjList(newAdjList);
  // }

  // Initialize context when the component mounts
  useEffect(() => {
    initContext();
  }, []); // Run only once when component mounts

  // useEffect(() => {
  //   console.log(adjList);
  // }, [adjList]);

  useEffect(() => {
    console.log("toolMode changed to " + toolMode);
  }, [toolMode]);

  return (
      <div className={styles.whiteboardContainer}>
        <div id="data-structure"></div>
        <div>
          <canvas
            ref={canvasRef}
            className={styles.whiteboard}
            width="1600"
            height="600"
            onMouseDown={handleMouseDown}
            onMouseMove={handleMouseMove}
            onMouseUp={handleMouseUp}
          >
          </canvas>
        </div>

        <div className={styles.stack}>
          {stack.map((value, index) => (
            <div key={index} className={styles.stackElement}>
              {value}
            </div>
          ))}
        </div>

        <Toolbar
          onToolChange={handleToolChange}
          onClearCanvas={handleClearCanvas}
          onColorChange={handleColorChange}
          onPushValueChange={handlePushValueChange}
          onPush={handlePush}
          onPop={handlePop}
          toolMode={toolMode}
          algoState={algoState}
          data={data}
          setData={setData}
          canvasRef={canvasRef}
        />
          {/* only rendered when graph is selected */}
          {/*selectAll && <Infobar adjList={adjList} />*/}
      </div>
  );
}

function Toolbar({ onToolChange, onClearCanvas, onColorChange, onPush, onPop, onPushValueChange, toolMode, algoState, data, setData, canvasRef }) {
  const state = algoState.current;

  return (
    <div className={styles.toolbar}>
      <button onClick={() => onToolChange('pencil')}>
        <img src="/pencil.png" alt="Pencil" />
      </button>
      <button onClick={() => onToolChange('eraser')}>
        <img src="/eraser.png" alt="Eraser"/>
      </button>
      <button onClick={onClearCanvas}>
        <img src="/clear.png" alt="Clear" />
      </button>
      <button onClick={() => onToolChange('select')}>
        <img src="/select.png" alt="Select"/>
      </button>

      {/* Input for color change */}
    <input type="text" placeholder="Enter RGB (Press Enter)" onKeyDown={onColorChange} />

    <div className="additional-buttons">
      <button onClick={() => onPush()}>
        Push
      </button> 
      <button onClick={() => onPop()}>
        Pop
      </button>
      <input type="text" placeholder="Enter value to push/pop" onKeyDown={onPushValueChange}/>
    </div>

      <div>
        <AlgorithmButtons state={state} data={data} setData={setData} canvas={canvasRef} setMode={onToolChange} mode={toolMode} />
      </div>
    </div>
  );
}

export default Whiteboard;