import { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import axios from "axios";
import "./styles/Bio.css"

function Bio() {
  const [info, setInfo] = useState("")
  const [file, setFile] = useState()
  const [editing, setEditing] = useState(false);
  const [changed, setChanged] = useState(false);
  const [tracking, setTracking] = useState(true);

  const seconds = localStorage.getItem('timeSpent');
  const { days, hours, minutes, remainingSeconds } = {
  days: Math.floor(seconds / (3600 * 24)),
  hours: Math.floor((seconds % (3600 * 24)) / 3600),
  minutes: Math.floor((seconds % 3600) / 60),
  remainingSeconds: seconds % 60,
};

  const handleChange = event => {
    setInfo(event.target.value);
    setChanged(true);
    console.log("Got value", event.target.value)
  }

  function handleFileChange(e) {
    setFile(e.target.files[0]);
    setChanged(true);
  }

  const getToggleStatus = async () => {
    const config = {
      headers: {
          'Content-Type': 'application/json',
          'Authorization': `JWT ${localStorage.getItem('access')}`,
          'Accept': 'application/json'
      }
    };
    try {
      const toggleStatus = await axios.get(`${process.env.REACT_APP_API_URL}/api/get-tracking-status/`, config);
      if (toggleStatus.data.tracking_status) {
        setTracking(true);
      } else {
        setTracking(false);
      }
    } catch (err) {
      console.log(err);
    }
  };

  

  const setTrackingStatus = async () => {
    const config = {
      headers: {
        "Authorization": `JWT ${localStorage.getItem("access")}`
      }
    };
    try {
      const data = {}
      await axios.post(`${process.env.REACT_APP_API_URL}/api/toggle-tracking/`, data, config);
    } catch (err) {
      console.log(err);
    }
    getToggleStatus();
  };

  const resetProgress = async () => {
    if (window.confirm("Doing this will reset all lesson progress and is irreversible. Are you sure you want to proceed?")) {
      const config = {
        headers: {
            'Content-Type': 'application/json',
            'Authorization': `JWT ${localStorage.getItem('access')}`
        },
      };
      try {
        await axios.delete(`${process.env.REACT_APP_API_URL}/api/reset-progress/`, config);
      } catch (err) {
        console.log(err);
      }
    }
  };

  useEffect(() => {
    getToggleStatus();
  }, []);

  useEffect(() => {
    (async () => {
      try {
        const { data } = await axios.get("http://localhost:8000/api/profile/", {
          headers: { "Authorization": `JWT ${localStorage.getItem("access")}` },
        });
        setInfo(data.bio);
      } catch (e) {
        console.error(e);
      }
    })();
  }, []);

  async function updateProfile() {
    try {
      const data = new FormData();
      data.append('bio', info);
      if (file) data.append('profile_picture', file, file.name);
      console.log(data);
      await axios.post("http://localhost:8000/api/profile/", data, {
        headers: {
          "Authorization": `JWT ${localStorage.getItem("access")}`,
          "content-type": "multipart/form-data",
        },
      });
    } catch (e) {
      console.error(e);
    }
    setChanged(false);
  };

  const tracking_progress_text = () => (
    <p>Your tracking progress is currently ON.</p>
  );

  const not_tracking_progress_text = () => (
    <p>Your tracking progress is currently OFF.</p>
  );

  return (
    <div className='card mx-auto mt-4 mb-4'>
      <h2 class="card-header">
        Profile Page
      </h2>
      <div className='bio card-body'>
        <h3> Your Bio:</h3>
        {editing ? (
            <input
              type="text"
              id="message"
              name="message"
              onChange={handleChange}
              value={info}
            />
        ) : (
          <p>
            {info}
          </p>
        )}
        <div className='mb-3'>
          <button className="mt-2" onClick={() => setEditing(e => !e)}>{editing ? 'Done' : 'Edit bio'}</button>
        </div>

        <h3>Add Profile Picture:</h3>
        <input type="file" onChange={handleFileChange} />
        {file && <img src={URL.createObjectURL(file)} />}
        {changed && (
          <div className="mt-4">
            <button onClick={updateProfile}>Update profile</button>
          </div>
        )}
      </div>

      <div className='bio'>
        <div>
          {tracking ? tracking_progress_text() : not_tracking_progress_text()}
          <button className='btn btn-primary mt-2' onClick={setTrackingStatus}>
            Toggle tracking {tracking ? "off" : "on" }</button>
        </div>
        <div>
          <button className='btn btn-danger mt-4' onClick={resetProgress}>
              Reset Progress
          </button>
        </div>
        <div className='mt-4 mb-5'>
          <Link className="link" to='/bio/delete' >Delete Account</Link>
        </div>
      </div>
      <div style={{textAlign: "center"}}>
        <p>Time spent: {days} days, {hours} hours, {minutes} minutes, {remainingSeconds} seconds</p>
      </div>
      
    </div>
  );
};

export default Bio;
