import { React, useEffect } from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
//import { render } from 'react-dom';
//import { ReactLaTeX } from 'react-latex';
import axios from "axios";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';
var Latex = require("react-latex")


//Lesson 1.1 Propositional Logic
function PropLogic() {
    //const truthtable=
    const url = window.location.href;
    
    const mark_incomplete = async () => {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `JWT ${localStorage.getItem('access')}`,
                'lesson-url': `${url}`
            }
          };
          try {
            await axios.delete(`${process.env.REACT_APP_API_URL}/api/modify-page/`, config);
          } catch (err) {
            console.log(err);
          }
    };

    const add_page = async () => {
        const config = {
            headers: {
                "Authorization": `JWT ${localStorage.getItem("access")}`,
                "lesson-url": `${url}`
            }
        };
        console.log(config);
        try {
            const data = {};
            await axios.post(`${process.env.REACT_APP_API_URL}/api/modify-page/`, data, config);
        } catch (err) {
            console.log(err);
        }
    };
    useEffect(() => {
        add_page()
    }, []);

    return (
        <LeafTopic>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <button className="btn btn-danger mt-4" onClick={mark_incomplete}>
                Mark Lesson as Incomplete
            </button>
            </div>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 1.1 Propositional Logic</h1>
            </div>
            {/*TODO: Implement text-based explanation of logic, truth tables*/}
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>A proposition is a declarative sentence that is either true or false, but not both. The statements "Toronto is the capital of Canada" and "<InlineMath>1+1=2</InlineMath>" are examples of propositions.
                The statement "<InlineMath>x+1=2</InlineMath>" is not a proposition because the value of <InlineMath>x</InlineMath> is unknown. 
            </h5>
            <h5>
                Below are the truth tables for negation, conjunction, disjunction, implication, and biconditional, respectively. We assume <InlineMath>p</InlineMath> and <InlineMath>q</InlineMath> are well defined propositions.
            </h5>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            {/*<Latex>{"$$c=\\frac{b}{a}$$"}</Latex>*/}
            <Latex displayMode={true}>{`$$
            \\left|
            \\begin{array}{c|c}
            \\hline
                p & \\neg p  \\\\
            \\hline
                T & F  \\\\
            \\hline
                F & T   \\\\
            \\hline
            \\end{array}\\right|
            \\hspace*{0.5cm}
            \\left|
            \\begin{array}{c|c|c}
            \\hline
                p & q & p \\land q  \\\\
            \\hline
                T & T & T  \\\\
            \\hline
                T & F & F  \\\\
            \\hline
                F & T & F  \\\\
            \\hline
                F & F & F  \\\\
            \\hline
            \\end{array}\\right|
            \\hspace*{0.5cm}
            \\left|
            \\begin{array}{c|c|c}
            \\hline
                p & q & p \\lor q  \\\\
            \\hline
                T & T & T  \\\\
            \\hline
                T & F & T  \\\\
            \\hline
                F & T & T  \\\\
            \\hline
                F & F & F  \\\\
            \\hline
            \\end{array}\\right|
            \\hspace*{0.5cm}
            \\left|
            \\begin{array}{c|c|c}
            \\hline
                p & q & p \\rightarrow q  \\\\
            \\hline
                T & T & T  \\\\
            \\hline
                T & F & F  \\\\
            \\hline
                F & T & T  \\\\
            \\hline
                F & F & T  \\\\
            \\hline
            \\end{array}\\right|
            \\hspace*{0.5cm}
            \\left|
            \\begin{array}{c|c|c}
            \\hline
                p & q & p \\leftrightarrow q  \\\\
            \\hline
                T & T & T  \\\\
            \\hline
                T & F & F  \\\\
            \\hline
                F & T & F  \\\\
            \\hline
                F & F & T  \\\\
            \\hline
            \\end{array}\\right|
            $$`}</Latex>
            <div>
                <h5>
                <p>
                    DeMorgan's Law. Let <InlineMath>p</InlineMath> and <InlineMath>q</InlineMath> be propositions. Then
                </p>
                <BlockMath math="\neg (p \land q)\equiv \neg p \lor \neg q \quad \neg (p \lor q )\equiv \neg p \land \neg q"/>
                <p>
                    Now, we will introduce the quantifiers <InlineMath>\forall </InlineMath> and <InlineMath>\exists</InlineMath>. The 
                    first one reads "for all" and the second one reads "there exists." Given <InlineMath>P(x)</InlineMath> denotes the statement 
                    that "x is a CS major at Purdue" and <InlineMath>Q(x)</InlineMath> denotes the statement that "x attends Purdue", the statement 
                    "All CS majors at Purdue attend Purdue" is expressed by
                 </p>
                 <BlockMath math="\forall x (P(x) \Longrightarrow Q(x))"/>
                 <p>
                    Now let <InlineMath>S(x,y)</InlineMath> denote the statement that "x studies y hours per week". The statement "Some CS majors at Purdue study 5 hours per week" is 
                    expressed by
                 </p>
                 <BlockMath math="\exists x (P(x)\land S(x,5))"/>
                 <p>
                    DeMorgan's Law for Quantifers. Suppose we are given <InlineMath>P(x)</InlineMath>. Then
                 </p>
                 <BlockMath math="\neg \forall x P(x) \equiv \exists x \neg P(x)\quad \neg \exists x P(x)\equiv \forall x \neg P(x)"/>
                </h5>
            </div>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/IntroToLogic/Proofs">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
            
        </LeafTopic>
    );
}

export default PropLogic;
