import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import axios from 'axios';


const Home = ({ user, isAuthenticated }) => {
    const [urlLinks, setUrlLinks] = useState([]);

    const add_quiz = async (quiz_name, difficulty) => {
        const config = {
            headers: {
                "Authorization": `JWT ${localStorage.getItem("access")}`,
                "quiz-name": `${quiz_name}`,
                "difficulty": `${difficulty}`
            }
        };
        console.log(config);
        try {
            const data = {};
            await axios.post(`${process.env.REACT_APP_API_URL}/api/add-quiz/`, data, config);
        } catch (err) {
            console.log(err);
        }
    };

    const loggedIn = () => (
        <div>
            Welcome back, {user.username}!
            <p style={{textAlign: "center", marginTop: '20px', color: "green", fontSize: "30px"}}>
                Current lesson progress:
            </p>
        </div>
    );

    const notLoggedIn = () => (
        <div>
            Welcome to BoilerAlgos!
        </div>
    );

    const getProgress = async () => {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `JWT ${localStorage.getItem('access')}`,
                'Accept': 'application/json'
            }
        };

        let urls = [];

        try {
            const progress = await axios.get(`${process.env.REACT_APP_API_URL}/api/get-page-views/`, config);
            for (let url of progress.data) {
                urls.push(url["fields"]["page_url"])
            }
            setUrlLinks(urls.map(link => <li>{link}</li>));
        }
        catch (err) {
            console.log(err);
        }
    };

    useEffect(() => {
        add_quiz("Logic 1", "Easy");
        add_quiz("Logic 2", "Medium");
        add_quiz("Logic 3", "Hard");
        add_quiz("Induction MCQ1", "Easy");
        getProgress();
    }, []);

    return (
        <div style={{display: "flex", flexDirection: "column", justifyContent: "center"}}>
            <p style={{textAlign: "center", marginTop: '80px', color: "gold", fontSize: "30px"}}>
                {(user !== null && isAuthenticated) ? loggedIn() : notLoggedIn()}
            </p>
            
            <ul style={{margin: "auto", marginBottom: "50px", textAlign: "center"}}>
                {(user !== null && isAuthenticated) ? urlLinks : null}
            </ul>
            
        </div>
    );
};

const mapStateToProps = state => ({
    user: state.auth.user,
    isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, { })(Home);