import GraphAlg from './GraphAlg.js';

class Dijkstra extends GraphAlg {
  
  *run() {
    
    if (this.data.AL.length === 0) {
      return;
    } 
    console.log("AL");
    console.log(this.data.AL);
    //dijkstra
    let initial = this.data.vertex_id_selected;
    if (initial === -1) {
      initial = this.data.AL[0].id;
    }
    console.log("initial: " + initial);
    let dist = new Map(); //initially infinite
    let prev = new Map(); //initially NULL
    let Q = [];

    //console.log("marked edges:  " + this.data.marked_edges);

    const pushIntoPriorityQueue = (vertexId, distance) => {
      const index = Q.findIndex(([, dist]) => distance < dist);
      if (index === -1) {
        Q.push([vertexId, distance]);
      } else {
        Q.splice(index, 0, [vertexId, distance]); //Inawer
      }
    };

    dist.set(initial, 0);
    for (let i = 0; i < this.data.AL.length; i++) {
      if (this.data.AL[i].id !== initial) {
        dist.set(this.data.AL[i].id, Number.MAX_SAFE_INTEGER);
      }
      pushIntoPriorityQueue(this.data.AL[i].id, dist.get(this.data.AL[i].id));
      prev.set(this.data.AL[i].id, -1);
    }
    
    this.data.visited.push(initial);
    this.setData({...this.data});
    yield;

    while (Q.length !== 0) {
      console.log("Q: " + Q);
      let u = Q.shift()[0];
      console.log("u: " + u);
      this.data.marked.push(u); //stack
      this.setData({...this.data});
      yield;
      
      for (let j = 0; j < this.data.AL[u].edges.length; j++) {
          let outgoing_vertex = this.data.AL[u].edges[j].vertex;
          /*
          if (this.data.marked.includes(outgoing_vertex)) {
            continue;
          }
          */
          console.log("outgoing vertex: " + outgoing_vertex);
          let tempDist = dist.get(u) + this.data.AL[u].edges[j].value;
          this.data.visiting = outgoing_vertex;
          this.setData({...this.data});
          yield;
          console.log("tempDist from " + u + " to " + outgoing_vertex + " is " + tempDist);
          console.log("dist.get(outgoing_vertex) " + dist.get(outgoing_vertex));

          if (tempDist < dist.get(outgoing_vertex)) {
            console.log("ehy");
            dist.set(outgoing_vertex, tempDist);
            prev.set(outgoing_vertex, u);

            const existingIndex = Q.findIndex(([id]) => id === outgoing_vertex);
            if (existingIndex !== -1) {
              Q.splice(existingIndex, 1);
            } 
            pushIntoPriorityQueue(outgoing_vertex, tempDist);

            console.log("new Q: " + Q);
          }
      }
      if (!this.data.visited.includes(u)) {
        this.data.visited.push(u);
        this.setData({...this.data});
        yield;
      }
    }
    console.log("marked");
    console.log(this.data.marked);

    console.log("dist map");
    for (const [key, value] of dist.entries()) {
      console.log(`${key} => ${value}`);
    }
    
    console.log("prev map");
    for (const [key, value] of prev.entries()) {
      console.log(`${key} => ${value}`);
      const vertex1 = this.data.AL.find(vertex => vertex.id === value);
      const vertex2 = this.data.AL.find(vertex => vertex.id === key);
      if (vertex1 && vertex2) {
        this.data.marked_edges.push({x1: vertex1.x, y1: vertex1.y, x2: vertex2.x, y2: vertex2.y}); 
      }
      this.setData({...this.data});
      yield;
      console.log("marked edges: " + this.data.marked_edges);
    }

    let traversal_order = [];
    for (let i = 0; i < this.data.marked.length; i++) {
        traversal_order.push(String.fromCharCode(this.data.marked[i] + 65));
    }
    const distString = Array.from(dist.entries())
    .map(([key, value]) => `${String.fromCharCode(key + 65)} : ${value > Math.pow(10, 13) ? "infinity" : value}`)
    .join('\n');

    alert("Distances from " + String.fromCharCode(initial + 65) + "\n" + distString);
    
 



    /*
    console.log("Q: " + Q);

    for (let i = 0; i < this.data.AL.length; i++) {
      console.log(this.data.AL[i]);
    }
    */
    /*
    while (Q.length !== 0) {
      let min = Math.min(...numbers);
      for () {

      }
    }
    */
    return;
  }
}

export default Dijkstra;
  