import React from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 9.1 Probability Theory 
function ProbTheory() {
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 9.1 Probability Theory </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>
                <p>
                An experiment is a procedure that yields one of a given set of possible outcomes. The sample space of the experiment is 
                the set of possible outcomes. An event is a subset of the sample space. Laplace's definition of the probability of an event 
                with finitely many possible outcomes will now be stated.
                </p>
                <p>
                    Definition 1. If <InlineMath>S</InlineMath> is a finite nonempty sample space of equally likely outcomes, 
                    and <InlineMath>E</InlineMath> is an event, that is, a subset of <InlineMath>S</InlineMath>, then the 
                    probability of <InlineMath>E</InlineMath> is 
                </p>
                <BlockMath math="p(E)=\frac{|E|}{|S|}"/>
                <p>
                    Theorem 1. Let <InlineMath>E</InlineMath> be an event in a sample space <InlineMath>S</InlineMath>. The 
                    probability of the event <InlineMath>{'\\overline{E}=S-E'}</InlineMath>, the complementary event 
                    of <InlineMath>E</InlineMath>, is given by
                </p>
                <BlockMath math="p(\overline{E})=1-p(E)"/>
                <p>
                    Theorem 2. Let <InlineMath>E_1</InlineMath> and <InlineMath>E_2</InlineMath> be events in the sample space <InlineMath>S</InlineMath>. Then
                </p>
                <BlockMath math="p(E_1\cup E_2)=p(E_1)+p(E_2)-p(E_1\cap E_2)"/>
                <p>
                    Definition 2. Let <InlineMath>E</InlineMath> and <InlineMath>F</InlineMath> be events with <InlineMath>p(F)\gt 0</InlineMath>. The 
                    conditional probability of <InlineMath>E</InlineMath> given <InlineMath>F</InlineMath>, denoted by <InlineMath>p(E \mid F)</InlineMath>, is 
                    defined as
                </p>
                <BlockMath math="p(E\mid F)=\frac{p(E\cap F)}{p(F)}"/>
                <p>
                    Definition 3. The events <InlineMath>E</InlineMath> and <InlineMath>F</InlineMath> are independent if and only if <InlineMath>p(E\cap F)=p(E)p(F)</InlineMath>.
                </p>
            </h5>
            </div>
             <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/DiscreteProbability/bayes">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
        </LeafTopic>
    );
}

export default ProbTheory;
