import React from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';
var Latex = require("react-latex")

//Lesson 7.1 Pigeonhole Principle
function Pigeon() {
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 7.1 Pigeonhole Principle </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>
                <p>
                Suppose that a flock of <InlineMath>20</InlineMath> pigeons flies into a set of <InlineMath>19</InlineMath> pigeonholes 
                to roost. Because there are <InlineMath>20</InlineMath> pigeons but only <InlineMath>19</InlineMath> pigeonholes, at 
                least one of these <InlineMath>19</InlineMath> pigeonholes must have at least two pigeons in it. To see this, suppose this 
                is not true. That is, all pigeonholes have at most one pigeon. Put the first pigeon into a pigeonhole, there are <InlineMath>19</InlineMath> pigeons 
                remaining. Put the second pigeon into one of the other <InlineMath>18</InlineMath> pigeonholes so that this pigeon is not in the 
                same pigeonhole as the first pigeon. Repeat this process until there is only one pigeon left. No matter which pigeonhole we put 
                this pigeon in, there will be <InlineMath>2</InlineMath> pigeons in a single pigeonhole, a contradiction. Thus, the original statement 
                is indeed true.
                </p>
                <p>
                    Theorem 1. The Pigeonhole Principle. If <InlineMath>k</InlineMath> is a positive integer and <InlineMath>k+1</InlineMath> or 
                    more objects are placed into <InlineMath>k</InlineMath> boxes, then there is at least one box containing two or more of the objects.
                </p>
                <p>
                    The pigeonhole principle states that there must be at least two objects in the same box when there are more objects than 
                    boxes. However, even more can be said when the number of objects exceeds a multiple of the number of boxes. For instance, 
                    among any set of <InlineMath>21</InlineMath> decimal digits there must be <InlineMath>3</InlineMath> that are the same. 
                    This follows because when <InlineMath>21</InlineMath> objects are distributed into <InlineMath>10</InlineMath> boxes, one 
                    box must have more than <InlineMath>2</InlineMath> objects.
                </p>
                <p>
                    Theorem 2. The Generalized Pigeonhole Principle. If <InlineMath>N</InlineMath> objects are placed into <InlineMath>k</InlineMath> boxes, 
                    then there is at least one box containing at least <InlineMath>\lceil N/k \rceil</InlineMath> objects.
                </p>
            </h5>
            </div>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' 
            }}>
                <Link to = "/topics/Counting/animations">
                <button
                    style={{margin: "20px", fontSize: "16px"}} >
                    Animations of Applications
                </button>
                </Link>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/Counting/permucomb">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
        </LeafTopic>
    );
}

export default Pigeon;
