import React from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import { useState } from "react";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 7.1  The Cut Property and Prim's Algorithms
function PrimsAlgo() {
   const graph = {
        'A': { 'B': 2, 'D': 3 },
        'B': { 'A': 2, 'C': 5, 'D': 1 },
        'C': { 'B': 5, 'D': 4, 'E': 6 },
        'D': { 'A': 3, 'B': 1, 'C': 4, 'E': 8 },
        'E': { 'C': 6, 'D': 8 },
        };
    
  const [selectedEdges, setSelectedEdges] = useState([]);
  const [errorMessage, setErrorMessage] = useState('');
  const [currentEdgeIndex, setCurrentEdgeIndex] = useState(0);

  const correctOrder = ['AB', 'BD', 'DC',  'CE'];

  const edgesList = Object.keys(graph).flatMap(node => 
    Object.keys(graph[node]).map(neighbor => `${node}${neighbor}`)
  );

  const handleClick = (from, to) => {
    const edge = `${from}${to}`;
    const edge2 = `${to}${from}`
    if ((edge === correctOrder[currentEdgeIndex]) || (edge2 === correctOrder[currentEdgeIndex])) {
      setSelectedEdges([...selectedEdges, edge]);
      setCurrentEdgeIndex(currentEdgeIndex + 1);
      if (currentEdgeIndex == 3) {
        setErrorMessage('You completed the puzzle!');
      }
      else {
        setErrorMessage('Thats correct!');
      }
    } else {
      setErrorMessage('Error! Wrong edge selected.');
    }
  };

  const nodesPosition = {
    'A': { x: 50, y: 50 },
    'B': { x: 150, y: 100 },
    'C': { x: 250, y: 50 },
    'D': { x: 150, y: 200 },
    'E': { x: 350, y: 150 },
  };

  const renderEdges = () => {
    return edgesList.map(edge => {
      const node = edge[0];
      const neighbor = edge[1];
      const weight = graph[node][neighbor];
      const startX = nodesPosition[node].x;
      const startY = nodesPosition[node].y;
      const endX = nodesPosition[neighbor].x;
      const endY = nodesPosition[neighbor].y;
      const color = selectedEdges.includes(edge) ? 'blue' : 'black';

      return (
        <g key={`${node}-${neighbor}`} onClick={() => handleClick(node, neighbor)} style={{ cursor: 'pointer' }}>
          <line x1={startX} y1={startY} x2={endX} y2={endY} style={{ stroke: color, strokeWidth: '2' }} />
          <text x={(startX + endX) / 2} y={(startY + endY) / 2} textAnchor="middle" dy="5" fill="black">
            {weight}
          </text>
        </g>
      );
    });
  };
    return (
        <LeafTopic>
            
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 7.1 The Cut Property and Prim's Algorithm </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>
              <p>
                Definition 1. A <InlineMath>{'\\textit{minimum spanning tree}'}</InlineMath> in a connected weighted graph is a 
                spanning tree that has the smallest possible sum of weights of its edges.
              </p>
              <p>
                Prim's algorithm is an algorithm for constructing a minimum spanning tree. Begin by choosing any edge with smallest weight, putting it into the spanning tree. 
                Successively add to the tree edges of minimum weight that are incident to a vertex already in the tree, never forming a simple circuit 
                with those edges already in the tree. Stop when <InlineMath>n-1</InlineMath> edges have been added.
              </p>
              <p>
                The correctness of Prim's algorithm can be proven by the <InlineMath>{'\\textit{Cut property}'}</InlineMath>. The Cut 
                property states that if we partition the set of vertices <InlineMath>V</InlineMath> (of a connected, edge-weighted 
                undirected graph) into <InlineMath>S</InlineMath> and <InlineMath>V\setminus S</InlineMath>, then the edge <InlineMath>{'\\{u,v\\}\\in E'}</InlineMath> with 
                the minimum weight such that <InlineMath>u\in S</InlineMath> and <InlineMath>v\in V\setminus S</InlineMath> must be part of the Minimum Spanning Tree (MST).
              </p>
              <p>
                In Prim's algorithm, we repeatedly apply the Cut property. Suppose we have visited the vertices <InlineMath>V'\subset V</InlineMath>. The next edge 
                we add to the MST is the edge <InlineMath>{'\\{u,v\\}'}</InlineMath> with the minimum weight such that <InlineMath>u\in V'</InlineMath> and <InlineMath>v\in V\setminus V'</InlineMath>.
              </p>
              <p>
                Below is the implementation of Prim's algorithm.
              </p>
              <p>
                Algorithm 1. Prim's Algorithm.
              </p>
              <BlockMath math="
              \begin{align*}
                & \textbf{procedure }\textit{Prim}(G:\text{ weighted connected undirected graph with }n\text{ vertices}) \\
                & T:=\text{ a minimum-weight edge}\\
                & \textbf{for }i:=1\textbf{ to }n-2\\
                & \quad e:=\text{an edge of minimum wedge incident to a vertex in }T\text{ and not forming a}\\
                & \quad \quad \text{simple circuit in }T\text{ if added to }T\\
                & \quad T:=T\text{ with }e\text{ added}\\
                & \textbf{return }T\ \{T\text{ is a minimum spanning tree of }G\}
              \end{align*}
              "/>
            </h5>
            </div>
            <div style={{ position: 'relative'}}>
                <Link to = "/topics/MinimumSpanningTree/kruskals">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
            <div style={{ textAlign: 'center', marginTop: '30px', marginBottom: "50px" }}>
                <h1>Select edges in order of Prim's starting at A</h1>
                <svg width="400" height="250" style={{ border: '1px solid black' }}>
                    {renderEdges()}
                    {Object.keys(nodesPosition).map((node) => (
                    <circle key={node} cx={nodesPosition[node].x} cy={nodesPosition[node].y} r="15" fill="lightblue" />
                    ))}
                    {Object.keys(nodesPosition).map((node) => (
                    <text key={`text-${node}`} x={nodesPosition[node].x} y={nodesPosition[node].y} textAnchor="middle" dy="5" fill="black">
                        {node}
                    </text>
                    ))}
                </svg>
                {errorMessage && <p style={{ color: 'red' }}>{errorMessage}</p>}
            </div>
        </LeafTopic>
    );
}

export default PrimsAlgo;
