import VectorAlg from './VectorAlg.js';

class Shuffle extends VectorAlg {
  *run() {
    const len = this.vec.length;
    for (let i = len-2; i >= 0; i--) {
      const idx = Math.floor(Math.random() * (len-i)) + i;
      // if (idx == i) continue;
      this.swp(i, idx);
      yield;
    }
  }
}

export default Shuffle;
