import { useEffect } from 'react';

function Cell({ id, elem, setData }) {
  // TODO allow user to edit this
  return (
    <div key={id} style={{
      display: 'inline',
      padding: '1em',
      border: 'solid 1px black',
      backgroundColor: (elem.swap) ? 'red' : (elem.read) ? 'green' : 'white',
    }}>
      {elem.val}
    </div>
  );
}

function Vector2({ data, setData }) {
  useEffect(() => {
    setData([[], [], [0, 0], [0, 0]]);
  }, [setData]);

  useEffect(() => {
    console.info(data);
  }, [data]);

  return (
    <div style={{
      position: 'absolute',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      WebkitTransform: 'translate(-50%, -50%)',
    }}>
      <div style={{
        transform: 'translate(0, -20px)',
      }}> Algorithm 1 | Compares: {data[2][0]} Swaps: {data[2][1]} </div>
      <div>
        {data[0].map((e, i) => (
          <Cell key={i} id={i} elem={e} setData={setData} />
        ))}
      </div>
      {/* make less jank */}
      <div style={{
        transform: 'translate(0, 80px)',
      }}> Algorithm 2 | Compares: {data[3][0]} Swaps: {data[3][1]} </div>
      <div style={{
        marginTop: '100px',
        top: '20%',
      }}>
        {data[1].map((e, i) => (
          <Cell key={i} id={i} elem={e} setData={setData} />
        ))}
      </div>
    </div>
  );
}

export default Vector2;
