import { useState, useEffect, useRef, createElement } from "react";
import { BSTree, RBTree, BTree } from "./TreeInteractive.js";
import { BSTreeRender, RBTreeRender, BTreeRender } from "./TreeRenderer.js";

const nums = [...Array(10).keys()];
const modeMap = {
  "bst": {
    make: () => new BSTree(),
    render: BSTreeRender,
  },
  "rbt": {
    make: () => new RBTree(),
    render: RBTreeRender,
  },
  "bt3": {
    make: () => new BTree(3),
    render: BTreeRender,
  },
  "bt5": {
    make: () => new BTree(5),
    render: BTreeRender,
  },
};

function TreePuzzle() {
  // we will have 2 copies of the tree, one that the user modifies
  // and another to hold the correct answer
  const tree = useRef(new BSTree());
  const answ = useRef(null);
  const [array, setArray] = useState(null);
  const [mode, setMode] = useState("bst");
  const [state, setState] = useState("shuffle");
  const renderFlag = useState(false)[1];
  const rerender = () => renderFlag(v => !v);
  const [message, setMessage] = useState(null);

  // state transitions:
  // 1. shuffle the numbers
  // 1a. Insert first 9 into the tree according to mode and show to user
  // 2. Wait for user response (What is the result of inserting the final element into the tree?
  // 2a.They will be able to click nodes on the tree:
  //      (left-click @node: modify value, left-click @NULL: insert value, right-click @leaf: delete node)
  // 3. Check if user is correct
  // 3a. If user is correct, goto (4:done)
  // 3b. If user is incorrect, goto (2:wait)
  // 4. Wait for user to click next
  useEffect(() => {
    if (state === "shuffle") {
      const a = shuffle(nums);
      setArray(a);
      tree.current = modeMap[mode].make();
      for (let i = 0; i < 9; i++) {
        tree.current.insert(a[i]);
      }
      answ.current = tree.current.clone();
      answ.current.insert(a[9]);
      setMessage(null);
      setState("wait");
      rerender();
    } else if (state === "feedback") {
      if (tree.current.equal(answ.current)) {
        setMessage({ type: "success", text: "You did it!" });
        setState("done");
      } else {
        setMessage({ type: "danger", text: "Please try again..." });
        setState("wait");
      }
    }
  }, [state, tree]);

  return <div style={{
    textAlign: 'justify',
    padding: '20px',
    margin: '10px' 
  }}>
    Tree Puzzle
    <div>
      <label htmlFor="puzzle-mode">Mode:</label>
      <select name="puzzle-mode" id="puzzle-mode" onChange={(e) => {
        const m = e.target.value;
        setMode(m);
        setState("shuffle");
      }}>
        <option value="bst">Binary Search Tree</option>
        <option value="rbt">Red Black Tree</option>
        <option value="bt3">B Tree (order 3)</option>
        <option value="bt5">B Tree (order 5)</option>
      </select>
    </div>
    <div>What is the result of inserting {array?.[9]} into the following tree?</div>
    {(state === "done") ? <button className="btn btn-primary" onClick={() => setState("shuffle")}>Next</button>
      : <button className="btn btn-primary" onClick={() => setState("feedback")}>Submit</button>
    }
    {(message != null) && <div className={`alert alert-${message.type} w-25`}>{message.text}</div>}
    <div>
      {createElement(modeMap[mode].render, {tree: tree.current, rerender: rerender})}
    </div>
  </div>;
}

function shuffle(a) {
  let n = [...a];
  for (let i = 0; i < n.length; i++) {
    const ind = Math.floor(Math.random() * (n.length-i)) + i;
    const tmp = n[ind];
    n[ind] = n[i];
    n[i] = tmp;
  }
  return n;
}

export default TreePuzzle;
