import React from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 5.2  Red-black BSTs
function RedBlackTree() {
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 5.2 Red-black BSTs </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5> 
                <p>
                    An equivalent definition for a 2-3 tree is a red-black tree. It is simply a different representation of a 2-3 tree.
                </p>
                <p>
                    Red-black BSTs are BSTs that have red and black links satisfying the following three restrictions:
                </p>
                <p>
                    1. Red links lean left.
                </p>
                <p>
                    2. No node has two red links connected to it.
                </p>
                <p>
                    3. The tree has <InlineMath>{'\\textit{perfect black balance:}'}</InlineMath> every path from the root to a null link 
                    has the same number of black links - we refer to this number as the tree's black height.
                </p>
                <p>
                    There is a one-to-one correspondence between red-black BSTs defined in this way and 2-3 tree. Note that we can 
                    modify the first restriction to "Red links lean right." This would result in right-leaning red-black trees. The 
                    definition given above is for left-leaning red-black trees.
                </p>
                <p>We have provided a visualization for red-black BST insertion below.</p>
            </h5>
            </div>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' 
            }}>
                <Link to = "/topics/BalancedSearchTree/redblacktree/visualizerb">
                <button
                    style={{margin: "20px", fontSize: "16px"}} >
                    Red-Black Tree Insertion Visualization
                </button>
                </Link>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/BFSandDFS/bfs">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
        </LeafTopic>
    );
}

export default RedBlackTree;
