import React from "react";
import LeafTopic from "../LeafTopic.js";
import { Link } from "react-router-dom";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 5.2 Complexity of Algorithms 
function Complexity() {
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 5.2 Complexity of Algorithms </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h3>Big-<InlineMath>O</InlineMath> Notation</h3>
            <h5>
                <p>
                    Definition 1. Let <InlineMath>f</InlineMath> and <InlineMath>g</InlineMath> be functions from <InlineMath>\Z</InlineMath> or <InlineMath>\R</InlineMath> to <InlineMath>\R</InlineMath>. We 
                    say that <InlineMath>f(x)</InlineMath> is <InlineMath>O(g(x))</InlineMath> if there are constants <InlineMath>C</InlineMath> and <InlineMath>k</InlineMath> such that
                </p>
                <BlockMath math="
                |f(x)|\leq C|g(x)|
                "/>
                <p>
                    whenever <InlineMath>x\gt k</InlineMath>. [This is read as "<InlineMath>f(x)</InlineMath> is big-oh of <InlineMath>g(x)</InlineMath>."]
                </p>
                <p>
                    Theorem 1. Let <InlineMath>f(x)=a_nx^n+\cdots+a_1x+a_0</InlineMath>, where <InlineMath>a_0,a_1,\dots,a_n\in\R</InlineMath>. Then <InlineMath>f(x)\in O(x^n)</InlineMath>.
                </p>
                <p>
                    Note our use of the notation <InlineMath>f(x)\in O(g(x))</InlineMath>. Strictly speaking, <InlineMath>O(g(x))</InlineMath> is the set 
                    of functions such that each function is big-oh of <InlineMath>g(x)</InlineMath>. However, you will often see the notation <InlineMath>f(x)=O(g(x))</InlineMath> in 
                    practice. 
                </p>
            </h5>
            <h3>Big-Omega and Big-Theta Notation</h3>
            <h5>
                <p>
                    Definition 2. Let <InlineMath>f</InlineMath> and <InlineMath>g</InlineMath> be functions from <InlineMath>\Z</InlineMath> or <InlineMath>\R</InlineMath> to <InlineMath>\R</InlineMath>. We 
                    say that <InlineMath>f(x)</InlineMath> is <InlineMath>\Omega(g(x))</InlineMath> if there are constants <InlineMath>C</InlineMath> and <InlineMath>k</InlineMath> with <InlineMath>C</InlineMath> positive 
                    such that
                </p>
                <BlockMath math="
                |f(x)|\geq C|g(x)|
                "/>
                <p>
                    whenever <InlineMath>x\gt k</InlineMath>. [This is read as "<InlineMath>f(x)</InlineMath> is big-Omega of <InlineMath>g(x)</InlineMath>."]
                </p>
                <p>
                    Definition 3. Let <InlineMath>f</InlineMath> and <InlineMath>g</InlineMath> be functions from <InlineMath>\Z</InlineMath> or <InlineMath>\R</InlineMath> to <InlineMath>\R</InlineMath>. We 
                    say that <InlineMath>f(x)</InlineMath> is <InlineMath>\Theta(g(x))</InlineMath> if <InlineMath>f(x)</InlineMath> is <InlineMath>O(g(x))</InlineMath> and <InlineMath>f(x)</InlineMath> is <InlineMath>\Omega(g(x))</InlineMath>. When <InlineMath>f(x)</InlineMath> is <InlineMath>\Theta(g(x))</InlineMath>, we 
                    say that <InlineMath>f</InlineMath> is big-Theta of <InlineMath>g(x)</InlineMath>, that <InlineMath>f(x)</InlineMath> is of order <InlineMath>g(x)</InlineMath>, and that <InlineMath>f(x)</InlineMath> and <InlineMath>g(x)</InlineMath> are 
                    of the <InlineMath>\textit same</InlineMath> <InlineMath>order</InlineMath>.
                </p>
                <p>
                    Note that in terms of sets,
                </p>
                <BlockMath math="
                \Theta(g(x)) = O(g(x)) \cap \Omega(g(x))
                "/>
            </h5>
            <h3>Time Complexity of Algorithms</h3>
            <h5>
                <p>
                Consider the brute-force linear search algorithm in which we compare the value we want to find to every term in the list. Now suppose that 
                the list contains <InlineMath>n</InlineMath> elements. The time complexity of this algorithm is <InlineMath>O(n)</InlineMath>. The idea of 
                time complexity is to express the amount of time the algorithm requires as a function of the input size <InlineMath>n</InlineMath>. 
                </p>
                <p>
                    Now consider the binary search algorithm that we introduced in Lesson 5.1. The time complexity of the binary search 
                    algorithm is <InlineMath>O(\log n)</InlineMath>. 
                </p>
                <p>
                    If we are dealing with time complexity, we usually consider the functions <InlineMath>f</InlineMath> and <InlineMath>g</InlineMath> as functions that 
                    map <InlineMath>\N</InlineMath> to <InlineMath>\R</InlineMath>, since the input size can never be negative.
                </p>
            </h5>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/Induction/induct">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
        </LeafTopic>
    );
}

export default Complexity;
