import React, { useState } from 'react';
import { Navigate } from 'react-router-dom';
import { connect } from 'react-redux';
import { reset_password } from '../authentication/auth';


const ResetPassword = ({ reset_password, isAuthenticated }) => {
    const [requestSent, setRequestSent] = useState(false);

    const [formData, setFormData] = useState({
        email: ''
    });

    const { email } = formData;

    const onChange = e => setFormData({ 
      ...formData, 
      [e.target.name]: e.target.value });

    const onSubmit = e => {
        e.preventDefault();
        reset_password(email);
        setRequestSent(true);
    };


    if (requestSent || isAuthenticated) {
        return <Navigate to='/' />;
    }

    return (
        <div className='container mt-5'>
            <h1 style={{fontFamily: "Acumin Pro"}}>Reset Password</h1>
            <p>Enter the email of the account you wish to reset the password for: </p>
            <form onSubmit={e => onSubmit(e)}>
                <div className='form-group'>
                    <input
                        className='form-control'
                        type='email'
                        placeholder='Email'
                        name='email'
                        value={email}
                        onChange={e => onChange(e)}
                        required
                    />    
                </div>
                <button className='btn btn-primary mt-4' type='submit'>Reset Password</button>
            </form>
            
        </div>
    );
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, { reset_password })(ResetPassword);