import React from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 6.3 Recursive Algorithms
function RecAlgo() {
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 6.3 Recursive Algorithms </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>
                <p>
                    Sometimes we can reduce the solution to a problem with a particular set of input values to the solution of the same problem 
                    with smaller input values. When such a reduction can be done, the solution to the original problem can be found with a sequence 
                    of reductions, until the problem has been reduced to some initial case for which the solution is known. 
                </p>
                <p>
                    Definition 1. An algorithm is called <InlineMath>\textit recursive</InlineMath> if it solves a problem 
                    by reducing it to an instance of the same problem with smaller input.
                </p>
                <p>
                    Consider the following recursive algorithms.
                </p>
                <h4>Algorithm 1. A Recursive Algorithm for Computing <InlineMath>n!</InlineMath>.</h4>
                <BlockMath math="
                \begin{align*}
                    & \textbf{procedure }\textit{factorial}(n:\text{ nonnegative integer})\\
                    & \textbf{if }n=0\textbf{ then return }1\\
                    & \textbf{else return }n\cdot\textit{factorial}(n-1)\\
                    & \{\text{output is }n!\}
                \end{align*}
                "/>
                <br/>
                <h4>Algorithm 2. A Recursive Algorithm for Computing <InlineMath>a^n</InlineMath>.</h4>
                <BlockMath math="
                \begin{align*}
                    & \textbf{procedure }\textit{power}(a:\text{ nonnegative real number, }n:\text{ nonnegative integer})\\
                    & \textbf{if }n=0\textbf{ then return }1\\
                    & \textbf{else return }a\cdot\textit{power}(a,n-1)\\
                    & \{\text{output is }a^n\}
                \end{align*}
                "/>
                <br/>
                <h4>Algorithm 3. A Recursive Algorithm for Computing <InlineMath>\gcd(a,b)</InlineMath>.</h4>
                <BlockMath math="
                \begin{align*}
                    & \textbf{procedure }\textit{gcd}(a,b:\text{ nonnegative integers with }a<b)\\
                    & \textbf{if }a=0\textbf{ then return }b\\
                    & \textbf{else return }\textit{gcd}(b\bmod{a},a)\\
                    & \{\text{output is }\gcd(a,b)\}
                \end{align*}
                "/>
                <br/>
                <h4>Algorithm 4. A Recursive Algorithm for Fibonacci Numbers.</h4>
                <BlockMath math="
                \begin{align*}
                    & \textbf{procedure }\textit{fibonacci}(n:\text{ nonnegative integer})\\
                    & \textbf{if }n=0\textbf{ then return }0\\
                    & \textbf{if }n=1\textbf{ then return }1\\
                    & \textbf{else return }\textit{fibonacci}(n-1)+\textit{fibonacci}(n-2)\\
                    & \{\text{output is }\textit{fibonacci}(n)\}
                \end{align*}
                "/>
                <p>
                    The correctness of recursive algorithms can be proven by induction.
                </p>
            </h5>
            </div>
            <div style={{
                textAlign: "center",
                padding: '20px',
                margin: '10px'
            }}>
                <Link to="/topics/Induction/RecursionVisualizer">
                <button style={{margin: "20px", fontSize: "16px"}} >
                    Visualizer
                </button>
                </Link>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/Counting/pigeonhole">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
        </LeafTopic>
    );
}

export default RecAlgo;
