import VectorAlg from './VectorAlg';

class SelectionSort extends VectorAlg {
  *run() {
    for (let i = 0; i < this.vec.length; i++) {
      let min = i;
      for (let j = i+1; j < this.vec.length; j++) {
        const c = this.cmp(min, j);
        yield;
        if (c < 0) continue;
        min = j;
      }
      this.swp(min, i);
      yield;
    }
  }
}

export default SelectionSort;
