import React from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import { useState, useEffect } from "react";

//this button will take user to modules associated with Introductin to Logic 
function IntroToLogic() {
    const [rating, setRating] = useState(0);
    //const [communityRating, setCommunityRating] = useState(0);
    const [hoverRating, setHoverRating] = useState(0);

    const lessonID = 1;
    const [scoreSum, setScoreSum] = useState(null);
    const [scoreCount, setScoreCount] = useState(null);

    useEffect(() => {
      const config = {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `JWT ${localStorage.getItem('access')}`,
                'Accept': 'application/json'
            }
        };   

    const fetchScoreData = async () => {
      
      try {
        const response = await axios.get(`http://localhost:8000/ratelesson/${lessonID}/`, config);
        setScoreSum(response.data.scoreSum);
        setScoreCount(response.data.scoreCount);
      } catch (error) {
        console.error('Error fetching score data:', error);
      }
    };

    fetchScoreData();
  }, [lessonID]);
    
    const handleStarClick = async (starValue) => {
       const config = {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `JWT ${localStorage.getItem('access')}`,
                'Accept': 'application/json'
            }
        };

        try {
            const res = await axios.put("http://localhost:8000/ratelesson/1/", 
            {
              scoreSum: scoreSum + starValue,
              scoreCount: scoreCount + 1,

            }, config);
            console.log(res);
        } catch (e) {
            console.error(e);
        }
        
            
    setScoreSum(scoreSum + starValue);
    setScoreCount(scoreCount + 1);
    setRating(starValue);
    
    const data = {
      rating: starValue
    };
  };

  const handleStarHover = (starValue) => {
    setHoverRating(starValue);

  };

  const renderStar = (starValue) => {
    const starStyle = {
      fontSize: '24px',
      cursor: 'pointer', //to change cursor
      color: starValue <= (hoverRating || rating) ? 'gold' : 'gray',
      transition: 'color 0.3s ease'
    };
    const filled = starValue <= (hoverRating || rating) ? 'filled' : '';
    return (
      <span
        style = {starStyle}
        onClick={() => handleStarClick(starValue)}
        onMouseEnter={() => handleStarHover(starValue)}
        onMouseLeave={() => handleStarHover(0)}
      >
        &#9733; 
      </span>
    );
  };
    return (
        <div>
            <div style={{ padding: '20px', margin: '10px' }}>
            <h3>The Foundations: Logic and Proofs</h3>
            <Link to="/topics/IntroToLogic/PropLogic">Lesson 1.1 Propositional Logic</Link>
            <br/>
            <Link to="/topics/IntroToLogic/Proofs">Lesson 1.2 Proof Methods and Strategy</Link>
            </div>
            <div style={{margin: '20px', padding: "10px"}}>
                <Link to = "/topics/IntroToLogic/PropLogic/truthtable" style={{paddingBottom: "20px"}}>
                <button >
                    Interactive Truth Table
                </button>
                </Link>
                <Link to = "/topics/IntroToLogic/selectproofprobs" style={{paddingTop: "20px"}}>
                <button >
                    Proof Problems
                </button>
                </Link>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' 
              }}>
                {[1, 2, 3, 4, 5].map((value) => (
                    <React.Fragment key={value}>
                        {renderStar(value)}
                    </React.Fragment>
                ))}
                <fetchScoreData/>
                <p>Your rating: {rating}/5</p>
                <p>Community rating: {scoreSum / scoreCount} / 5</p>
            </div>
        </div>
    );
}

export default IntroToLogic;