import React from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 3.1 Relations and their Properties 
function RelProp() {
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 3.1 Relations and their Properties </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>
                <p>
                    The most direct way to express a relationship between elements of two sets is to use ordered pairs made up ot two related elements. 
                    For this reason, sets of ordered pairs are called binary relations.
                </p>
                <p>
                Definition 1. Let <InlineMath>A</InlineMath> and <InlineMath>B</InlineMath> be sets.  A binary relation from <InlineMath>A</InlineMath> to <InlineMath>B</InlineMath> is a subset of <InlineMath>A\times B</InlineMath>.
                </p>
                <p>
                    In other words, a binary relation from <InlineMath>A</InlineMath> to <InlineMath>B</InlineMath> is a set <InlineMath>R</InlineMath> of ordered pairs, where the first element of each ordered pair comes from <InlineMath>A</InlineMath> and the second element comes from <InlineMath>B</InlineMath>. 
                    We use the notation <InlineMath>a\ R \ b</InlineMath> to denote that <InlineMath>(a,b)\in R</InlineMath>. Moreover, when <InlineMath>(a,b)\in R</InlineMath>, <InlineMath>a</InlineMath> is said to be 
                    related to <InlineMath>b</InlineMath> by <InlineMath>R</InlineMath>.
                </p>
                <p>
                    Relations on a set <InlineMath>A</InlineMath> to itself are of special interest.
                </p>
                <p>
                Definition 2. A relation on a set <InlineMath>A</InlineMath> is a relation from <InlineMath>A</InlineMath> to <InlineMath>A</InlineMath>.
                </p>
                <p>
                    In other words, a relation on a set <InlineMath>A</InlineMath> is a subset of <InlineMath>A \times A</InlineMath>.
                </p>
                <p>
                    Now, we will introduce some properties of relations.
                </p>
                <p>
                    Definition 3. A relation <InlineMath>R</InlineMath> on a set <InlineMath>A</InlineMath> is called reflexive if <InlineMath>(a,a)\in R</InlineMath> for every element <InlineMath>a\in A</InlineMath>.
                </p>
                <p>
                    Definition 4. A relation <InlineMath>R</InlineMath> on a set <InlineMath>A</InlineMath> is called symmetric if <InlineMath>(b,a)\in R</InlineMath> whenever <InlineMath>(a,b)\in R</InlineMath>, 
                    for all <InlineMath>a,b\in A</InlineMath>. A relation <InlineMath>R</InlineMath> on a set <InlineMath>A</InlineMath> such that for all <InlineMath>a,b\in A</InlineMath>, if <InlineMath>(a,b)\in A</InlineMath> and <InlineMath>(b,a)\in R</InlineMath>, then <InlineMath>a=b</InlineMath> is called antisymmetric.
                </p>
                <p>
                    Definition 5. A relation <InlineMath>R</InlineMath> on a set <InlineMath>A</InlineMath> is called transitive if whenever <InlineMath>(a,b)\in R</InlineMath> and <InlineMath>(b,c)\in R</InlineMath>, then <InlineMath>(a,c)\in R</InlineMath>, for all <InlineMath>a,b,c\in A</InlineMath>.
                </p>
                <p>
                    We can extend these definition to <InlineMath>n</InlineMath>-ary relations by considering <InlineMath>n</InlineMath> sets <InlineMath>A_1,\ A_2,\dots, \ A_n</InlineMath>.
                </p>
            </h5>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/Relations/EquivRel">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
        </LeafTopic>
    );
}

export default RelProp;
