import LeafTopic from "../LeafTopic";
import React, { useRef, useState, useEffect } from 'react';
import axios from "axios";



function TruthTable() {
    const [expression, setExpression] = useState('');
    const [variableCount, setVariableCount] = useState(0);
    const [combinations, setCombinations] = useState([]);
    const [results, setResults] = useState([]);

    const MAX_VARIABLE_COUNT = 10;

    const url = window.location.href;

    const add_page = async () => {
        const config = {
            headers: {
                "Authorization": `JWT ${localStorage.getItem("access")}`,
                "lesson-url": `${url}`
            }
        };
        console.log(config);
        try {
            const data = {};
            await axios.post(`${process.env.REACT_APP_API_URL}/api/modify-page/`, data, config);
        } catch (err) {
            console.log(err);
        }
    };

    useEffect(() => {
        add_page()
    }, []);
    
    function isValidExpression(expression) {
        expression = expression.replace(/\s/g, '').toLowerCase(); //remove whitespace, and change to lower case
        const allowedSymbols = /^[a-z~|^]+$/; //variables must be single letters
        if (!allowedSymbols.test(expression)) { //test if the expression only consists of the above symbols
            console.log("Violated the allowedSymbols requirement");
            return false; 
        }
        
        for (let i = 0; i < expression.length; i++) {
            const char = expression[i];
            const prevChar = i > 0 ? expression[i - 1] : null;
            const nextChar = i < expression.length - 1 ? expression[i + 1] : null;
            if (char === '~') { //unary operator - check if the char to the right is a variable
              if (!nextChar || !nextChar.match(/[a-z]/)) {
                console.log("Unary operator - no character to the right");
                return false;
              }
              else if (prevChar && prevChar.match(/[a-z]/)) {
                console.log("Unary operator - incorrect position")
                if (nextChar && nextChar.match(/[a-z]/))
                    return false;
                else if (!nextChar)
                    return false;
              }
            } else if (char === '^' || char === '|') {
              // Binary operators should be surrounded by variables
              if (nextChar && nextChar === "~") {
                continue;
              }
              else if (!prevChar || !prevChar.match(/[a-z]/) || !nextChar || !nextChar.match(/[a-z]/)) {
                console.log("Binary operator - incorrect position");
                return false;
              }
            } else if (char.match(/[a-z]/)) { 
                if (nextChar && ((nextChar.match(/[a-z]/)))) { 
                    console.log("Consecutive variables without a binary operator in between");
                    return false;
                }
            } else {
                console.log("Unknown character");
                return false;
            }
          }
          return true;
    }
    //https://en.wikipedia.org/wiki/Shunting_yard_algorithm
    //uses Shunting Yard algorithm to convert infix to reverse polish notation
    function infixToRPN(expression) { 
        const precedence = {
            '~': 3, 
            '^': 2, 
            '|': 1, 
        };
        const outputQueue = [];
        const operatorStack = [];
        const regexPattern = /(\w+|[\^|~])/; 
        const tokens = expression.split(regexPattern); //split the expression into tokens
        const nonEmptyTokens = tokens.filter(token => token.trim() !== ''); // remove any tokens that are empty after trimming whitespace

        for (const token of nonEmptyTokens) { 
            if (/[a-z]/i.test(token)) { //operand
                outputQueue.push(token);
            } 
            else if (token in precedence) { //operator
                while (operatorStack.length > 0 && 
                    precedence[token] <= precedence[operatorStack[operatorStack.length - 1]]) {
                    outputQueue.push(operatorStack.pop());
                }
                operatorStack.push(token);
            }
        }
        // Pop any remaining operators from the stack and add them to the output queue
        while (operatorStack.length > 0) {
            outputQueue.push(operatorStack.pop());
        }
        //console.log(outputQueue);
        return outputQueue;
    }

    //based on the number of variables, get every possible permutation of values, and return a 2d array
    function generateCombinations(combinations, nVariables, list) {
        if (nVariables === 0) {
            combinations.push(list.slice());
            return list;
        }
        list.push(true);
        generateCombinations(combinations, nVariables - 1, list.slice());
        list.pop();
        list.push(false);
        generateCombinations(combinations, nVariables - 1, list.slice());
        list.pop();
    }

    function getUniqueVariables(expression) {
        const uniqueVariables = new Set();
        const variablePattern = /[a-z]/g;
        let match;
        while ((match = variablePattern.exec(expression)) !== null) {
            uniqueVariables.add(match[0]);
        }
        // Return the size of the Set, which represents the number of unique variables
        return uniqueVariables.size;
    }

    //https://en.wikipedia.org/wiki/Reverse_Polish_notation
    //uses a stack to evaluate an expression in reverse polish notation
    function evaluateExpressions(expression, combinations) { //standard way of evaluating boolean expression
        const results = [];
        for (const combination of combinations) {
            const stack = [];
            for (const char of expression) {
                if (/[a-z]/i.test(char)) { //matches a variable
                    const variableIndex = char.charCodeAt(0) - 97; // Convert variable letter to index 
                    stack.push(combination[variableIndex]);        // 'a' is 97
                } else if (char === '~') {
                    //Unary operator - pop the one below
                    const operand = stack.pop();
                    stack.push(!operand);
                } else if (char === '^') {
                    // Binary operator - perform AND operation on the two below
                    const operand2 = stack.pop();
                    const operand1 = stack.pop();
                    stack.push(operand1 && operand2);
                } else if (char === '|') {
                    // Binary operator - perform OR operation on bottom two
                    const operand2 = stack.pop();
                    const operand1 = stack.pop();
                    stack.push(operand1 || operand2);
                }
            }
            // The result of the expression is the final value remaining on the stack
            const result = stack.pop();
            results.push(result);
        }
        return results;
    }

    //run when enter is pressed in the text box
    function parseExpression(expression) {
        const ret = isValidExpression(expression);
        if (ret === false) {
            alert("Invalid input!");
            return;
        }
        const uniqueVariableCount = getUniqueVariables(expression);
        if (uniqueVariableCount > MAX_VARIABLE_COUNT) {
            alert("Number of variables cannot exceed " + MAX_VARIABLE_COUNT);
            return;
        }
        setVariableCount(uniqueVariableCount);

        console.log("valid");
        const rpnExpression = infixToRPN(expression);
        console.log("RPN: " + rpnExpression);
       
        let combinationsArr = [];
        let myArr = [];
        //console.log("var count: " + variableCount);
        generateCombinations(combinationsArr, uniqueVariableCount, myArr);
        setCombinations(combinationsArr);
        setResults(evaluateExpressions(rpnExpression, combinationsArr));
    }

    

    useEffect(() => {
        console.log("var count: " + variableCount);
    }, [variableCount]);

    useEffect(() => {
        console.log("results: ");
        console.log(results);
    }, [results]);

    useEffect(() => {
        console.log("combinations: ");
        console.log(combinations);
    }, [combinations]);

    function generateTruthTable() {
        return (
            
            <table style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' 
            }}>
                <thead>
                    <tr>
                        {combinations[0].map((_, index) => (
                            <th key={index}>{String.fromCharCode(97 + index)}</th> // Display variable names (a, b, c, ...)
                        ))}
                        <th>Result</th>
                    </tr>
                </thead>
                <tbody>
                    {combinations.map((combination, index) => (
                        <tr key={index}>
                            {combination.map((value, idx) => (
                                <td key={idx}>{String(value)}</td> // Display boolean values (true/false)
                            ))}
                            <td>{String(results[index])}</td> 
                        </tr>
                    ))}
                </tbody>
            </table>
        );
    };

    return (
        <div>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' 
            }}>
	        <h1>Interactive Truth Table</h1>
            </div>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' 
            }}>
                <label htmlFor={'my-input'}>Enter boolean expression: </label>
                <input
                    id={'my-input'}
                    type={'text'}
                    value={expression}
                    placeholder={'Type boolean expression'}
                    onChange={(event) => setExpression(event.target.value)}
                    onKeyDown={(event) => {
                        if (event.key === 'Enter') {
                            parseExpression(expression);
                        }
                    }}
                />
                <p style={{ marginBottom: '10px' }}> Currently supported operations are ~ (not), ^ (and), | (or). <br />
        As of now, variable names must be entered from a-z, without skips in the alphabet, e.g. a^b^c. <br />
        The maximum number of variables is 10 (more than enough). <br />
        As of now, parentheses are not supported. </p>
            </div>
            <div>{combinations && results && (combinations.length > 0) && (results.length > 0) && generateTruthTable()}</div>
            
        </div>
    );
}

export default TruthTable;
