import React from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import { useState } from "react";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 7.2  The Cycle Property and Kruskal's Algorithm
function KruskalAlgo() {
    const graph = {
        'A': { 'B': 2, 'D': 3 },
        'B': { 'A': 2, 'C': 5, 'D': 1 },
        'C': { 'B': 5, 'D': 4, 'E': 6 },
        'D': { 'A': 3, 'B': 1, 'C': 4, 'E': 8 },
        'E': { 'C': 6, 'D': 8 },
        };
    
  const [selectedEdges, setSelectedEdges] = useState([]);
  const [errorMessage, setErrorMessage] = useState('');
  const [currentEdgeIndex, setCurrentEdgeIndex] = useState(0);

  const correctOrder = ['BD', 'AB', 'DC',  'CE'];

  const edgesList = Object.keys(graph).flatMap(node => 
    Object.keys(graph[node]).map(neighbor => `${node}${neighbor}`)
  );

  const handleClick = (from, to) => {
    const edge = `${from}${to}`;
    const edge2 = `${to}${from}`
    if ((edge === correctOrder[currentEdgeIndex]) || (edge2 === correctOrder[currentEdgeIndex])) {
      setSelectedEdges([...selectedEdges, edge]);
      setCurrentEdgeIndex(currentEdgeIndex + 1);
      if (currentEdgeIndex == 3) {
        setErrorMessage('You completed the puzzle!');
      }
      else {
        setErrorMessage('Thats correct!');
      }
    } else {
      setErrorMessage('Error! Wrong edge selected.');
    }
  };

  const nodesPosition = {
    'A': { x: 50, y: 50 },
    'B': { x: 150, y: 100 },
    'C': { x: 250, y: 50 },
    'D': { x: 150, y: 200 },
    'E': { x: 350, y: 150 },
  };

  const renderEdges = () => {
    return edgesList.map(edge => {
      const node = edge[0];
      const neighbor = edge[1];
      const weight = graph[node][neighbor];
      const startX = nodesPosition[node].x;
      const startY = nodesPosition[node].y;
      const endX = nodesPosition[neighbor].x;
      const endY = nodesPosition[neighbor].y;
      const color = selectedEdges.includes(edge) ? 'blue' : 'black';

      return (
        <g key={`${node}-${neighbor}`} onClick={() => handleClick(node, neighbor)} style={{ cursor: 'pointer' }}>
          <line x1={startX} y1={startY} x2={endX} y2={endY} style={{ stroke: color, strokeWidth: '2' }} />
          <text x={(startX + endX) / 2} y={(startY + endY) / 2} textAnchor="middle" dy="5" fill="black">
            {weight}
          </text>
        </g>
            );
            });
        };
    return (
        <LeafTopic>
            
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 7.2 The Cycle Property and Kruskal's Algorithm </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>
              <p>
                Recall the following definition:
              </p>
              <p>
                Definition 1. A <InlineMath>{'\\textit{minimum spanning tree}'}</InlineMath> in a connected weighted graph is a 
                spanning tree that has the smallest possible sum of weights of its edges.
              </p>
              <p>
                To carry out Kruskal's algorithm, choose an edge in the graph with minimum weight. Successively add edges with 
                minimum weight that do not form a simple circuit with those edges already chosen. Stop after <InlineMath>n-1</InlineMath> edges 
                have been selected.
              </p>
              <p>
               The correctness of Kruskal's algorithm can be proven by the <InlineMath>{'\\textit{Cycle property}'}</InlineMath>. The Cycle 
               property states that if have a cycle in <InlineMath>G</InlineMath>, then the edge with the maximum weight in the 
               cycle must not be a part of the Minimum Spanning Tree (MST). 
              </p>
              <p>
                In Kruskal's algorithm, we repeatedly apply the Cycle property. Suppose we have added the edges <InlineMath>E'\subset E</InlineMath>. The 
                next edge we add is the edge in <InlineMath>E\setminus E'</InlineMath> with the minimum weight. If adding this edge creates a cycle, 
                then we do not add it. In this case, this edge must be in the edge in the cycle with the maximum weight because the previous added 
                edges all have a smaller weight.
              </p>
              <p>
                Below is the implementation of Kruskal's algorithm.
              </p>
              <p>
                Algorithm 1. Kruskal's Algorithm.
              </p>
              <BlockMath math="
              \begin{align*}
                & \textbf{procedure }\textit{Kruskal}(G:\text{ weighted connected undirected graph with }n\text{ vertices}) \\
                & T:=\text{ empty graph}\\
                & \textbf{for }i:=1\textbf{ to }n-1\\
                & \quad e:=\text{any edge in }G\text{ with smallest weight that does not form a simple circuit}\\
                & \quad \quad \text{when added to }T\\
                & \quad T:=T\text{ with }e\text{ added} \\
                & \textbf{return }T\ \{T\text{ is a minimum spanning tree of }G\}
              \end{align*}
              "/>
            </h5>
            </div>
            <div style={{ position: 'relative'}}>
                <Link to = "/topics/ShortestPath/dijkstra">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
            <div style={{ textAlign: 'center', marginTop: "30px", marginBottom: "50px"}}>
                <h1>Select edges in order of Kruskal's</h1>
                <svg width="400" height="250" style={{ border: '1px solid black' }}>
                    {renderEdges()}
                    {Object.keys(nodesPosition).map((node) => (
                    <circle key={node} cx={nodesPosition[node].x} cy={nodesPosition[node].y} r="15" fill="lightblue" />
                    ))}
                    {Object.keys(nodesPosition).map((node) => (
                    <text key={`text-${node}`} x={nodesPosition[node].x} y={nodesPosition[node].y} textAnchor="middle" dy="5" fill="black">
                        {node}
                    </text>
                    ))}
                </svg>
                {errorMessage && <p style={{ color: 'red' }}>{errorMessage}</p>}
            </div>
        </LeafTopic>
    );
}

export default KruskalAlgo;
