import React from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 8.2 Trees 
function Trees() {
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 8.2 Trees </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>
                <p>
                    A tree is a type of graph.
                </p>
                <p>
                    Definition 1. A tree is a connected undirected graph with no simple circuits.
                </p>
                <p>
                    Theorem 1. An undirected graph is a tree if and only if there is a unique simple path between any two of its vertices.
                </p>
                <p>
                    Definition 2. A rooted tree is a tree in which one vertex has been designated as the root and every edge is directed away from the root.
                </p>
                <p>
                    Definition 3. A rooted tree is called an <InlineMath>n</InlineMath>-ary tree if every internal vertex has no more than <InlineMath>n</InlineMath> children. 
                    The tree is called a full <InlineMath>n</InlineMath>-ary tree if every internal vertex has exactly <InlineMath>n</InlineMath> children An <InlineMath>n</InlineMath>-ary tree 
                    with <InlineMath>n=2</InlineMath> is called a binary tree.
                </p>
                <p>
                    Theorem 2. A tree with <InlineMath>n</InlineMath> vertices has <InlineMath>n-1</InlineMath> edges.
                </p>
            </h5>
            <h3>Tree Traversal</h3>
            <h5>
                <p>
                    Given a tree, preorder traversal, inorder traversal, and postorder traversal results in an order of the vertices of the tree called 
                    prefix notation, infix notation, and postfix notation, respectively. Given below are the algorithms.
                </p>
                <p>Algorithm 1. Preorder Traversal</p>
                <BlockMath math="
                \begin{align*}
                    & \textbf{procedure}\textit{ preorder}(T:\text{ ordered rooted tree}) \\
                    & r:=\text{ root of }T \\
                    & \text{list }r \\
                    & \textbf{for }\text{each child }c\text{ of }r\text{ from left to right}\\
                    & \quad T(c):=\text{ subtree with }c\text{ as its root}\\
                    & \quad\textit{preorder}(T(c))
                \end{align*}
                "/>
                <p>Algorithm 2. Inorder Traversal</p>
                <BlockMath math="
                \begin{align*}
                    & \textbf{procedure}\textit{ inorder}(T:\text{ ordered rooted tree}) \\
                    & r:=\text{ root of }T \\
                    & \textbf{if }r\text{ is a leaf }\textbf{then}\text{ list }r\\
                    & \textbf{else }\\
                    & \quad l:=\text{first child of }r\text{ from left to right}\\
                    & \quad T(l):=\text{subtree with }l\text{ as its root} \\
                    & \quad \textit{inorder}(T(l))\\
                    & \quad \text{list }r\\
                    & \quad \textbf{for }\text{each child }c\text{ of }r\text{ except for }l\text{ from left to right}\\
                    & \quad \quad T(c):=\text{subtree with }c\text{ as its root}\\
                    & \quad \quad \textit{inorder}(T(c))
                \end{align*}
                "/>
                <p>Algorithm 3. Postorder Traversal</p>
                <BlockMath math="
                \begin{align*}
                    & \textbf{procedure}\textit{ postorder}(T:\text{ ordered rooted tree}) \\
                    & r:=\text{ root of }T \\
                    & \textbf{for }\text{each child }c\text{ of }r\text{ from left to right}\\
                    & \quad T(c):=\text{ subtree with }c\text{ as its root}\\
                    & \quad\textit{postorder}(T(c)) \\
                    & \text{list }r
                \end{align*}
                "/>
            </h5>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/DiscreteProbability/probtheory">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
        </LeafTopic>
    );
}

export default Trees;
