import Algorithm from '../Algorithm.js';

class GraphAlg extends Algorithm {
  constructor(data, setData) {
    super();
    this.data = data;
    this.setData = setData;
  }

  reset(setPlaying, setFinished) {
    this.data.visited = [];
    this.data.marked = [];
    this.data.visiting = -1;
    this.data.marked_edges = [];
    this.setData({...this.data});
    super.reset(setPlaying, setFinished);
  }
}

export default GraphAlg;
