const MAX_LIMIT = 1000000;
export default function RecDecorator(f) {
  let v = [];
  let root = true;
  let id = 0;
  let d = 0;
  const ret = function(...params) {
    const isRoot = root;
    root = false;
    v.push({ "id": id++, "params": [...params], "children": [] });
    if (id >= MAX_LIMIT || d >= 170) {
      throw new Error("Max recursion");
    }
    try {
      const i = v.length;
      d += 1;
      const ret = f(...params);
      d -= 1;
      const children = v.splice(i);
      v[v.length - 1].children = children;
      v[v.length - 1].return = ret;
      if (!isRoot) return ret;
      root = true;
      id = 0;
      d = 0;
      return v.pop();
    } catch (e) {
      if (!isRoot) throw e;
      root = true;
      id = 0;
      d = 0;
      v.length = 0;
      return null;
    }
  };
  ret.toString = f.toString();
  return ret;
}
