import Shuffle from './Shuffle.js';
import BubbleSort from './BubbleSort.js';
import InsertionSort from './InsertionSort.js';
import SelectionSort from './SelectionSort.js';
import QuickSort from './QuickSort.js';

import Dfs from './Dfs.js';
import Bfs from './Bfs.js';
import Dijkstra from './Dijkstra.js'

import Noop from './Noop.js';
import Vector2Alg from './Vector2Alg.js';

export const VectorAlgs = {
  algs: [
    { name: "Shuffle", alg: Shuffle, instant: true, },
    { name: "Bubble Sort", alg: BubbleSort },
    { name: "Insertion Sort", alg: InsertionSort },
    { name: "Selection Sort", alg: SelectionSort },
    { name: "Quick Sort", alg: QuickSort },
  ],
  init: (setData) => {
    setData([]);
    },
    alg_init: (s, a, data, setData) => {
      s.alg = new (a.alg)(data, setData);
      if (a.instant) {
        while (true) {
          const { done } = s.alg.handle.next();
          if (done) break;
          setData(o => o.map(e => ({val: e.val})));
        }
      }
    },
};

export const Vector2Algs = {
  algs: [
    { name: "Shuffle", alg: new Vector2Alg(Shuffle, Noop), copy: true },
    { name: "Compare", alg: null },
  ],
  init: (setData) => {
    setData([[], [], [0, 0], [0, 0]]);
  },
  alg_init: (s, a, data, setData) => {
    if (a.alg == null) {
      s.alg = new (Vector2Alg(data[4], data[5]))(data, setData);
    } else {
      s.alg = new (a.alg)(data, setData);
    }
    if (a.copy) {
      while (true) {
        const { done } = s.alg.a1.handle.next();
        setData(o => {
          const c = [...o];
          c[0] = c[0].map(({ val }) => ({ val }));
          c[1] = [...c[0]];
          return c;
        });
        if (done) break;
      }
    }
  },
};

export const GraphAlgs = {
  algs: [
    { name: "DFS", alg: Dfs },
    { name: "BFS", alg: Bfs },
    { name: "Dijkstra", alg: Dijkstra}
  ],
  init: (setData) => {
    setData({AL: []});
  },
  alg_init: (s, a, data, setData) => {
    setData(d => {
      const c = {...d, visited: [], marked: [], visiting: -1, selected: false, marked_edges : []};
      s.alg = new (a.alg)(c, setData);
      return c;
    });
  },
};
