export const infixPrecedence = new Map(
  [
    ['=', [2, 1]],
    ['+', [3, 4]],
    ['-', [3, 4]],
    ['*', [5, 6]],
  ]);

export default class SetProblem {
  constructor() {
    this.sets = new Map();
  }

  set(ident, iset) {
    this.sets.set(ident, iset);
  }

  get(ident) {
    return this.sets.get(ident);
  }

  intersection(src, ...others) {
    if (!src) return new Set();
    return new Set(
      [...src]
        .filter(v => others.every(o => o.has(v))));
  }

  union(...srcs) {
    return new Set(srcs.flatMap(s => [...s]));
  }

  difference(src, ...others) {
    if (!src) return new Set();
    return new Set(
      [...src]
        .filter(v => others.every(o => !o.has(v))));
  }

  *evalStep(expr) {
    yield expr;
    yield* this.evalStepH(expr);
  }

  *evalStepH(expr) {
    if (expr instanceof Set || typeof expr === "string") {
      if (typeof expr === "string") {
        yield this.get(expr);
      }
      return;
    }
    let f = null;
    switch (expr.type) {
      case "=":
        for (const e of this.evalStepH(expr.sets[1])) {
          expr.sets[1] = e;
          yield expr;
        }
        this.set(expr.sets[0], expr.sets[1]);
        yield expr.sets[1];
        return;
      case "+":
        f = this.union;
        break;
      case "*":
        f = this.intersection;
        break;
      case "-":
        f = this.difference;
        break;
    }
    for (let i = 0; i < expr.sets.length; i++) {
      for (const e of this.evalStepH(expr.sets[i])) {
        expr.sets[i] = e;
        yield expr;
      }
    }
    yield f(...expr.sets);
  }

  eval(expr) {
    let res;
    for (const e of this.evalStep(expr)) {
      res = e;
    }
    return res;
  }

  static #parseSet(lex) {
    let v = [];
    while (true) {
      const next = lex.next();
      if (next == null) return null;
      const {type, value} = next;
      if (type === 'op' && value !== '}') return null;
      if (type === 'name') {
        v.push(value);
      } else {
        return new Set(v);
      }
    }
  }

  // https://matklad.github.io/2020/04/13/simple-but-powerful-pratt-parsing.html
  static #parseR(lex, min_bp, root=false) {
    const s = lex.next();
    let lhs;
    if (s == null) {
      return null;
    } else if (s.type === 'op' && s.value === '{') {
      let r = this.#parseSet(lex);
      lhs = r;
    } else if (s.type === 'op' && s.value === '(') {
      lhs = this.#parseR(lex, 0);
      const next = lex.next();
      if (next == null) return null;
      const {type, value} = next;
      if (!(type === 'op' && value === ')')) return null;
    } else if (s.type === 'op') {
      return null;
    } else {
      lhs = s.value;
    }

    while (true) {
      let op = lex.peek;
      if (op == null) break;
      if (op.type === 'name') return null;
      let bps = infixPrecedence.get(op.value);
      if (bps == null) {
        if (root) return null;
        break;
      }
      let [l_bp, r_bp] = bps;
      if (l_bp < min_bp) break;
      lex.next();
      let rhs = this.#parseR(lex, r_bp);
      if (rhs == null) return null;
      lhs = { "type": op.value, "sets": [lhs, rhs] };
    }

    return lhs;
  }

  static parse(str) {
    const lex = new Lexer(str);
    return this.#parseR(lex, 0, true);
  }
};

class Lexer {
  constructor(str) {
    this.lexer = this.lex(str);
    this.token = undefined;
    this.peek = undefined;
    this.next();
  }

  *lex(str) {
    let s = 0;
    for (let i = 0; i <= str.length; i++) {
      if (i === str.length || /\s|[{}()+=*,-]/.test(str[i])) {
        if (s < i) yield {"type": "name", "value": str.substring(s, i)};
        s = i+1;
      }
      if (/[{}()+=*-]/.test(str[i])) {
        yield {"type": "op", "value": str[i]};
      }
    }
  }

  next() {
    this.token = this.peek;
    this.peek = this.lexer.next().value;
    return this.token;
  }
}
