import { React, useEffect } from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import axios from "axios";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 1.2 Introduction to Proofs 
function Proofs() {
    const url = window.location.href;

    const mark_incomplete = async () => {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `JWT ${localStorage.getItem('access')}`,
                'lesson-url': `${url}`
            }
          };
          try {
            await axios.delete(`${process.env.REACT_APP_API_URL}/api/modify-page/`, config);
          } catch (err) {
            console.log(err);
          }
    };

    const add_page = async () => {
        const config = {
            headers: {
                "Authorization": `JWT ${localStorage.getItem("access")}`,
                "lesson-url": `${url}`
            }
        };
        console.log(config);
        try {
            const data = {};
            await axios.post(`${process.env.REACT_APP_API_URL}/api/modify-page/`, data, config);
        } catch (err) {
            console.log(err);
        }
    };

    useEffect(() => {
        add_page()
    }, []);
    
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px'
            }}>

            <button className="btn btn-danger mt-4" onClick={mark_incomplete}>
                Mark Lesson as Incomplete
            </button>
            <h1 style={{textAlign: 'center'}}>Lesson 1.2 Proof Methods</h1>
            <h5>
            In direct proofs, we prove a statement directly by making the necessary assumption and proving the result is true. 
            <br/>
            <BlockMath math="p\Longrightarrow q"/>
            <br/>
            Another proof technique is proof by contraposition. Remember, the contrapositive of a proposition is logically equivalent to
            the original proposition. Hence, a proof by contraposition is simply proving the contrapositive instead of the original statement.
            <br/>
            <BlockMath math="\neg q \Longrightarrow \neg p"/>
            <br/>
            Proofs by contradiction begin by assuming the negation of the proposition we want to prove, and arriving at a contradiction, meaning the 
            original proposition is indeed true. 
            <br/>
            <br/>
            Proof by cases involve separating a proof into more than one case. For example, case 1 may be that the number is even, and case 2 
            is that the number is odd. Proof by cases can be used in any of the above proof strategies. It is used whenever we need to consider 
            the different cases separately for a proof. 
            </h5>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/SetsFunctions/sets">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
        </LeafTopic>
    );
}

export default Proofs;
