import VectorAlg from './VectorAlg.js';

class BubbleSort extends VectorAlg {
  *run() {
    let swapped = true;
    while (swapped) {
      swapped = false;
      for (let i = 1; i < this.vec.length; i++) {
        const c = this.cmp(i-1, i);
        yield;
        if (c <= 0) continue;
        this.swp(i-1, i);
        yield;
        swapped = true;
      }
    }
  }
}

export default BubbleSort;
