import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import styles from './styles/Topics.module.css';

//import Topics from './components/Topics'
//import IntroToLogic from './IntroToLogic'

function Lesson({ path, desc, pinned, onPin, isSearch }) {
  return (
    <>
      {isSearch ? <div>
        <button className={pinned ? "btn btn-primary" : "btn btn-secondary"} onClick={onPin} style={{
          padding: '4px',
          margin: '2px',
        }}>
          <img src="/pin.svg" style={{
            width: '10px',
            height: '10px',
            padding: '0px',
            margin: '0px',
          }}/>
        </button>
        <Link className="link" to={path}>{desc}</Link>
      </div> : 
      <div className="mt-3">
         <Link className="link" to={path}>{desc}</Link>
      </div>}
    </>
    
  );
}

const topics = [
  {
    group: "Discrete Mathematics and Its Applications",
    lessons: [
      { path: "/topics/IntroToLogic", desc: "Lesson 1 Introduction to Logic and Proofs" },
      { path: "/topics/SetsFunctions", desc: "Lesson 2 Sets, Functions, Sequences, Summations" },
      { path: "/topics/Relations", desc: "Lesson 3 Relations" },
      { path: "/topics/NumberTheory", desc: "Lesson 4 Number Theory" },
      { path: "/topics/AlgorithmsComplexity", desc: "Lesson 5 Introduction to Algorithms" },
      { path: "/topics/Induction", desc: "Lesson 6 Induction and Recursion" },
      { path: "/topics/Counting", desc: "Lesson 7 Counting" },
      { path: "/topics/GraphTheory", desc: "Lesson 8 Graphs and Trees" },
      { path: "/topics/DiscreteProbability", desc: "Lesson 9 Discrete Probability" },
    ],
  },
  {
    group: "Data Structures and Algorithms",
    lessons: [
      { path: "/topics/ElementarySorts", desc: "Lesson 1 Selection sort and Insertion sort" },
      { path: "/topics/MergeSort", desc: "Lesson 2 Mergesort" },
      { path: "/topics/QuickSort", desc: "Lesson 3 Quicksort" },
      { path: "/topics/BinarySearchTree", desc: "Lesson 4 Binary Search Trees" },
      { path: "/topics/BalancedSearchTree", desc: "Lesson 5 Balanced Search Trees" },
      { path: "/topics/BFSandDFS", desc: "Lesson 6 BFS and DFS" },
      { path: "/topics/MinimumSpanningTree", desc: "Lesson 7 Minimum Spanning Trees" },
      { path: "/topics/ShortestPath", desc: "Lesson 8 Shortest Paths" },
    ],
  },
];

//this button will take user to a list of all lessons
function Topics() {
  const [pinned, setPinned] = useState(null);
  const [resume, setResume] = useState(null);
  const [searchResult, setSearchResult] = useState([]);

  const [query, setQuery] = useState('');

  const handleInputChange = e => {
    setQuery(e.target.value);
  };

  const insert_lesson = async (url, lesson_name) => {
    const config = {
      headers: {
        "lesson-url": `${url}`,
        "lesson-name": `${lesson_name}`
      }
    };
    try {
      const data = {};
      await axios.post(`${process.env.REACT_APP_API_URL}/api/create-lesson/`, data, config);
    }
    catch (err) {
      console.log(err);
    }
  };

  const handleSearch = async (query) => {
    if (query.trim().length === 0) {
      setSearchResult([]);
      return;
    }
    const config = {
      headers: {
        'search-keyword': `${query}`,
        'Content-type': 'application/json',
        'Accept': 'application/json'
      }
    };
    try {
      const search_results = await axios.get(`${[process.env.REACT_APP_API_URL]}/api/search/`, config);
      const resultsArray = [];
      for (const result of search_results.data) {
        resultsArray.push(result.fields);
      }
      setSearchResult(resultsArray);
    } 
    catch (err) {
      console.log(err);
    }
  };

  useEffect(() => {
    for (const section of topics) {
      for (const lesson of section.lessons) {
        insert_lesson(lesson.path, lesson.desc);
      }
    }
  }, []);

  useEffect(() => {
    const resumeI = localStorage.getItem("resume");
    setResume(resumeI);
    const oldPinned = localStorage.getItem("pinned");
    setPinned(new Set(JSON.parse(oldPinned)));
  }, []);

  useEffect(() => {
    if (pinned == null) return;
    localStorage.setItem("pinned", JSON.stringify([...pinned]));
  }, [pinned]);

  const onPin = (key) => {
    return () => {
      setPinned(s => {
        const c = new Set(s);
        if (c.has(key)) {
          c.delete(key);
        } else {
          c.add(key);
        }
        return c;
      });
    }
  };

  return (
    //<button>View All Topics</button>
    <div style={{marginBottom: "40px", marginTop: "40px", textAlign: "center"}}>

      <div className={styles.wrapper}>
        <div className={styles.searchBar}>
          <input
          id={styles.searchQueryInput} 
          type='text'
          placeholder='Search for a lesson...'
          value={query}
          onChange={e => handleInputChange(e)}
          className={styles.input}/>

          <button id={styles.searchQuerySubmit} onClick={() => handleSearch(query)} style={{marginLeft: "20px"}}>
            <i className="fa fa-search"></i>
          </button>
        </div>
      </div>

      <h1 className="mt-3">Topics</h1>
      {resume && (
        <div>
          <Link to={resume}>
            <button className="btn btn-primary">Resume</button>
          </Link>
        </div>
      )}

      {pinned && pinned.size !== 0 && (
        <div key={-2}>
          <h3 key={-1}>Pinned</h3>
          {[...pinned].sort().map(k => {
            const [i, j] = k.split("-");
            return (
              <Lesson key={k} pinned={true} onPin={onPin(k)} {...topics[i].lessons[j]} isSearch={false} />
            );
          })}
        </div>
      )}

      {(searchResult.length === 0) ? topics.map((g, i) => (
        <div key={`${i}-ww`}>
          <h3 className="mt-3 mb-3" key={i}>{g.group}</h3>
          {g.lessons.map((l, j) => {
            const key = `${i}-${j}`;
            return (
              <Lesson key={key} pinned={pinned?.has(key)} onPin={onPin(key)} isSearch={true} {...l} />
            );
          })}
        </div>
      )) : searchResult.map((res, i) => (
        <div>
          {i == 0 ? <h3 className="mt-3 mb-3">Search Results</h3> : <></>}
          <Lesson desc={res.lessonName} path={res.urlLink} isSearch={false} />
        </div>
      ))}
    </div>
  );
}

export default Topics;
