import React, { useEffect, useState, useCallback } from 'react';
import { createPortal } from 'react-dom';

import { VectorAlgs, Vector2Algs, GraphAlgs } from './whiteboard/algorithms/Algorithms.js';
import Vector from './whiteboard/data-structures/Vector.js';
import Vector2 from './whiteboard/data-structures/Vector2.js';
import Graph from './whiteboard/data-structures/Graph.js';

const sets = {
  "vector": { group: VectorAlgs, component: Vector },
  "vector2": { group: Vector2Algs, component: Vector2 },
  "graph": { group: GraphAlgs, component: Graph },
};

function AlgorithmButtons({ state, data, setData, canvas, mode, setMode }) {
  const [playing, setPlaying] = useState(false);
  const [finished, setFinished] = useState(false);
  const [algset, setAlgset] = useState("vector");
  const [onInteract, setOnInteract] = useState(null);
  const [initialized, setInitialized] = useState(false);

  useEffect(() => {
    sets[algset]?.group.init(setData);
    state.alg = null;
    state.reset = sets[algset]?.group.init;
    setFinished(false);
    setPlaying(false);
    setInitialized(true);
  }, [algset, setData, state]);

  useEffect(() => {
    const defaultInteract = {
      "graph": ({ id, other }) => {
        if (mode === "eraser") {
          setData(o => {
            const c = {...o};
            if (other != null) {
              const e = c.AL[id].edges;
              const ind = e.findIndex(({ vertex }) => vertex === other);
              e.splice(ind, 1);
              
              //Remove "twin" edge in an undirected graph
              if (!c.directed) {
                const twinIndex = c.AL[other].edges.findIndex(({ vertex }) => vertex === id);
                if (twinIndex >= 0) {
                  c.AL[other].edges.splice(twinIndex, 1);
                }
              }
            } else { //other is null
              c.AL[id] = null;
              for (let v of c.AL) {
                if (!v) continue;
                v.edges = v.edges.filter(({ vertex }) => vertex !== id);
              }
              
              //Remove any "twin" edges incident to the removed vertex
              if (!c.directed) { 
                for (let vertex of c.AL) {
                  if (!vertex) continue;
                  vertex.edges = vertex.edges.filter(({ vertex: twinVertex }) => twinVertex !== id);
                }
              }
              
            }
            return c;
          });
        } else if (mode === "select") {
          setData(o => {
            const c = {...o};
            if (other != null) {
              const e = c.AL[id].edges;
              const ind = e.findIndex(({ vertex }) => vertex === other);
              c.AL[id].edges[ind].value = Number(window.prompt("Enter weight:", e[ind].value));
              
              if (!c.directed) {
                const twinIndex = c.AL[other].edges.findIndex(({ vertex }) => vertex === id);
                if (twinIndex !== -1) {
                  c.AL[other].edges[twinIndex].value = c.AL[id].edges[ind].value;
                }
              } 
            } else {
              c.selected = true;
            }
            return c;
          });
        }
      },
    };
    setOnInteract(() => defaultInteract[algset]);
  }, [algset, mode, setData, state]);

  const handleDirectedChange = (e) => {
    //setDirected(e.target.value === "directed"); - no hook for this, don't include
    // Update data.directed based on the selected option
    console.log("e target value: " + e.target.value);
    setData((prevData) => ({
      ...prevData,
      directed: (e.target.value === "directed"),
      AL: [], //clear AL when directed value changes 
      marked_edges: []
    }));
    console.log("Graph has been changed to directed=" + data?.directed + " and AL has been cleared");
    console.log("AL");
    console.log(data.AL);
  };

  const inputs = {
    "vector": (
      <>
        <button className="btn btn-primary" onClick={() => {
          const elem = document.getElementById("element");
          const val = Number(elem.value);
          setData(o => [...o, {val}]);
          elem.value = val+1;
        }}> Add </button> element <input id="element" type="number" size="4"/> to vector
      </>
    ),
    "vector2": (
      <>
        <button className="btn btn-primary" onClick={() => {
          const elem = document.getElementById("element");
          const val = Number(elem.value);
          setData(o => {
            const c = [...o];
            c[0] = [...c[0], { val }];
            c[1] = [...c[0]];
            return c;
          });
          elem.value = val+1;
        }}> Add </button> element <input id="element" type="number" size="4"/> to vector
        <div style={{
          display: 'inline',
          margin: '1em',
        }}>
          <select name="sort1" id="sort1" defaultValue="-1" onChange={
            () => setData(d => {
              const c = [...d];
              c[4] = VectorAlgs.algs[document.getElementById('sort1').value].alg;
              return c;
            })
          }>
            <option value="-1" disabled>---</option>
            {VectorAlgs.algs.map(({ name }, i) => (i > 0) && <option value={i}>{name}</option>)}
          </select> vs
          <select name="sort2" id="sort2" defaultValue="-1" onChange={
            () => setData(d => {
              const c = [...d];
              c[5] = VectorAlgs.algs[document.getElementById('sort2').value].alg;
              return c;
            })
          }>
            <option value="-1" disabled>---</option>
            {VectorAlgs.algs.map(({ name }, i) => (i > 0) && <option value={i}>{name}</option>)}
          </select>
        </div>
      </>
    ),
    "graph": (
      <>
        <button onClick={() => { //Vertex button
          setMode('handling');
          canvas.current.onmousedown = ({ pageX, pageY }) => {
            function touch(x, y, tx, ty, r, min_d=0) {
              return (x-tx)*(x-tx) + (y-ty)*(y-ty) <= r*r + min_d;
            }
            setData(o => {
              if (o.AL.some((v) => v && touch(v.x, v.y, pageX-25, pageY-25, 50, 40))) return o;
              const c = {...o};
              c.AL = [...c.AL];
              const ind = c.AL.indexOf(null);
              if (ind > -1) {
                c.AL[ind] = { id: ind, x: pageX-25, y: pageY-25, edges: [] };
              } else if (c.AL.length < 26) {
                c.AL.push({ id: c.AL.length, x: pageX-25, y: pageY-25, edges: [] });
              }
              
              return c;
            });
          };
        }}
        ><img src="./vertex.png" alt="Vertex" /></button>

        <button onClick={() => { //Edge
          setMode('handling');
          const handle = ({ id: id1 }) => { //Note: this may not work after deleting a vertex
            setOnInteract(() => ({ id: id2 }) => {
              if (id1 === id2 || data.AL[id1].edges.some(({ vertex }) => vertex === id2)) return;
              setData(o => {
                const c = {...o};
                c.AL[id1].edges.push({ value: 1, vertex: id2 });
                c.AL[id1].edges.sort((a, b) => a.vertex - b.vertex);
                if (!(data?.directed)) {
                  c.AL[id2].edges.push({ value: 1, vertex: id1 });
                  c.AL[id2].edges.sort((a, b) => a.vertex - b.vertex);
                }
                return c;
              });
              setOnInteract(() => handle);
            });
          };
          setOnInteract(() => handle);
        }}
        ><img src="/edge.jpg" alt="Edge" /></button>

      <select
        name="directed"
        id="directed"
        value={data?.directed ? "directed" : "undirected"}
        onChange={handleDirectedChange}
      >
        <option value="directed">Directed</option>
        <option value="undirected">Undirected</option>
      </select>
      </>
    ),
  };

  const buttonClick = useCallback((a) => () => {
    sets[algset]?.group.alg_init(state, a, data, setData);
    setFinished(false);
  }, [algset, data, setData, state]);

  const portal = document.getElementById("data-structure");
  const component = sets[algset]?.component;

  function indexToLetter(index) {
    return String.fromCharCode(65 + index);
  }
  return (
    <>
      {
        initialized &&
          createPortal(React.createElement(component, {data, setData, mode, onInteract}), portal)
      }
      <select name="algset" id="algset" defaultValue="vector" onChange={e => {setInitialized(false); setAlgset(e.target.value); }}>
        <option value="vector">Sorting Algorithms</option>
        <option value="vector2">Compare Sorting Algorithms</option>
        <option value="graph">Graph Algorithms</option>
      </select>
      <div>
        {inputs[algset]}
      </div>
      <div>
        {sets[algset]?.group.algs.map((a, i) =>
          <button style={{paddingTop: '10px', fontSize: '12px'}} key={i} onClick={buttonClick(a)}>{a.name}</button>
        )}
      </div>
      <div>
        <button onClick={() => state.alg?.toggle(playing, setPlaying, setFinished)} style={{paddingTop: '10px', fontSize: '14px'}}>
          {playing ? "Pause" : "Play" }
         </button>
        <button onClick={() => state.alg?.step(setPlaying, setFinished)} style={{paddingTop: '10px', fontSize: '14px'}}>Step</button> 
        <button onClick={() => state.alg?.reset(setPlaying, setFinished)} style={{paddingTop: '10px', fontSize: '14px'}}>Reset</button>
        {finished && "Algorithm finished!"}
      </div>

      {data != null && data.visited?.length > 0 && (
        <p>visited: {data.visited.map(indexToLetter).join(", ")}</p>
      )}
    </>
  );
}

export default AlgorithmButtons;
