import Latex from "react-latex";

export default function SetTree({ tree, depth }) {
  let inner;
  if (tree instanceof Set) {
    inner = tree.size === 0 ? <div><Latex displayMode={false}>{`$\\emptyset$`}</Latex></div> : (
      <>
        {[...tree].map((v, i) => <div key={i} style={{
          display: 'inline-block',
          marginRight: '1em',
        }}>{v}</div>)}
      </>
    );
  } else if (typeof tree === "string") {
    inner = <>{tree}</>;
  } else {
    inner = (
      <>
        {tree.type}
        <SetTree tree={tree.sets[0]} depth={depth+1} />
        <SetTree tree={tree.sets[1]} depth={depth+1} />
      </>
    );
  }

  return <div style={{
    borderLeft: 'solid 1px black',
    marginLeft: '2em',
    paddingLeft: '.25em',
  }}>{inner}</div>;
}
