import React from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';
var Latex = require("react-latex")

//Lesson 6.1 Mathematical Induction 
function MathInduct() {
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 6.1 Mathematical Induction </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>
            <p>
                Suppose that we have an infinite ladder, and we want to know whether we can reach every step on this ladder. We know two things:
            </p>
            <p>
                1. We can reach the first rung of the ladder.
            </p>
            <p>
                2. If we can reach a particular rung of the ladder, then we can reach the next rung.
            </p>
            <p>
                Then we can conclude that that we can reach every rung. By (1), we know that we can reach the first rung of the ladder. Moreover, 
                because we can reach the first rung, by (2), we can also reach the second rung; it is the next rung after the first rung. Applying (2) again,
                 because we can reach the second rung, we can also reach the third rung. Continuing in this way, we can show that we can reach the 
                 fourth rung, fifth rung, and so on. 
            </p>
            <p>
                Here, we have introduced the concept of mathematical induction, an important proof technique. 
            </p>
            <p>
                Principle of Mathematical Induction: To prove that <InlineMath>P(n)</InlineMath> is true for all positive integers <InlineMath>n</InlineMath>, where <InlineMath>P(n)</InlineMath> is a 
                propositional function, we complete two steps: 
            </p>
            <p>
                Basis Step: We verify that <InlineMath>P(1)</InlineMath> is true.
            </p>
            <p>
                Inductive Step: We show that the conditional statement <InlineMath>P(k)\Longrightarrow P(k+1)</InlineMath> is true 
                for all positive integers <InlineMath>k</InlineMath>.
            </p>
            <p>
                Expressed as a rule of inference, this proof technique can be stated as
            </p>
            <BlockMath math="(P(1)\land \forall k (P(k)\Longrightarrow P(k+1))) \Longrightarrow \forall n P(n)"/>
            <p>
                Example 1. Use mathematical induction to show that 
            </p>
            <BlockMath math="1+2^1+2^2+\cdots+2^n=2^{n+1}-1"/>
            <p>
                for all nonnegative integers <InlineMath>n</InlineMath>.
            </p>
            <p>
                <InlineMath>\textit Solution:</InlineMath> Let <InlineMath>P(n)</InlineMath> be the proposition that <InlineMath>{'1+2^1+2^2+\\cdots+2^n=2^{n+1}-1'}</InlineMath> for the integer <InlineMath>n</InlineMath>.
            </p>
            <p>
                Basis Step: <InlineMath>P(0)</InlineMath> is true because <InlineMath>2^0=1=2^1-1</InlineMath>.
            </p>
            <p>
                Inductive Step: For the inductive hypothesis, we assume that <InlineMath>P(k)</InlineMath> is true for an arbitrary nonnegative 
                integer <InlineMath>k</InlineMath>. 
            </p>
            <BlockMath math="1+2^1+2^2+\cdots+2^k=2^{k+1}-1"/>
            <p>
                We need to use this assumption to show <InlineMath>P(k+1)</InlineMath> is also true.
            </p>
            <BlockMath math="
            \begin{equation*}
                \begin{split}
                    1+2^1+2^2+\cdots+2^{k+1} &= (1+2^1+2^2+\cdots+2^k)+2^{k+1} \\
                    &= (2^{k+1}-1)+2^{k+1} \hspace*{0.5cm}(\text{by IH}) \\
                    &= 2\cdot 2^{k+1}-1\\
                    &= 2^{k+2}-1
                \end{split}
            \end{equation*}
            "/>
            <p>
                Example 2. Use mathematical induction to prove that <InlineMath>2^n\lt n!</InlineMath> for every integer <InlineMath>n</InlineMath> with <InlineMath>n \geq 4</InlineMath>.
            </p>
            <p>
                <InlineMath>\textit Solution:</InlineMath> Let <InlineMath>P(n)</InlineMath> be the proposition that <InlineMath>2^n \lt n!</InlineMath>.
            </p>
            <p>
                Basis Step: The base case is <InlineMath>n=4</InlineMath> because we want to prove <InlineMath>P(n)</InlineMath> is true <InlineMath>n\geq 4</InlineMath>. <InlineMath>P(4)</InlineMath> is 
                true because <InlineMath>2^4=16\lt 24=4!</InlineMath>.
            </p>
            <p>
                Inductive Step: Assume that <InlineMath>P(k)</InlineMath> for an arbitrary integer <InlineMath>k\geq 4</InlineMath>. Then we have
            </p>
            <BlockMath math="
            \begin{equation*}
                \begin{split}
                    2^{k+1} &= 2\cdot 2^k \\
                    &< 2\cdot k!\hspace*{0.5cm}(\text{by IH}) \\
                    &<(k+1)k!\hspace*{0.5cm}(\text{because }2<k+1)\\
                    &= (k+1)!
                \end{split}
            \end{equation*}
            "/>
            </h5>
            </div>
            <div style={{ position: 'relative'}}>
                <Link to = "/topics/Induction/recdef">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
            <div style={{
                textAlign: "center",
                padding: '20px',
                margin: '10px'
            }}>
                <Link to = "/topics/Induction/induct/inductprob1">
                <button >
                    Proof Problems
                </button>
                </Link>
                <Link to = "/topics/Induction/selectinductmcq">
                <button >
                    MCQ Problems
                </button>
                </Link>
            </div>
        </LeafTopic>
    );
}

export default MathInduct;
