import React, { useState } from 'react';
import { Link, Navigate } from 'react-router-dom';
import { connect } from 'react-redux';
import { login } from '../authentication/auth';
import './styles/Login.css';
import background from './images/background.jpg'


const Login = ({ login, isAuthenticated, responseStatus }) => {
    const [clickedSubmit, setClickedSubmit] = useState(false);

    const [formData, setFormData] = useState({
        email: '',
        password: '' 
    });

    const { email, password } = formData;

    const onChange = e => setFormData({ 
      ...formData, 
      [e.target.name]: e.target.value });

    const onLogin = async e => {
        e.preventDefault();
        await login(email, password);
        setClickedSubmit(true);
    };


    if (isAuthenticated) {
        return <Navigate to='/' />;
    }

    return (
        <div className='container mt-5'>
            <h1 style={{fontFamily: "Acumin Pro"}}>Sign In</h1>
            <p>Sign into your Account</p>
            <form onSubmit={e => onLogin(e)}>
                <div className='form-group'>
                    <input
                        className='form-control'
                        type='email'
                        placeholder='Email'
                        name='email'
                        value={email}
                        onChange={e => onChange(e)}
                        required
                    />    
                </div>
                <div className='form-group'>
                    <label></label>
                    <input
                        className='form-control'
                        type='password'
                        placeholder='Password'
                        name='password'
                        value={password}
                        onChange={e => onChange(e)}
                        minLength='6'
                        required
                    />
                </div>
                {(responseStatus === -1 && clickedSubmit) ?
                <div class="alert alert-danger mt-3" role="alert">
                    Incorrect email and/or password entered!
                </div> : <></>}
                <button className='btn btn-primary mt-4' type='submit' id="login-button">Login</button>
            </form>
            <p className='mt-3'>
                Don't have an account? <Link className="link" to='/signup'>Sign Up</Link>
            </p>
            <p className='mt-3'>
                Forgot your Password? <Link className="link" to='/reset_password'>Reset Password</Link>
            </p>
        </div>
    );
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated,
    responseStatus: state.auth.responseStatus
});

export default connect(mapStateToProps, { login })(Login);