import React, { useState } from 'react';
import { Link, Navigate } from 'react-router-dom';
import { connect } from 'react-redux';
import { delete_account } from '../authentication/auth';


const Delete = ({ delete_account, isAuthenticated }) => {
    const [formData, setFormData] = useState({
        password: '' 
    });

    const { password } = formData;

    const onChange = e => setFormData({ 
      ...formData, 
      [e.target.name]: e.target.value });
        
    const onDelete = e => {
        e.preventDefault();
        delete_account(password);
    };


    if (!isAuthenticated) {
        return <Navigate to='/' />;
    }

    return (
        <div className='container mt-5'>
            <h1>Delete Account</h1>
            <p style={{color: 'red'}}>
                Warning: this action is irreversible and will destroy all data associated with your account.
            </p>
            <form onSubmit={e => onDelete(e)}>
                <div className='form-group'>
                    <input
                        className='form-control'
                        type='password'
                        placeholder='password'
                        name='password'
                        value={password}
                        onChange={e => onChange(e)}
                        required
                    />
                </div>
                <button className='btn btn-danger mt-4' type='submit'>Delete Account</button>
            </form>
        </div>
    );
};

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, { delete_account })(Delete);