import { useState, useRef, useEffect } from 'react';

import SetProblem from './SetProblem';
import SetTree from './SetTree';

export default function SetVisualizer({ expr, env }) {
  const [tree, setTree] = useState(null);
  const handle = useRef(null);
  const timer = useRef(null);


  useEffect(() => {
    const exprTree = SetProblem.parse(expr);
    handle.current = env.evalStep(exprTree);
    const func = () => {
      let {value, done} = handle.current.next();
      if (done) {
        clearInterval(timer.current);
        return;
      }
      if (typeof value === "string") {
        setTree(value);
      } else if (value instanceof Set) {
        setTree(new Set(value));
      } else {
        setTree({...value});
      }
    };
    func();
    timer.current = setInterval(func, 1000);
    return () => {
      clearInterval(timer.current);
    };
  }, [env, expr]);

  return (
    tree ? <SetTree tree={tree} depth={0} /> : null
  );
};
