import VectorAlg from './VectorAlg.js';

class QuickSort extends VectorAlg {
  constructor(data, setData) {
    super(data, setData);
    this.pivot = -1;
  }

  *partition(a, b) {
    let i = a-1;
    for (let j = a; j < b; j++) {
      const c = this.cmp(j, b);
      yield;
      if (c < 0) {
        i++;
        this.swp(i, j);
        yield;
      }
    }
    i++;
    this.swp(i, b);
    this.pivot = i;
    yield;
  }

  *helper(a, b) {
    if (a >= b) return;
    yield* this.partition(a, b);
    const p = this.pivot;
    yield* this.helper(a, p-1);
    yield* this.helper(p+1, b);
  }

  *run() {
    // simplicity always use last element as pivot
    yield* this.helper(0, this.vec.length-1);
  }
}

export default QuickSort;
