import React from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 1.1 Selection Sort
function SelectSort() {
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 1.1 Selection Sort </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>
                <p>
                    One of the simplest sorting algorithms works as follows: First, find the smallest item in the array and exchange it 
                    with the first entry (itself if the first entry is already the smallest). Then, find the next smallest item and exchange 
                    it with the second entry. Continue in this way until the entire array is sorted. This method is called selection sort 
                    because it works by repeatedly selecting the smallest remaining item.
                </p>
                <p>
                    As you can see from the below pseudocode, the inner loop of selection sort is just a compare to test a current item 
                    against the smallest item found so far (plus the code necessary to increment the current index and to check that it 
                    does not exceed the array bounds); it could hardly be simpler. The work of moving the items around falls outside the 
                    inner loop: each exchange puts an item into its final position, so the number of exchanges is <InlineMath>n</InlineMath>. Thus, 
                    the running time is dominated by the number of compares.
                </p>
                <br/>
                <p>
                    Algorithm 1. The Selection Sort.
                </p>
                <BlockMath math="
                \begin{align*}
                    & \textbf{procedure }\textit{selection sort}(a_1,a_2,\dots,a_n:\text{ real numbers with }n\geq 2) \\
                    & \textbf{for }i:=1\textbf{ to }n-1\\
                    & \quad min:= i \\
                    & \quad \textbf{for }j:=i+1\textbf{ to }n \\
                    & \quad \quad \textbf{if }a_j<a_{min}\textbf{ then} \\ 
                    & \quad \quad \quad min:=j \\
                    & \quad \textbf{if }min\neq i\text{ then interchange }a_i\text{ and }a_{min} \\
                    & \{a_1,\dots,a_n\text{ is in increasing order}\}
                \end{align*}
                "/>
                <p>
                    The time complexity of the selection sort is <InlineMath>O(n^2)</InlineMath>.
                </p>
            </h5>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/ElementarySorts/insertsort">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
        </LeafTopic>
    );
}

export default SelectSort;
