import React from "react";
import LeafTopic from "./LeafTopic";
import { Link } from "react-router-dom";
import axios from "axios";
import { useState, useEffect } from "react";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//this button will take user to modules associated with Sets, Functions, sequences, sums 
function MergeSort() {
     const [rating, setRating] = useState(0);
    //const [communityRating, setCommunityRating] = useState(0);
    const [hoverRating, setHoverRating] = useState(0);

    const lessonID = 11;
    const [scoreSum, setScoreSum] = useState(null);
    const [scoreCount, setScoreCount] = useState(null);

    useEffect(() => {
      const config = {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `JWT ${localStorage.getItem('access')}`,
                'Accept': 'application/json'
            }
        };   

    const fetchScoreData = async () => {
      
      try {
        const response = await axios.get(`http://localhost:8000/ratelesson/${lessonID}/`, config);
        setScoreSum(response.data.scoreSum);
        setScoreCount(response.data.scoreCount);
      } catch (error) {
        console.error('Error fetching score data:', error);
      }
    };

    fetchScoreData();
  }, [lessonID]);
    
    const handleStarClick = async (starValue) => {
       const config = {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `JWT ${localStorage.getItem('access')}`,
                'Accept': 'application/json'
            }
        };

        try {
            const res = await axios.put("http://localhost:8000/ratelesson/11/", 
            {
              scoreSum: scoreSum + starValue,
              scoreCount: scoreCount + 1,

            }, config);
            console.log(res);
        } catch (e) {
            console.error(e);
        }
        
            
    setScoreSum(scoreSum + starValue);
    setScoreCount(scoreCount + 1);
    setRating(starValue);
    
    const data = {
      rating: starValue
    };
  };

  const handleStarHover = (starValue) => {
    setHoverRating(starValue);

  };

  const renderStar = (starValue) => {
    const starStyle = {
      fontSize: '24px',
      cursor: 'pointer', //to change cursor
      color: starValue <= (hoverRating || rating) ? 'gold' : 'gray',
      transition: 'color 0.3s ease'
    };
    const filled = starValue <= (hoverRating || rating) ? 'filled' : '';
    return (
      <span
        style = {starStyle}
        onClick={() => handleStarClick(starValue)}
        onMouseEnter={() => handleStarHover(starValue)}
        onMouseLeave={() => handleStarHover(0)}
      >
        &#9733; 
      </span>
    );
  };
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' 
            }}>
            <h1>Mergesort</h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' 
            }}>
            <h5>
              <p>
                A merge sort begins by splitting the list into individual elements by successively spliting lists in two. Sorting is 
                done by successively merging pairs of the lists. At the first stage, pairs of individual elements are merged into lists 
                of length two in increasing order. Then successive merges of pairs of lists are performed until the entire list is put 
                into increasing order. 
              </p>
              <p>
                In general, a merge sort proceeds by iteratively splitting lists into two sublists of equal length (or where one 
                sublist has one more element than the other) until each sublist contains one element. This succession of sublists 
                can be represented by a balanced binary tree. The procedure continues by successively merging pairs of lists, where 
                both lists are in increasing order, into a largest list with elements in increasing order, until the original list 
                is put into increasing order. The succession of merged lists can be represented by a balanced binary tree.
              </p>
              <p>
                We can also describe the merge sort recursively. To do a merge sort, we split a list into two sublists of equal, or 
                approximately equal, size, sorting each sublist using the merge sort algorithm, and then merging the two lists. The 
                recursive version of the merge sort is given in Algorithm 1. This algorithm uses the subroutine merge, which is describes 
                in Algorithm 2. 
              </p>
              <p>
                Algorithm 1. A Recursive Merge Sort.
              </p>
              <BlockMath math="
              \begin{align*}
                  & \textbf{procedure }\textit{mergesort}(L=a_1,\dots,a_n) \\
                  & \textbf{if }n>1\textbf{ then} \\
                  & \quad m:=\lfloor n/2 \rfloor \\
                  & \quad L_1:=a_1,a_2,\dots,a_m \\
                  & \quad L_2:=a_{m+1},a_{m+2},\dots,a_n \\
                  & \quad L:= \textit{merge}(\textit{mergesort}(L_1),\textit{mergesort}(L_2))\\
                  & \{L\text{ is now sorted into elements in nondecreasing order}\}
              \end{align*}
              "/>
              <p>
                Algorithm 2. Merging Two Lists.
              </p>
              <BlockMath math="
              \begin{align*}
                  & \textbf{procedure}\textit{ merge}(L_1,L_2:\text{ sorted lists}) \\
                  & L:=\text{empty list} \\
                  & \textbf{while }L_1\text{ and }L_2\text{ are both nonempty} \\
                  & \quad \text{remove smaller of first elements of }L_1\text{ and }L_2\text{ from its list; put it at the right end of L}\\
                  & \quad \textbf{if }\text{this removal makes one list empty }\textbf{then }\text{remove all elements from the other list and}\\
                  & \quad \quad \text{append them to }L\\
                  & \textbf{return }L\{L\text{ is the merged list with elements in increasing order}\}
              \end{align*}
              "/>
            <p>
              The time complexity of merge sort is <InlineMath>O(n\log n)</InlineMath>.
            </p>
            </h5>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/QuickSort">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' 
            }}>
                {[1, 2, 3, 4, 5].map((value) => (
                    <React.Fragment key={value}>
                        {renderStar(value)}
                    </React.Fragment>
                ))}
                <fetchScoreData/>
                <p>Your rating: {rating}/5</p>
                <p>Community rating: {scoreSum / scoreCount} / 5</p>
            </div>
        </LeafTopic>
    );
}

export default MergeSort;
