import { useState, useEffect } from 'react';

export default function Series() {
  const [initial, setInitial] = useState(0);
  const [step, setStep] = useState(0);
  const [N, setN] = useState(0);
  const [A, setA] = useState([]);
  const [mode, setMode] = useState(1);

  useEffect(() => {
    setA([...Array(N).keys()].map(v => (mode === 1) ? initial+step*v : initial * step**v));
  }, [N, mode, initial, step]);

  return (
    <div>
      <select onChange={(e) => setMode(Number(e.target.value))}>
        <option value="1">Arithmetic</option>
        <option value="2">Geometric</option>
      </select>
      <div>
        Initial value: <input onChange={(e) => setInitial(Number(e.target.value))} />
        Step: <input onChange={(e) => setStep(Number(e.target.value))} />
        N: <input onChange={(e) => setN(Number(e.target.value))} />
      </div>
      <div>
        <div>
          Series: [ {A.join()} ]
        </div>
        <div>
          Sum: {A.reduce((x, a) => x + a, 0)}
        </div>
      </div>
    </div>
  );
}
