import React from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 8.1 Graphs 
function Graphs() {
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 8.1 Graphs </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h3>Introduction</h3>
            <h5>
                <p>
                We begin with the definition of a graph.
                </p>
                <p>
                    Definition 1. A graph <InlineMath>G=(V,E)</InlineMath> consists of <InlineMath>V</InlineMath>, a nonempty set of vertices (or nodes) and <InlineMath>E</InlineMath>, a set 
                    of edges. Each edge has either one or two vertices associated with it, called its endpoints. An edge is said to connect its endpoints.
                </p>
                <p>A computer network can be modeled using a graph, where each vertex represents a computer, and each edge connects two 
                    computers that are able to communicate with each other.
                </p>
                <p>
                    Sometimes, the edges may need to have directions. For example, consider a one-way road. Therefore, we introduce a new definition.
                </p>
                <p>
                    Definition 2. A directed graph (or digraph) <InlineMath>(V,E)</InlineMath> consists of a nonempty set of vertices <InlineMath>V</InlineMath> and a 
                    set of directed edges (or arcs) <InlineMath>E</InlineMath>. Each directed edge is associated with an ordered pair of vertices. The directed edge 
                    associated with the ordered pair <InlineMath>(u,v)</InlineMath> is said to start at <InlineMath>u</InlineMath> and end at <InlineMath>v</InlineMath>.
                </p>
            </h5>
            <h3>
                Basic Terminology
            </h3>
            <h5>
                <p>
                    Definition 3. Two vertices <InlineMath>u</InlineMath> and <InlineMath>v</InlineMath> in an undirected 
                    graph <InlineMath>G</InlineMath> are adjacent (or neighbors) in <InlineMath>G</InlineMath> if <InlineMath>u</InlineMath> and <InlineMath>v</InlineMath> are 
                    endpoints of an edge <InlineMath>e</InlineMath> of <InlineMath>G</InlineMath>. Such an edge <InlineMath>e</InlineMath> is called incident with 
                    the vertices <InlineMath>u</InlineMath> and <InlineMath>v</InlineMath> and <InlineMath>e</InlineMath> is said to connect <InlineMath>u</InlineMath> and <InlineMath>v</InlineMath>.
                </p>
                <p>
                    Definition 4. The set of all neighbors of a vertex <InlineMath>v</InlineMath> of <InlineMath>G=(V,E)</InlineMath>, denoted by <InlineMath>N(v)</InlineMath>, is called 
                    the neighborhood of <InlineMath>v</InlineMath>. If <InlineMath>A</InlineMath> is a subset of <InlineMath>V</InlineMath>, we denote by <InlineMath>N(A)</InlineMath> the 
                    set of all vertices in <InlineMath>G</InlineMath> that are adjacent to at least one vertex in <InlineMath>A</InlineMath>. So <InlineMath>{'N(A)=\\bigcup_{v\\in A}N(v)'}</InlineMath>.
                </p>
                <p>
                    Definition 5. The degree of a vertex in an undirected graph is the number of edges incident with it, except that a loop at 
                    a vertex contributes twice to the degree of that vertex. The degree of the vertex <InlineMath>v</InlineMath> is denoted by <InlineMath>{'\\text{deg}(v)'}</InlineMath>.
                </p>
                <p>
                    Definition 6. When <InlineMath>(u,v)</InlineMath> is an edge of the graph <InlineMath>G</InlineMath> with directed edges, <InlineMath>u</InlineMath> is said to be 
                    adjacent to <InlineMath>v</InlineMath> and <InlineMath>v</InlineMath> is said to be adjacent from <InlineMath>u</InlineMath>. The vertex <InlineMath>u</InlineMath> is 
                    called the initial vertex of <InlineMath>(u,v)</InlineMath>, and <InlineMath>v</InlineMath> is called the terminal or end vertex of <InlineMath>(u,v)</InlineMath>. The 
                    initial vertex and terminal vertex of a loop are the same.
                </p>
                <p>
                    Definition 7. In a graph with directed edges the in-degree of a vertex <InlineMath>v</InlineMath>, denoted by <InlineMath>{'\\text{deg}^-(v)'}</InlineMath>, is the 
                    number of edges with <InlineMath>v</InlineMath> as their terminal vertex. The out-degree of <InlineMath>v</InlineMath>, denoted by <InlineMath>{'\\text{deg}^+(v)'}</InlineMath>, is the 
                    number of edges with <InlineMath>v</InlineMath> as their initial vertex. (Note that a loop at a vertex contributes 1 to both the in-degree and the out-degree 
                    of this vertex.)
                </p>
            </h5>
            <h3>
                The Handshaking Theorem
            </h3>
            <h5>
                <p>
                Let <InlineMath>G=(V,E)</InlineMath> be an undirected graph with <InlineMath>m</InlineMath> edges. Each edge contributes two degrees. Thus,
                </p>
                <BlockMath math="2m=\sum_{v\in V}\text{deg}(v)"/>
                <p>
                    Now consider a directed graph <InlineMath>G=(V,E)</InlineMath>. Each directed edge contributes <InlineMath>1</InlineMath> outdegree 
                    and <InlineMath>1</InlineMath> indegree. Therefore, as a consequence of the Handshaking Theorem, 
                </p>
                <BlockMath math="\sum_{v\in V}\text{deg}^-(v)=\sum_{v\in V}\text{deg}^+(v)=|E|"/>
            </h5>
            <h3>Connectivity</h3>
            <h5>
                <p>
                    A <InlineMath>{'\\textbf{path}'}</InlineMath> is a sequence of edges that begins at a vertex of a graph and travels from 
                    vertex to vertex along edges of the graph.
                </p>
                <p>
                    Definition 8. Let <InlineMath>n</InlineMath> be a nonnegative integer and <InlineMath>G</InlineMath> an undirected graph. A path 
                    of length <InlineMath>n</InlineMath> from <InlineMath>u</InlineMath> to <InlineMath>v</InlineMath> in <InlineMath>G</InlineMath> is a 
                    sequence of <InlineMath>n</InlineMath> edges <InlineMath>e_1,\dots,e_n</InlineMath> of <InlineMath>G</InlineMath> for which there exists 
                    a sequence <InlineMath>x_0=u,x_1,\dots,x_n=v</InlineMath> of vertices such that <InlineMath>e_i</InlineMath> has, for <InlineMath>i=1,\dots,n</InlineMath>, the 
                    endpoints <InlineMath>{'x_{i-1}'}</InlineMath> and <InlineMath>x_i</InlineMath>. When the graph is simple, we denote this path by its vertex 
                    sequence <InlineMath>x_0,x_1,\dots,x_n</InlineMath> (because listing these vertices uniquely determines the path). The path is a circuit 
                    if it begins and ends at the same vertex, that is, if <InlineMath>u=v</InlineMath>, and has length greater than zero. The path or 
                    circuit is said to pass through the vertices <InlineMath>{'x_1,x_2,\\dots,x_{n-1}'}</InlineMath> or traverse the edges <InlineMath>e_1,e_2,\dots,e_n</InlineMath>. A path 
                    or circuit is simple if it does not contain the same edge more than once.
                </p>
                <p>
                    Definition 9. Let <InlineMath>n</InlineMath> be a nonnegative integer and <InlineMath>G</InlineMath> a directed graph. A path of 
                    length <InlineMath>n</InlineMath> from <InlineMath>u</InlineMath> to <InlineMath>v</InlineMath> in <InlineMath>G</InlineMath> is a 
                    sequence of edges <InlineMath>e_1,e_2,\dots,e_n</InlineMath> of <InlineMath>G</InlineMath> such that <InlineMath>e_1</InlineMath> is 
                    associated with <InlineMath>(x_0,x_1)</InlineMath>, <InlineMath>e_2</InlineMath> is associated with <InlineMath>(x_1,x_2)</InlineMath>, and 
                    so on, with <InlineMath>e_n</InlineMath> associated with <InlineMath>{'(x_{n-1},x_n)'}</InlineMath>, where <InlineMath>x_0=u</InlineMath> and <InlineMath>x_n=v</InlineMath>. When 
                    there are no multiple edges in the directed graph, this path is denoted by its vertex sequence <InlineMath>x_0,x_1,x_2,\dots,x_n</InlineMath>. A path 
                    of length greater than zero that begins and ends at the same vertex is called a circuit or cycle. A path or circuit is called simple if it does 
                    not contain the same edge more than once.
                </p>
                <p>
                    Definition 10. An undirected graph is called connected if there is a path between every pair of distinct 
                    vertices of the graph. An undirected graph that is not connected is called disconnected. 
                </p>
            </h5>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/GraphTheory/trees">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
        </LeafTopic>
    );
}

export default Graphs;
