import React from "react";
import LeafTopic from "../LeafTopic.js";
import { Link } from "react-router-dom";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 5.1 Algorithms 
function Algos() {
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 5.1 Algorithms </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>
                <p>
                There are many general classes of problems that arise in discrete mathematics. Ideally, what is required for the solution 
                is a procedure that follows a sequence of steps that leads to the desired answer. Such a sequence of steps is called an algorithm.
                </p>
                <p>
                    Definition 1. An <InlineMath>\textit algorithm</InlineMath> is a finite sequence of precise instructions for performing 
                    a computation or for solving a problem.
                </p>
                <p>
                    Consider the binary search algorithm. Compared to the linear search algorithm, which is a brute force algorithm in which 
                    we compare the value we want to find to every value in the list, the binary search algorithm is faster.
                </p>
                <BlockMath math="
                \begin{align*}
                    & \textbf{procedure }\textit{binary search }(x: \text{ integer, }a_1,a_2,\dots,a_n:\text{ increasing integers})\\
                    & i:=1\ \{i \text{ is left endpoint of search interval}\} \\
                    & j:=n\ \{j\text{ is right endpoint of search interval}\} \\
                    & \textbf{while }i<j\\
                    & \quad m:= \lfloor (i+j)/2 \rfloor \\
                    & \quad \textbf{if }x>a_m\textbf{ then }i:=m+1 \\
                    & \quad \textbf{else }j:=m\\
                    & \textbf{if }x=a_i\text{ then }\textit{location }:=i\\
                    & \textbf{else }\textit{location }:=0\\
                    & \textbf{return }\textit{location}\{\textit{location }\text{is the subscript }i\text{ of the term }a_i\text{ equal 
                    to }x,\text{ or }0\text{ if }x\text{ is not found}\}
                \end{align*}
                "/>
                <p>
                    Sorting algorithms also come up quite often. Consider the bubble sort algorithm, which is one of the simplest sorting algorithms.
                </p>
                <BlockMath math="
                \begin{align*}
                    & \textbf{procedure }\textit{bubblesort}(a_1,\dots,a_n:\text{ real numbers with }n\geq 2)\\
                    & \textbf{for }i:=1\textbf{ to }n-1 \\
                    & \quad \textbf{for }j:=1\textbf{ to }n-i \\
                    & \quad \quad \textbf{if }a_j>a_{j+1}\textbf{ then }\text{interchange }a_j\text{ and }a_{j+1}\\
                    & \{a_1,\dots,a_n\text{ is in increasing order}\}
                \end{align*}
                "/>
            </h5>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/AlgorithmsComplexity/complexity">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
        </LeafTopic>
    );
}

export default Algos;
