import GraphAlg from './GraphAlg.js';


class Dfs extends GraphAlg {
  *run() {
    if (this.data.AL.length === 0) {
      return;
    } 
    console.log("visited: " + this.data.visited);
    console.log("AL");
    console.log(this.data.AL);
    let initial = this.data.vertex_id_selected;
    console.log("initial: " + initial);
    if (initial === -1) {
      initial = this.data.AL[0].id;
    }
      this.data.marked.push(initial); //stack
      this.setData({...this.data});
      while (this.data.marked.length > 0) {
          let s = this.data.marked.pop();
          //console.log("s : " + s);
          this.data.visiting = s;
          this.setData({...this.data});
          yield;
          if (!this.data.visited.includes(s)) {
            this.data.visited.push(s);
            this.setData({...this.data});
            yield;
          }
          for (const n of this.data.AL[s].edges) {
            //console.log("n : " + n.vertex);
            if (this.data.visited.includes(n.vertex)) continue;
            this.data.marked.push(n.vertex);
            //console.log(this. data.marked);
            this.setData({...this.data});
            yield;
          }
      }

    
    let traversal_order = [];
    for (let i = 0; i < this.data.visited.length; i++) {
        traversal_order.push(String.fromCharCode(this.data.visited[i] + 65));
    }
    alert("Vertices traversed in order: " + traversal_order);
  }
}

export default Dfs;
