import React from 'react';



const Vertex = ({ x, y, letter, bg_color, onClick }) => {
  return (
    <div
      style={{
        position: 'absolute',
        left: x,
        top: y,
        width: '50px', 
        height: '50px',
        borderRadius: '50%', //circle's border radius
        backgroundColor: bg_color,
        border: '2px solid black',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        cursor: 'pointer', //clickable
        zIndex: 1,
      }}
      onClick={onClick}
    >
      {letter}
    </div>
  );
};

export default Vertex;
