import React from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

import LeafTopic from "../LeafTopic";
import DFSPuzzle from "./dfsPuzzle";



//Lesson 6.2  Depth-first search
function DFS() {
    const handleCreateLesson = async () => {
      try {
        const currentDate = new Date().toISOString().split('T')[0];
        //const csrfToken = getCookie('csrftoken');
        const body = {
          lessonName: 'dfs',
          topicID: 1,
          dateComplete: currentDate
        }
        const config = {
          headers: {
                  'Content-Type': 'application/json',
                  'Accept': 'application/json',
                  "Authorization": `JWT ${localStorage.getItem("access")}` 
          }}
        const response = await axios.post('http://localhost:8000/api/lessons/', body, config);
        console.log('New lesson created:', response.data);
      } catch (error) {
        console.error('Error creating new lesson:', error);
      }
    };
    return (
          <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 6.2 Depth-first search </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>
              <p>Recall the following definition and theorem:</p>
              <p>
                Definition 1. Let <InlineMath>G</InlineMath> be a simple graph. A <InlineMath>{'\\textit{spanning tree }'}</InlineMath> of <InlineMath>G</InlineMath> is 
                a subgraph of <InlineMath>G</InlineMath> that is a tree containing every vertex of <InlineMath>G</InlineMath>.
              </p>
              <p>
                Theorem 1. A simple graph is connected if and only if it has a spanning tree.
              </p>
              <p>
                We can build a spanning tree for a connected simple graph using <InlineMath>{'\\textbf{depth-first search}'}</InlineMath>. We 
                will form a rooted tree, and the spanning tree will be the underlying undirected graph of this rooted tree. Arbitrarily choose 
                a vertex of the graph as the root. Form a path starting at this vertex by successively adding vertices and edges, where each new 
                edge is incident with the last vertex in the path and a vertex not already in the path. Continue adding vertices and edges to this path 
                as long as possible. If the path goes through all vertices of the graph, the tree consisting of this path is a spanning tree. However, if 
                the path does not go through all vertices, more vertices and edges must be added. Move back to the next to last vertex in the path, and, 
                if possible, form a new path starting at this vertex passing through vertices that were not already visited. If this cannot be done, 
                move back another vertex in the path, that is, two vertices back in the path, and try again. 
              </p>
              <p>
                Repeat this procedure, beginning at the last vertex visited, moving back up the path one vertex at a time, forming new 
                paths that are as long as possible until no more edges can be added. Because the graph has a finite number of edges and is connected, 
                this process ends with the production of a spanning tree. Each vertex that ends a path at a stage of the algorithm will be a leaf 
                in the rooted tree, and each vertex where a path is connected starting at this vertex will be an internal vertex.
              </p>
              <p>
                Note the recursive nature of this procedure. The implementation of depth-first search (DFS) is shown below.
              </p>
              <p>
                Algorithm 1. Depth-First Search.
              </p>
              <BlockMath math="
              \begin{align*}
                & \textbf{procedure }\textit{DFS}(G:\text{ a connected graph with vertices }v_1,v_2,\dots,v_n) \\
                & T:=\text{tree consisting only of the vertex }v_1\\
                & \textit{visit}(v_1)\\
                & \\
                & \textbf{procedure }\textit{visit}(v:\text{ vertex of }G) \\
                & \textbf{for }\text{each vertex }w\text{ adjacent to }v\text{ and not yet in }T\\
                & \quad\text{add vertex }w\text{ and edge }\{v,w\}\text{ to }T\\
                & \textit{visit}(w)
              \end{align*}
              "/>
            </h5>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <DFSPuzzle/>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
            <Link to="/topics/MinimumSpanningTree/prims">
            <button 
              style={{
                position: 'fixed',
                bottom: '0',
                right: '0',
                margin: '20px',
                }} 
              onClick={handleCreateLesson}>Finish Lesson</button>
            </Link>

            </div>
        </LeafTopic>
          
    );
}

export default DFS;
