function Vector2Alg(a1, a2) {
  return class {
    constructor(data, setData) {
      this.a1 = new a1(data[0], (x) => {
        setData(d => {
          const c = [...d];
          if (typeof x === 'function') {
            c[0] = x(c[0]);
          } else {
            c[0] = x;
          }
          c[2][0] = this.a1.cmps;
          c[2][1] = this.a1.swps;
          return c;
        });
      });
      this.a2 = new a2(data[1], (x) => {
        setData(d => {
          const c = [...d];
          if (typeof x === 'function') {
            c[1] = x(c[1]);
          } else {
            c[1] = x;
          }
          c[3][0] = this.a2.cmps;
          c[3][1] = this.a2.swps;
          return c;
        });
      });
      this.finished = [false, false];
      this.playing = [false, false];
    }

    reset(setPlaying, setFinished) {
      this.a1.reset(setPlaying, setFinished);
      this.a2.reset(setPlaying, setFinished);
    }

    toggle(playing, setPlaying, setFinished) {
      this.a1.toggle(playing, p => {
        this.playing[0] = p;
        setPlaying(this.playing[0] || this.playing[1]);
      }, f => {
        this.finished[0] = f;
        setFinished(this.finished[0] && this.finished[1]);
      });
      this.a2.toggle(playing, p => {
        this.playing[1] = p;
        setPlaying(this.playing[0] || this.playing[1]);
      }, f => {
        this.finished[1] = f;
        setFinished(this.finished[0] && this.finished[1]);
      });
    }

    step(setPlaying, setFinished) {
      this.a1.step(p => {
        this.playing[0] = p;
        setPlaying(this.playing[0] || this.playing[1]);
      }, f => {
        this.finished[0] = f;
        setFinished(this.finished[0] && this.finished[1]);
      });
      this.a2.step(p => {
        this.playing[1] = p;
        setPlaying(this.playing[0] || this.playing[1]);
      }, f => {
        this.finished[1] = f;
        setFinished(this.finished[0] && this.finished[1]);
      });
    }
  }
}

export default Vector2Alg;
