import React from "react";
import LeafTopic from "../LeafTopic";
import { Link } from "react-router-dom";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 9.2 Bayes Theorem
function Bayes() {
    return (
        <LeafTopic>
             <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 9.2 Bayes' Theorem  </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>
                <p>
                    Suppose that <InlineMath>E</InlineMath> and <InlineMath>F</InlineMath> are events from a sample space <InlineMath>S</InlineMath> such 
                    that <InlineMath>p(E)\neq 0</InlineMath> and <InlineMath>p(F)\neq 0</InlineMath>. Then
                </p>
                <BlockMath math="p(F\mid E)=\frac{p(E\mid F)p(F)}{p(E\mid F)p(F)+p(E\mid \overline{F})p(\overline{F})}"/>
                <br/>
                <p>
                    <InlineMath>\textit Proof:</InlineMath> The definition of conditional probability tells us 
                    that <InlineMath>p(F\mid E)=p(E \cap F)/p(E)</InlineMath> and <InlineMath>p(E\mid F)=p(E\cap F)/p(F)</InlineMath>. Therefore, <InlineMath>p(E\cap F)=p(F\mid E)p(E)</InlineMath> and <InlineMath>p(E\cap F)=p(E\mid F)p(F)</InlineMath>. Equating 
                    these two expressions for <InlineMath>p(E\cap F)</InlineMath> shows that
                </p>
                <BlockMath math="p(F\mid E)p(E)=p(E\mid F)p(F)"/>
                <p>
                    Dividing both sides by <InlineMath>p(E)</InlineMath>, we find that 
                </p>
                <BlockMath math="p(F\mid E)=\frac{p(E\mid F)p(F)}{p(E)}"/>
                <p>
                    Next, we show that <InlineMath>{'p(E)=p(E\\mid F)p(F)+p(E\\mid \\overline{F})p(\\overline{F})'}</InlineMath>. To 
                    see this, first note that <InlineMath>{'E=E\\cap S = E \\cap (F \\cup \\overline{F}) = (E\\cap F )\\cup (E \\cap \\overline{F})'}</InlineMath>. Furthermore, <InlineMath>E\cap F</InlineMath> and <InlineMath>{'E\\cap\\overline{F}'}</InlineMath> are 
                    disjoint, because if <InlineMath>x\in E \cap F</InlineMath> and <InlineMath>{'x\\in E \\cap \\overline{F}'}</InlineMath>, then <InlineMath>{'x\\in F\\cap \\overline{F}=\\emptyset'}</InlineMath>. Consequently, <InlineMath>{'p(E)=p(E\\cap F)+p(E\\cap\\overline{F})'}</InlineMath>. We 
                    have already shown that <InlineMath>p(E\cap F)=p(E \mid F)p(F)</InlineMath>. Moreover, we have <InlineMath>{'p(E \\mid \\overline{F})=p(E\\cap\\overline{F})/p(\\overline{F})'}</InlineMath>, which shows 
                    that <InlineMath>{'p(E\\cap \\overline{F})=p(E\\mid \\overline{F})p(\\overline{F})'}</InlineMath>. It now follows that
                </p>
                <BlockMath math="p(E)=p(E\cap F)+p(E\cap \overline{F})=p(E\mid F)p(F)+p(E\mid \overline{F})p(\overline{F})"/>
                <p>
                    To complete the proof we insert this expression for <InlineMath>p(E)</InlineMath> into the 
                    equation <InlineMath>p(F \mid E)=p(E \mid F)p(F)/p(E)</InlineMath>. We have proved that
                </p>
                <BlockMath math="p(F\mid E)=\frac{p(E\mid F)p(F)}{p(E\mid F)p(F)+p(E\mid \overline{F})p(\overline{F})}"/>
            </h5>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/ElementarySorts/selectsort">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
        </LeafTopic>
    );
}

export default Bayes;
