import VectorAlg from './VectorAlg';

class InsertionSort extends VectorAlg {
  *run() {
    for (let i = 1; i < this.vec.length; i++) {
      for (let j = i-1; j >= 0; j--) {
        const c = this.cmp(j, j+1);
        yield;
        if (c <= 0) break;
        this.swp(j, j+1);
        yield;
      }
    }
  }
}

export default InsertionSort;
