import React from "react";
import { Link } from "react-router-dom";
import LeafTopic from "../LeafTopic";
import 'katex/dist/katex.min.css';
import { InlineMath, BlockMath } from 'react-katex';

//Lesson 6.2 Recursive Definitions and Structural Induction
function RecDef() {
    return (
        <LeafTopic>
            <div style={{
                textAlign: 'center',
                padding: '20px',
                margin: '10px' }}>
            <h1>Lesson 6.2 Recursive Definitions and Structural Induction </h1>
            </div>
            <div style={{
                textAlign: 'justify',
                padding: '20px',
                margin: '10px' }}>
            <h5>
                <p>
                Sometimes it is difficult to define an object explicitly. However, it may be easy to define this object in terms of itself. This 
                process is called <InlineMath>{'\\textbf{recursion}'}</InlineMath>. 
                </p>
                <p>
                Consider the recurrence relation
                </p>
                <BlockMath math="f(n)=f(n-1)+f(n-2)"/>
                <p>This defines a function recursively. It's the same recurrence relation as the Fibonacci sequence.</p>
                <p>
                    Example 1. Find the closed form of the following recurrence relation given <InlineMath>T(1)=c</InlineMath>. Assume <InlineMath>n=2^k</InlineMath>, <InlineMath>k\in\N</InlineMath>.
                </p>
                <BlockMath math="T(n)=2T(n/2)+c"/>
                <p>
                    <InlineMath>\textit Solution</InlineMath>:
                </p>
                <BlockMath math="
                \begin{equation*}
                    \begin{split}
                        T(n) &= 2^1 T(n/2^1)+c \\
                        &= 2^2 T(n/2^2) + 2^1c+c \\
                        &= 2^3 T(n/2^3) + 2^2c+2^1c+c \\
                        &= \vdots \\
                        &= 2^kT(1)+c\sum_{i=0}^{k-1}2^i \\
                        &= 2^kc+2^kc-c \\
                        &= 2^{k+1}c-c \\
                        &= 2nc-c
                    \end{split}
                \end{equation*}
                "/>
                <p>
                    Definition 1. The set of rooted trees, where a rooted tree consists of a set of vertices containing a distinguished 
                    vertex called the root, and edges connecting these vertices, can be defined recursively by these steps:
                </p>
                <p>
                    Basis Step: A single vertex <InlineMath>r</InlineMath> is a rooted tree.
                </p>
                <p>
                    Recursive Step: Suppose that <InlineMath>T_1,T_2,\dots,T_n</InlineMath> are disjoint rooted trees with roots <InlineMath>r_1,r_2,\dots,r_n</InlineMath>, respectively. 
                    Then the graph formed by starting with a root <InlineMath>r</InlineMath>, which is not in any of the rooted trees <InlineMath>T_1,T_2,\dots,T_n</InlineMath>, and adding an 
                    edge from <InlineMath>r</InlineMath> to each of the vertices <InlineMath>r_1,r_2,\dots,r_n</InlineMath> is also a rooted tree.
                </p>
                <p>
                    We have a binary tree when there are only two subtrees <InlineMath>T_1</InlineMath> and <InlineMath>T_2</InlineMath>. A full binary tree, which can 
                    also be defined recursively in a similar way, is a binary tree where each vertex except for the leaves has exactly 2 children.
                </p>
                <p>
                    Definition 2. We define the number of vertices <InlineMath>n(T)</InlineMath> of a full binary tree <InlineMath>T</InlineMath> recursively.
                </p>
                <p>
                    Basis Step: The number of vertices <InlineMath>n(T)</InlineMath> of the full binary tree <InlineMath>T</InlineMath> consisting 
                    of only a root <InlineMath>r</InlineMath> is <InlineMath>n(T)=1</InlineMath>.
                </p>
                <p>
                    Recursive Step: If <InlineMath>T_1</InlineMath> and <InlineMath>T_2</InlineMath> are full binary trees, then 
                    the number of vertices of the full binary tree <InlineMath>T=T_1\cdot T_2</InlineMath> is <InlineMath>n(T)=1+n(T_1)+n(T_2)</InlineMath>.
                </p>
                <p>
                    Definition 3. We define the height <InlineMath>h(T)</InlineMath> of a full binary tree <InlineMath>T</InlineMath> recursively.
                </p>
                <p>
                    Basis Step: The height of the full binary tree <InlineMath>T</InlineMath> consisting of only a root <InlineMath>r</InlineMath> is <InlineMath>h(T)=0</InlineMath>.
                </p>
                <p>
                    Recursive Step: If <InlineMath>T_1</InlineMath> and <InlineMath>T_2</InlineMath> are full binary trees, then 
                    the full binary tree <InlineMath>T=T_1\cdot T_2</InlineMath> has height <InlineMath>h(T)=1+\max(h(T_1),h(T_2))</InlineMath>.
                </p>
                <p>
                    Theorem 1. If <InlineMath>T</InlineMath> is a full binary tree <InlineMath>T</InlineMath>, then <InlineMath>{'n(T)\\leq 2^{h(T)+1}-1'}</InlineMath>.
                </p>
                <p>
                    The proof of this theorem uses structural induction. There is the basis step and the recursive step. Try proving this on your own.
                </p>
            </h5>
            </div>
            <div style={{ position: 'relative', minHeight: '100vh' }}>
                <Link to = "/topics/Induction/recalgo">
                <button
                    style={{
                    position: 'fixed',
                    bottom: '0',
                    right: '0',
                    margin: '20px',
                    }} >
                    Finish Lesson
                </button>
                </Link>
            </div>
        </LeafTopic>
    );
}

export default RecDef;
