export class BSTree {
  static Node = class {
    constructor(val, left=null, right=null) {
      this.val = val;
      this.child = [left, right];
    }
  };
  constructor() {
    this.root = null;
    this.visited = [];
  }
  #cloneR(n) {
    if (n == null) return null;
    const c = new BSTree.Node(n.val);
    c.child[0] = this.#cloneR(n.child[0]);
    c.child[1] = this.#cloneR(n.child[1]);
    return c;
  }
  clone() {
    const c = new BSTree();
    c.root = this.#cloneR(this.root);
    return c;
  }
  #equalR(n, o) {
    if (n == null && o == null) return true;
    if (n == null || o == null) return false;
    if (n.val !== o.val) return false;
    return this.#equalR(n.child[0], o.child[0]) && this.#equalR(n.child[1], o.child[1]);
  }
  equal(o) {
    if (!(o instanceof BSTree)) return false;
    return this.#equalR(this.root, o.root);
  }
  clearVisits() {
    for (const x of this.visited) {
      delete x.visited;
    }
    this.visited = [];
  }
  *#insertR(val, arr, ind) {
    if (arr[ind] == null) {
      arr[ind] = new BSTree.Node(val);
      return;
    }
    arr[ind].visited = true;
    this.visited.push(arr[ind]);
    yield;
    yield* this.#insertR(val, arr[ind].child, +(arr[ind].val<val));
  }
  *insertSteps(val) {
    const arr = [this.root];
    yield* this.#insertR(val, arr, 0);
    this.root = arr[0];
    this.clearVisits();
  }
  insert(val) {
    for (const x of this.insertSteps(val)) {}
  }
  #removeR(val, arr, ind) {
    if (arr[ind] == null) return;
    if (arr[ind].val !== val) {
      this.#removeR(val, arr[ind].child, +(arr[ind].val<val));
      return;
    }
    if (arr[ind].child[0] == null) {
      arr[ind] = arr[ind].child[1];
    } else if (arr[ind].child[1] == null) {
      arr[ind] = arr[ind].child[0];
    } else {
      // find successor and swap
      let prev = null;
      let succ = arr[ind].child[1];
      while (succ.child[0] !== null) {
        prev = succ;
        succ = succ.child[0];
      }
      if (prev) prev.child[0] = null;
      succ.child[0] = arr[ind].child[0];
      if (arr[ind].child[1] !== succ) succ.child[1] = arr[ind].child[1];
      arr[ind] = succ;
    }
  }
  remove(val) {
    const arr = [this.root];
    this.#removeR(val, arr, 0);
    this.root = arr[0];
  }
  *#findR(val, n) {
    if (n == null) {
      yield null;
      return;
    }
    n.visited = true;
    this.visited.push(n);
    yield n;
    console.log(val, n.val);
    if (val !== n.val) yield* this.#findR(val, n.child[+(n.val<val)]);
  }
  *findSteps(val, n) {
    yield* this.#findR(val, this.root);
    this.clearVisits();
  }
  find(val) {
    let r;
    for (const x of this.findSteps(val, this.root)) r = x;
    return r;
  }
  #reprR(n) {
    if (n == null) return "N";
    return `${n.val} (${this.#reprR(n.child[0])}, ${this.#reprR(n.child[1])})`;
  }
  repr() {
    return this.#reprR(this.root);
  }
  print() {
    console.log(this.repr());
  }
}

export class RBTree {
  static Node = class {
    constructor(val, par=null, left=null, right=null) {
      this.val = val;
      this.par = par;
      this.child = [left, right];
      // (null|color = 0) : black || (color = 1) : red
      this.color = 1;
    }
    rotate(dir) {
      const p = this.par;
      const c = this.child[dir];
      this.child[dir] = c.child[1-dir];
      c.child[1-dir] = this;
      if (p !== null) p.child[+(this===p.child[1])] = c;
      c.par = p;
      this.par = c;
      if (this.child[dir]) this.child[dir].par = this;
    }
  };
  constructor() {
    this.root = null;
    this.visited = [];
  }
  #cloneR(n, p) {
    if (n == null) return null;
    const c = new RBTree.Node(n.val, p);
    c.color = n.color;
    c.child[0] = this.#cloneR(n.child[0], c);
    c.child[1] = this.#cloneR(n.child[1], c);
    return c;
  }
  clone() {
    const c = new RBTree();
    c.root = this.#cloneR(this.root, null);
    return c;
  }
  #equalR(n, o) {
    if (n == null && o == null) return true;
    if (n == null || o == null) return false;
    if (n.val !== o.val || n.color !== o.color) return false;
    return this.#equalR(n.child[0], o.child[0]) && this.#equalR(n.child[1], o.child[1]);
  }
  equal(o) {
    if (!(o instanceof RBTree)) return false;
    return this.#equalR(this.root, o.root);
  }
  clearVisits() {
    for (const x of this.visited) {
      delete x.visited;
    }
    this.visited = [];
  }
  *#fixup(n) {
    if (n == null) {
      yield null;
      return;
    }
    if (n.par !== null) n.par.child[+(n.par.val<n.val)] = n;
    while (true) {
      yield null;
      if (n.par == null) {
        n.color = 0;
        break;
      }
      if (n.color === 0 || n.par.color === 0) break;
      const p = n.par;
      const g = p.par;

      const s = +(n.par.child[1]===n);
      const ps = +(g.child[1]===p);

      const u = g.child[1-ps];
      if (u !== null && u.color === 1) { // red uncle case
        p.color = 0;
        u.color = 0;
        g.color = 1;
        n = g;
      } else { // black uncle case
        if (s !== ps) p.rotate(1-ps);
        g.rotate(ps);
        g.par.color = 1;
        g.par.child[0].color = 0;
        g.par.child[1].color = 0;
        n = g.par;
      }
    }
    while (n !== null && n.par !== null) n = n.par;
    yield n;
  }
  *#insertR(val, n, par) {
    if (n == null) {
      this.clearVisits();
      let p = null;
      for (const x of this.#fixup(new RBTree.Node(val, par))) {
        p = x;
        yield;
      }
      this.root = p;
      return;
    }
    n.visited = true;
    this.visited.push(n);
    yield;
    yield* this.#insertR(val, n.child[+(n.val<val)], n);
  }
  *insertSteps(val) {
    yield* this.#insertR(val, this.root, null);
  }
  insert(val) {
    for (const x of this.insertSteps(val)) {}
  }
  #dFixup(n) {
    while (n !== null && n.par !== null) n = n.par;
    return n;
  }
  #removeR(val, n, par) {
    if (n == null) return par;
    if (n.val !== val) {
      return this.#removeR(val, n.child[+(n.val<val)], n);
    }
    if (n.child[0] !== null && n.child[1] !== null) {
      let succ = n.child[1];
      while (succ.child[0] !== null) {
        succ = succ.child[0];
      }
      let tmp = succ.val;
      succ.val = n.val;
      n.val = tmp;
      n = succ;
      par = n.par;
    }
    while (this.root.par !== null) this.root = this.root.par;
    const u = (n.child[0] == null) ? n.child[1] : n.child[0];
    if (u !== null) {
      u.color = 0;
      u.par = par;
      if (par) par.child[+(n===par.child[1])] = u;
      return u;
    }
    if (n.color === 1) {
      if (par) par.child[+(n===par.child[1])] = null;
      return par;
    }
    if (par == null) return null;
    const orig = [par.child, +(n===par.child[1])];
    while (true) {
      if (par == null) break;
      const s = +(n===par.child[1]);
      const w = par.child[1-s];
      if (w.color === 1) {
        par.rotate(1-s);
        w.color = 0;
        par.color = 1;
        continue;
      }
      let rc = -1;
      if (w.child[1-s] !== null && w.child[1-s].color === 1) rc = 1-s;
      else if (w.child[s] !== null && w.child[s].color === 1) rc = s;
      if (rc === -1) {
        w.color = 1;
        if (par.color === 1) {
          par.color = 0;
          break;
        }
        n = par;
        par = n.par;
        continue;
      }
      if (s === rc) {
        w.rotate(s);
      }
      par.rotate(1-s);
      par.par.color = par.color;
      par.par.child[0].color = 0;
      par.par.child[1].color = 0;
      break;
    }
    orig[0][orig[1]] = null;
    return n;
  }
  remove(val) {
    this.root = this.#dFixup(this.#removeR(val, this.root, null));
  }
  *#findR(val, n) {
    if (n == null) {
      yield null;
      return;
    }
    n.visited = true;
    this.visited.push(n);
    yield n;
    if (val !== n.val) yield* this.#findR(val, n.child[+(n.val<val)]);
  }
  *findSteps(val, n) {
    yield* this.#findR(val, this.root);
    this.clearVisits();
  }
  find(val) {
    let r;
    for (const x of this.findSteps(val, this.root)) r = x;
    return r;
  }
  #reprR(n) {
    if (n == null) return "N";
    return `${n.val} ${n.color} (${this.#reprR(n.child[0])}, ${this.#reprR(n.child[1])})`;
  }
  repr() {
    return this.#reprR(this.root);
  }
  print() {
    console.log(this.repr());
  }
  #sizeR(n) {
    if (n == null) return 0;
    return 1 + this.#sizeR(n.child[0]) + this.#sizeR(n.child[1]);
  }
  size() {
    return this.#sizeR(this.root);
  }
}

