export class BTree {
  static Node = class {
    constructor(par=null) {
      this.par = par;
      this.nodes = [];
      this.child = [];
    }
    bsearch(val) {
      if (this.nodes.length === 0 || this.nodes[0] > val) return -1;
      let a = 0;
      let b = this.nodes.length-1;
      while (a < b) {
        let m = b - Math.floor((b-a)/2);
        if (this.nodes[m] <= val) {
          a = m;
        } else {
          b = m-1;
        }
      }
      // finds last element <= val
      return a;
    }
    rotate(ind, dir, verbose=false) {
      // go around this.child[ind], this.nodes[ind], this.child[ind+1]
      // direction specified by dir
      const left = this.child[ind+dir-1];
      const right = this.child[ind+dir];
      const old = this.nodes[ind+dir-1];
      if (dir === 0) {
        this.nodes.splice(ind+dir-1, 1, left.nodes.pop());
        right.nodes.unshift(old);
        if (left.child.length !== 0) {
          const l = left.child.pop();
          l.par = right;
          right.child.unshift(l);
        }
      } else {
        this.nodes.splice(ind, 1, right.nodes.shift());
        left.nodes.push(old);
        if (right.child.length !== 0) {
          const r = right.child.shift();
          r.par = left;
          left.child.push(r);
        }
      }
    }
  };
  constructor(order=3) {
    // nodes can have up to order children
    // can have up to order-1 nodes
    // temporarily can hold order nodes while tree is restructuring
    this.order = order;
    this.root = new BTree.Node();
    this.visited = [];
  }
  #cloneR(n, p) {
    const c = new BTree.Node(p);
    c.nodes = [...n.nodes];
    c.child = n.child.map(ch => this.#cloneR(ch, c));
    return c;
  }
  clone() {
    const c = new BTree(this.order);
    c.root = this.#cloneR(this.root, null);
    return c;
  }
  #equalR(n, o) {
    if (n.nodes.length !== o.nodes.length) return false;
    if (n.nodes.some((v, i) => v !== o.nodes[i])) return false;
    return n.child.every((c, i) => this.#equalR(c, o.child[i]));
  }
  equal(o) {
    if (!(o instanceof BTree)) return false;
    return this.#equalR(this.root, o.root);
  }
  clearVisits() {
    for (const x of this.visited) {
      delete x.visited;
    }
    this.visited = [];
  }
  *#insertR(val, n) {
    let i = n.bsearch(val);
    while (true) {
      n.nodes.splice(i+1, 0, val);
      if (n.nodes.length < this.order) break;
      // rebalance
      const l = n.nodes.length;
      const m = Math.floor(l/2);
      const left = new BTree.Node();
      left.nodes = n.nodes.slice(0, m);
      left.child = n.child.slice(0, m+1);
      for (const n of left.child) n.par = left;
      const right = new BTree.Node();
      right.nodes = n.nodes.slice(m+1);
      right.child = n.child.slice(m+1);
      for (const n of right.child) n.par = right;
      const mid = n.nodes[m];
      if (n.par == null) {
        this.root = new BTree.Node();
        this.root.nodes = [mid];
        this.root.child = [left, right];
        left.par = this.root;
        right.par = this.root;
        break;
      } else {
        left.par = n.par;
        right.par = n.par;
        i = n.par.bsearch(mid);
        n.par.child.splice(i+1, 1, left, right);
        // insert into parent
        n = n.par;
        val = mid;
      }
      yield;
    }
  }
  *insertSteps(val) {
    let l = this.root;
    l.visited = true;
    this.visited.push(l);
    yield;
    while (l.child.length > 0) {
      const i = l.bsearch(val);
      if (l.nodes[i] === val) return;
      l = l.child[i+1];
      l.visited = true;
      this.visited.push(l);
      yield;
    }
    this.clearVisits();
    yield* this.#insertR(val, l);
  }
  insert(val) {
    for (const x of this.insertSteps(val)) {}
  }
  #removeR(val, n) {
    let i = n.bsearch(val);
    while ((i === -1 || n.nodes[i] !== val) && n.child.length > 0) {
      n = n.child[i+1];
      i = n.bsearch(val);
    }
    if (i === -1 || n.nodes[i] !== val) return null;
    let swap = null;
    if (n.child.length !== 0) {
      let pred = n.child[i];
      while (pred.child.length > 0) {
        pred = pred.child[pred.child.length-1];
      }
      let succ = n.child[i+1];
      while (succ.child.length > 0) {
        succ = succ.child[0];
      }
      if (pred.nodes.length > succ.nodes.length) {
        swap = pred;
      } else {
        swap = succ;
      }
    }
    if (swap !== null) {
      const id = (swap.nodes[0] > n.nodes[i]) ? 0 : swap.nodes.length-1;
      const tmp = swap.nodes[id];
      swap.nodes[id] = n.nodes[i];
      n.nodes[i] = tmp;
      n = swap;
      i = id;
      swap = tmp;
    }
    const min = Math.ceil(this.order/2)-1;
    while (true) {
      if (n.par == null || n.nodes.length > min) {
        n.nodes.splice(i, 1);
        if (n.nodes.length === 0 && n.child.length !== 0) {
          // assert n.child.length === 1
          this.root = n.child[0];
        }
        break;
      }
      const par = n.par;
      const pi = par.bsearch((swap == null) ? val : swap);
      let rotated = false;
      if (pi >= 0 && par.child[pi].nodes.length > min) {
        // par.rotate from left left
        par.rotate(pi+1, 0);
        i += 1;
        rotated = true;
      } else if (pi+2 < par.child.length && par.child[pi+2].nodes.length > min) {
        // par.rotate from right sib
        par.rotate(pi+1, 1);
        rotated = true;
      }
      if (rotated) {
        n.nodes.splice(i, 1);
        break;
      }
      n.nodes.splice(i, 1);
      if (pi >= 0) {
        for (const x of par.child[pi+1].child) x.par = par.child[pi];
        par.child[pi].nodes = par.child[pi].nodes.concat(par.nodes[pi], par.child[pi+1].nodes);
        par.child[pi].child = par.child[pi].child.concat(par.child[pi+1].child);
        par.child.splice(pi+1, 1);
      } else {
        for (const x of par.child[pi+2].child) x.par = par.child[pi+1];
        par.child[pi+1].nodes = par.child[pi+1].nodes.concat(par.nodes[pi+1], par.child[pi+2].nodes);
        par.child[pi+1].child = par.child[pi+1].child.concat(par.child[pi+2].child);
        par.child.splice(pi+2, 1);
      }
      n = par;
      i = pi + (pi === -1);
    }
  }
  remove(val) {
    this.#removeR(val, this.root);
  }
  *#findR(val, n) {
    n.visited = true;
    this.visited.push(n);
    yield n;
    if (n.nodes.length === 0) {
      yield null;
      return;
    }
    let i = n.bsearch(val);
    if (i === -1 || n.nodes[i] !== val) {
      if (n.child.length === 0) {
        yield null;
        return;
      }
      yield* this.#findR(val, n.child[i+1]);
    } else {
      yield n.nodes[i];
    }
  }
  *findSteps(val, n) {
    yield* this.#findR(val, this.root);
    this.clearVisits();
  }
  find(val) {
    let r;
    for (const x of this.findSteps(val, this.root)) r = x;
    return r;
  }
  #reprR(n, d) {
    return `${n.nodes}{${n.child.map(x => this.#reprR(x, d+1)).join('|')}}`
  }
  repr() {
    return this.#reprR(this.root, 0);
  }
  print() {
    console.log(this.repr());
  }
  #sizeR(n) {
    let x = n.nodes.length;
    for (const c of n.child) {
      x += this.#sizeR(c);
    }
    return x;
  }
  size() {
    return this.#sizeR(this.root);
  }
}
